<template>
  <div class="component_example">
    <h1>input-number </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal"></zoehis-input-number>
        <zoehis-input-number v-model="numberVal" width='190px'></zoehis-input-number>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>v-model </li>
              <li>width String  '150px'/ 'calc(100% - 30px)' / '50%', number</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
    &lt;zoehis-input-number v-model=&quot;numberVal&quot;&gt;&lt;/zoehis-input-number&gt;
    &lt;zoehis-input-number v-model=&quot;numberVal&quot; width=&quot;190px&quot; &gt;&lt;/zoehis-input-number&gt;
    &lt;script&gt;
      data () {
        return {
          numberVal:0
        }
      }
    &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal1" :disabled='true'></zoehis-input-number>
        <zoehis-input-number v-model="numberVal2" :min='1.6' :max='16'></zoehis-input-number>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>disabled </li>
              <li>min:max:: <strong></strong>numberVal2=0,1.6</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
       &lt;zoehis-input-number v-model=&quot;numberVal1&quot; :disabled=&#x27;true&#x27;&gt;&lt;/zoehis-input-number&gt;
       &lt;zoehis-input-number v-model=&quot;numberVal2&quot; :min=&#x27;1.6&#x27; :max=&#x27;16&#x27;&gt;&lt;/zoehis-input-number&gt;
       &lt;script&gt;
         data () {
           return {
             numberVal1:0,
             numberVal2:0
           }
         }
       &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal3" :step='1.6' :precision='2'></zoehis-input-number>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>step: Number, </li>
              <li>precision: Number, </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
       &lt;zoehis-input-number v-model=&quot;numberVal3&quot; :step=&#x27;1.6&#x27; :precision=&#x27;2&#x27;&gt;&lt;/zoehis-input-number&gt;
       &lt;script&gt;
         data () {
           return {
             numberVal3:0
           }
         }
       &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal" :controls='false'></zoehis-input-number>
        <zoehis-input-number v-model="numberVal" controls-position='right'></zoehis-input-number>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>controls: Boolean, true</li>
              <li>controls-position: String, 'right' </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
       &lt;zoehis-input-number v-model=&quot;numberVal&quot; :controls=&#x27;false&#x27; &gt;&lt;/zoehis-input-number&gt;
       &lt;zoehis-input-number v-model=&quot;numberVal&quot; :controls-position=&#x27;right&#x27; &gt;&lt;/zoehis-input-number&gt;
       &lt;script&gt;
         data () {
           return {
             numberVal:0
           }
         }
       &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal" ref='inputNumber'></zoehis-input-number>
        <zoehis-button @clickenter='doFocus'></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>focus()</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
       &lt;zoehis-input-number v-model=&quot;numberVal&quot; ref=&#x27;inputNumber&#x27; &gt;&lt;/zoehis-input-number&gt;
       &lt;zoehis-button @clickenter=&quot;doFocus&quot; &gt;&lt;/zoehis-button&gt;
       &lt;script&gt;
         data () {
           return {
             numberVal:0
           }
         },
         methods: {
           doFocus(){
             this.$refs.inputNumber.focus();
           }
         }
       &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input-number v-model="numberVal" @change='handleChange' @blur='handleBlur' @focus='handleFocus'></zoehis-input-number>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>@change:,</li>
              <li>@blur:</li>
              <li>@focus:</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
       &lt;zoehis-input-number v-model=&quot;numberVal&quot; @change=&#x27;handleChange&#x27; @focus=&#x27;handleFocus&#x27; @blur=&#x27;handleBlur&#x27; &gt;&lt;/zoehis-input-number&gt;
       &lt;script&gt;
         data () {
           return {
             numberVal:0
           }
         },
         methods: {
          /**
            *  
            * @param {Number} val 
            * @param {Number} old 
            */
           handleChange(val,old){
             console.log(&quot;val,old&quot;,val,old)
           },
           /**
            * 
            * @param {Object} e event
            */
           handleBlur(e){
             console.log(&quot;blur e&quot;,e)
           },
           /**
            * 
            * @param {Object} e event
            */
           handleFocus(e){
             console.log(&quot;focus e&quot;,e)
           }
         }
       &lt;script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>v-model='value'</td>
          <td></td>
          <td>number</td><td>-</td><td>0</td>
        </tr>
        <tr>
          <td>min</td>
          <td></td>
          <td>Number</td><td>-</td><td>-</td>
        </tr>
        <tr>
          <td>max</td><td></td>
          <td>Number</td><td>-</td><td>-</td>
        </tr>
        <tr>
          <td>step</td><td></td>
          <td>Number</td><td>-</td><td>1</td>
        </tr>
        <tr>
          <td>precision</td><td></td>
          <td>Number</td><td>-</td><td>-</td>
        </tr>
        <tr>
          <td>disabled</td><td></td>
          <td>Boolean</td><td>true/false</td><td>false</td>
        </tr>
        <tr>
          <td>controls</td><td></td>
          <td>Boolean</td><td>true/false</td><td>true</td>
        </tr>
        <tr>
          <td>width</td><td></td>
          <td>String</td><td>'150px' / 'calc(100% - 30px)' / '50%'</td><td>130px</td>
        </tr>
        <tr>
          <td>controls-position</td><td></td>
          <td>String</td><td>'right'/''</td><td>-</td>
        </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script type="text/babel">
  export default {
    data(){
      return {
        numberVal:0,
        numberVal1:0,
        numberVal2:0,
        numberVal3:0,
        numberVal4:0,
        numberVal5:0,
      }
    },
    watch:{
      numberVal(val){
        console.log("val",val)
      }
    },
    methods:{
      handleToggle(e){
        $(e.target).next().find("pre code").toggle()
      },
      /**
       *  
       * @param {Number} val 
       * @param {Number} old 
       */
      handleChange(val,old){
        console.log("val,old",val,old)
      },
      /**
       * 
       * @param {Object} e event
       */
      handleBlur(e){
        console.log("blur e",e)
      },
      /**
       * 
       * @param {Object} e event
       */
      handleFocus(e){
        console.log("focus e",e)
      },
      doFocus(){
        this.$refs.inputNumber.focus();
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style scoped>
  .component_example{height: auto;}
  .item-content{  height: auto;  }
  ul{margin: 0 0 0 20px; padding: 0;}
  .component_example .clearflex{display: block;}
  .component_example .item-toggle{cursor: pointer; background: #8fcae5; text-align: center; font-weight: bold;}
  .component_example .item-desc{width: 100%;max-width: 100%;}
  .component_example .item-code{ width: 100%;max-width: 100%;}
  .component_example .item-code pre{overflow: auto;}
  .component_example .item-code pre code{display: none;}
</style>



