<template>
  <div>

    <div class="component_example">
      <h1>Page </h1>
      <h2></h2>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page unit-text="" :stripCount="stripCount" :current_page.sync="currrentPage" :set_strip.sync="setStrip" :beforeClick="beforeClickFirst">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripCount=&quot;stripCount&quot; :current_page.sync=&quot;currrentPage&quot; :set_strip.sync=&quot;setStrip&quot;&gt;
            &lt;/zoehis-page&gt;

            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    stripCount: 40,
                    currrentPage: 1,
                    setStrip: 5
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            zoehis-pagestripCountnumber<br><br>
            current_page.syncnumber<br><br>
            set_strip.syncnumber<br><br>
            /  /
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripArr="stripArr" :stripCount="stripCount"
                       :set_strip.sync="setStrip" :current_page.sync="currrentPage">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripArr=&quot;stripArr&quot; :stripCount=&quot;stripCount&quot;
            :set_strip.sync=&quot;setStrip&quot; :current_page.sync=&quot;currrentPage&quot;&gt;
            &lt;/zoehis-page&gt;

            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    stripArr: [5,10,15],  //
                    stripCount: 40,  //
                    setStrip: 5,  //
                    currrentPage: 1
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            zoehis-page<br><br>
            1.stripArr,<br>
            [10,20,30,40]<br><br>
            2.current_page.sync<br>
            1<br><br>
            3.set_strip.sync<br>
            10
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripCount="stripCount" :set_strip.sync="setStrip" :current_page.sync="currrentPage"
                       :isSetStrip="false">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripCount=&quot;stripCount&quot; :set_strip.sync=&quot;setStrip&quot; :current_page.sync=&quot;currrentPage&quot;
            :isSetStrip=&quot;false&quot;&gt;
            &lt;/zoehis-page&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            isSetStripboolean,false<br><br>
            
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripArr="stripArr" :stripCount="stripCount"
                       :set_strip.sync="setStrip" :current_page.sync="currrentPage"
                       @changeStrip="changeStrip" @changePage="changePage" :beforeClick="beforeClick">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripArr=&quot;stripArr&quot; :stripCount=&quot;stripCount&quot;
              :set_strip.sync=&quot;setStrip&quot; :current_page.sync=&quot;currrentPage&quot;
              @changeStrip=&quot;changeStrip&quot; @changePage=&quot;changePage&quot;&gt;
            &lt;/zoehis-page&gt;

            methods: {
              changeStrip: function (strip) {
                console.log(&#x27;&#x27;+strip+&#x27;&#x27;);
              },

              changePage: function (page) {
                console.log(&#x27;&#x27;+page+&#x27;&#x27;);
              }

            }
          </code></pre>
          </div>
          <div class="item-desc">
            changeStripchangePage<br><br>
            1.@changeStrip
            <br><br>
            2.@changePage
            

          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>:stripArr</td>
            <td>stripArr,</td>
            <td></td><td>-</td><td>[10,20,30,40]</td>
          </tr>
          <tr>
            <td>unit-text</td><td>''15</td><td>string</td><td>-</td><td></td>
          </tr>
          <tr>
            <td>:stripCount</td><td>stripCount</td><td>num</td><td>-</td><td>-</td>
          </tr>
          <tr>
            <td>:set_strip.sync</td><td>set_strip.sync</td><td>num</td><td>-</td><td>10</td>
          </tr>
          <tr>
            <td>:current_page.sync</td><td>current_page.sync</td><td>num</td><td>-</td><td>1</td>
          </tr>
          <tr>
            <td>:isSetStrip</td><td>isSetStripboolean,false</td><td>boolean</td><td>-</td><td>true</td>
          </tr>
          <tr>
            <td>:beforeClick</td><td>return true</td><td>function</td><td>1.2.</td><td>-</td>
          </tr>
          <tr>
            <td>@changeStrip</td><td>@changeStrip</td><td></td><td>-</td><td></td>
          </tr>
          <tr>
            <td>@changePage</td><td>@changePage</td><td></td><td>-</td><td></td>
          </tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>

    </div>
  </div>
</template>

<script>
  export default{
    data: function () {
      return {
        stripArr: [5,10,15],  //
        stripCount: 40,  //
        setStrip: 5,  //
        currrentPage: 1,
      }
    },

    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },

    methods: {
      beforeClickFirst(newP,oldP){
//        console.log("",newP,oldP)
        return true
      },
      beforeClick(newPage,oldPage){
        console.log(newPage,oldPage)
        return true
      },
      changeStrip: function (strip) {
        console.log(''+strip+'');
      },

      changePage: function (page) {
        console.log(''+page+'');
      }

    }
  }
</script>



