<template>
  <zoehis-input
    class="zoehis_time_editor"
    ref="reference"
    :icon="icon"
    :readonly="readonly"
    :disabled="disabled"
    @focusout.native="handleBlur"
    @click="handleClick"
    @keydown.native="handleKeydown"
    @keyup.native="handleKeyup"
    :value="displayValue"
    @change.native="displayValue = $event.target.value"
    @focus="handleFocus"
    @clear="handleClear"
    @mousedownClear="handleMousedownClear"
    @clickicon="handleClickIcon"
    @mousedownIcon="mousedownIcon"
    :width="width"
    :clickselect="false"
    :showclose="showclose"
    :clearable="clearable"
    :error-tip="errorTip"
    :show-error-tip="showErrorTip"
    :prevent-over-id="preventOverId"
    :ime-mode="imeMode"
  ></zoehis-input>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';
  import Clickoutside from '../../../src/utils/clickoutside'; //
  import { formatDate, parseDate, getWeekNumber, equalDate, isDate } from './util';
  import Popper from '../../../src/utils/vue-popper';//
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInput from '../../input';

  const NewPopper = {
    props: {
      preventOverId: Popper.props.preventOverId,
      offset: Popper.props.offset,
      boundariesPadding: Popper.props.boundariesPadding
    },
    methods: Popper.methods,
    data: Popper.data,
    beforeDestroy: Popper.beforeDestroy
  };

  //
  const DEFAULT_FORMATS = {
    date: 'yyyy-MM-dd',
    month: 'yyyy-MM',
    datetime: 'yyyy-MM-dd HH:mm:ss',
    time: 'HH:mm:ss',
    week: 'yyyywWW',
    timerange: 'HH:mm:ss',
    daterange: 'yyyy-MM-dd',
    datetimerange: 'yyyy-MM-dd HH:mm:ss',
    year: 'yyyy'
  };
  //picker
  const HAVE_TRIGGER_TYPES = [
    'date',
    'datetime',
    'time',
    'time-select',
    'week',
    'month',
    'year',
    'daterange',
    'timerange',
    'datetimerange'
  ];
  //
  const DATE_FORMATTER = function(value, format) {
    return formatDate(value, format);
  };
  //
  const DATE_PARSER = function(text, format) {
    return parseDate(text, format);
  };
  const RANGE_FORMATTER = function(value, format, separator) {
    if (Array.isArray(value) && value.length === 2) {
      const start = value[0];
      const end = value[1];

      if (start && end) {
        return formatDate(start, format) + separator + formatDate(end, format);
      }
    }
    return '';
  };
  /**
   * text separator
   * @param text  
   * @param format 
   * @param separator  
   * @returns {*} 
   */
  const RANGE_PARSER = function(text, format, separator) {
    const array = text.split(separator);
    if (array.length === 2) {
      const range1 = array[0];
      const range2 = array[1];

      return [parseDate(range1, format), parseDate(range2, format)];
    }
    return [];
  };
  //-
  const TYPE_VALUE_RESOLVER_MAP = {
    default: {
      formatter(value) {
        if (!value) return '';
        return '' + value;
      },
      parser(text) {
        if (text === undefined || text === '') return null;
        return text;
      }
    },
    week: {
      formatter(value, format) {
        let date = formatDate(value, format);
        const week = getWeekNumber(value);

        date = /WW/.test(date)
          ? date.replace(/WW/, week < 10 ? '0' + week : week)
          : date.replace(/W/, week);
        return date;
      },
      parser(text) {
        const array = (text || '').split('w');
        if (array.length === 2) {
          const year = Number(array[0]);
          const month = Number(array[1]);

          if (!isNaN(year) && !isNaN(month) && month < 54) {
            return text;
          }
        }
        return null;
      }
    },
    date: {
      formatter: DATE_FORMATTER,
      parser: DATE_PARSER
    },
    datetime: {
      formatter: DATE_FORMATTER,
      parser: DATE_PARSER
    },
    daterange: {
      formatter: RANGE_FORMATTER,
      parser: RANGE_PARSER
    },
    datetimerange: {
      formatter: RANGE_FORMATTER,
      parser: RANGE_PARSER
    },
    timerange: {
      formatter: RANGE_FORMATTER,
      parser: RANGE_PARSER
    },
    time: {
      formatter: DATE_FORMATTER,
      parser: DATE_PARSER
    },
    month: {
      formatter: DATE_FORMATTER,
      parser: DATE_PARSER
    },
    year: {
      formatter: DATE_FORMATTER,
      parser: DATE_PARSER
    },
    number: {
      formatter(value) {
        if (!value) return '';
        return '' + value;
      },
      parser(text) {
        let result = Number(text);

        if (!isNaN(text)) {
          return result;
        } else {
          return null;
        }
      }
    }
  };
  //todo
  const PLACEMENT_MAP = {
    left: 'bottom-start',
    center: 'bottom',
    right: 'bottom-end'
  };
  //data-pickervalue Date or [Date,Date]
  const valueEquals = function(a, b) {
    const aIsArray = a instanceof Array;
    const bIsArray = b instanceof Array;
    if (aIsArray && bIsArray) {
      return new Date(a[0]).getTime() === new Date(b[0]).getTime() &&
        new Date(a[1]).getTime() === new Date(b[1]).getTime();
    }
    if (!aIsArray && !bIsArray) {
      return new Date(a).getTime() === new Date(b).getTime();
    }
    return false;
  };
  export default {
    mixins:[Emitter,NewPopper],
    components: {ZoehisInput},
    directives: { Clickoutside },
    props:{
      value: {},//
      defaultValue: {},//
      format: String,//
      readonly: Boolean,//
      disabled: Boolean,//
      popperClass: String,//popperclass
      align: {    //
        type: String,
        default: 'left'
      },
      rangeSeparator: {//time  
        default: ' - '
      },
      pickerOptions: {},//time
      tabInner: {    //tab()
        type:Boolean,
        default:false
      },
      clearable: {    //
        type: Boolean,
        default: true
      },
      showclose: {    //
        type: Boolean,
        default: true
      },
      pickerWidth:{     //
        type: [Number,String],
        default:133
      },
      width:{     //
        type: [Number,String],
        default:133
      },
      standardtype:{
        type:Boolean,
        default:false
      },
      errorTip: { //  "" 
        type: String,
        default: ""
      },
      icon: { //
        type: String,
        default: "z_timeA_normal"
      },
      showErrorTip:{   //true 
        type:Boolean,
        default:false
      },
      imeMode:{   // ime-mode 
        type:String,
        default:"disabled"
      },
    },
    data(){
      return {
        pickerVisible: false,  //time()
        currentValue: '',  //
        unwatchPickerOptions: null,
        keyTabEsc: false,   //tab/esc
        appendToBody: true,
        inputFocus:false,
        otherFocus:false,
      }
    },
    watch: {
      //
      pickerVisible(val){
        // 
        if(this.disabled) return;
        if(val){
          this.showPicker();
          if(!this.disabled){
            this.focus();
          }
        }else {
          this.hidePicker();
          this.curPlacement="";
        }
      },
      //
      currentValue(val){
        if(val) return; //
        if(this.picker && typeof this.picker.handleClear==='function') {
          this.picker.handleClear();
        }else {
          this.$emit('input','')
        }
      },
      //v-model
      value:{
        immediate:true,//value
        handler(val){
          this.currentValue = isDate(val) ? new Date(val) : val; //
        }
      },
      displayValue(val){
        this.$emit('change', val);
      }
    },
    computed: {
      focusFlag(){
        return this.inputFocus || this.otherFocus;
      },
      //
      valueIsEmpty() {
        const val = this.currentValue;
        if (Array.isArray(val)) {
          for (let i = 0, len = val.length; i < len; i++) {
            if (val[i]) {
              return false;
            }
          }
        } else {
          if (val) {
            return false;
          }
        }
        return true;
      },
      //inputel
      reference(){
        return this.$refs.reference.$el;
      },
      //input
      refInput(){
        if(this.reference) return this.reference.querySelector("input");
        return {};
      },
      displayValue:{
        //
        get(){
          const value = this.currentValue;
          return this.timeToFormatter(value);
        },
        set(value){
          //
          if(value){
            const type = this.type;
            const parser = (
              TYPE_VALUE_RESOLVER_MAP[type] ||
              TYPE_VALUE_RESOLVER_MAP['default']
            ).parser;
            //
            const parserValue = parser(value,this.format || DEFAULT_FORMATS[type]);
            //picker
            if(parserValue){
              //
              parserValue.setFullYear(this.currentValue.getFullYear());
              parserValue.setMonth(this.currentValue.getMonth(),this.currentValue.getDate());
              if(this.picker){//picker 
                let t1 = isDate(parserValue) ? new Date(parserValue).getTime() : parserValue; //
                let t2 = isDate(this.picker.value) ? new Date(this.picker.value).getTime() : this.picker.value; //
                if(t1==t2){
                  this.$refs.reference.setValue(this.displayValue);
                }else {
                  this.picker.value = parserValue;
                }
              }else {//pickerinput
                this.$emit('input', this.standardtype?parserValue:parserValue.getTime());
              }
            }

          }else {//
            this.$emit('input', value);
            this.picker.value = value;
          }
          this.$forceUpdate();// todo 

        }
      }
    },
    created(){
      // vue-popper 
      this.popperOptions = {
        boundariesPadding: 0,
        gpuAcceleration: false
      };
      this.placement = PLACEMENT_MAP[this.align] || PLACEMENT_MAP.left;//vue-popper 
      //placement
      this.curPlacement = this.placement;
    },
    methods: {
      //  placement placement
      handlePlacementChange(){
        let placement2 = this.curPlacement;
        if(!placement2) return;
        //bottomtop
        if(placement2.indexOf("bottom")!=-1){
          placement2 = placement2.replace("bottom","top");
        }else {
          placement2 = placement2.replace("top","bottom");
        }
        this.$refs.reference.setPlacementToError(placement2);
      },
      //
      //return true   false
      getFocusFlag(){
        return this.focusFlag;
      },
      mousedownIcon(){
        if(this.pickerVisible){// 
          this.otherFocus=true;//
          clearInterval(this.visibleTimer);
        }

      },
      //
      handleClickIcon(e){
        if(this.disabled) return;
        e.stopPropagation();
        this.pickerVisible = !this.pickerVisible;
        if(this.pickerVisible){// 
          this.inputFocus=true;//
          clearInterval(this.visibleTimer);
        }
      },
      //
      getDisabled(){
        return this.disabled;
      },
      //
      focus(){
        this.refInput.focus();
        this.setInputSelect(0,0);//
      },
      //
      timeToFormatter(value,newFormat){
        if(!value) return "";//""
        //typetypeformatter
        const formatter = (
          TYPE_VALUE_RESOLVER_MAP[this.type] ||
          TYPE_VALUE_RESOLVER_MAP['default']
        ).formatter;
        //type
        const format = DEFAULT_FORMATS[this.type];
        return formatter(value, newFormat || this.format || format);//format
      },
      /**
       *  
       * @param isFormat   HH:mm:ss
       * @returns {*}
       */
      getTime(isFormat){
        return isFormat ? this.timeToFormatter(this.currentValue,isFormat) : this.currentValue;
      },
      handleMousedownClear(){
        if(this.pickerVisible){
          this.otherFocus=true;
        }
      },
      //
      handleClear(e){
        e.stopPropagation();
        this.currentValue = '';
        this.otherFocus=true;//
        if(this.pickerVisible){// 
            clearInterval(this.visibleTimer);
            this.$nextTick(()=>{
            this.pickerVisible=true;
           });
        }

        e.stopPropagation();
      },
      //
      hidePicker() {
        if (this.picker) {
          this.picker.visible = false;
          this.destroyPopper();
        }
      },

      //
      showPicker() {
        if (this.$isServer) return; //  todo 
        //pickervm
        if (!this.picker) {
          this.mountPicker();
        }

        this.picker.visible = true;//time
        if (this.currentValue instanceof Date) {
          this.picker.date = new Date(this.currentValue.getTime());
          this.handleNow(this.picker.date);//v-model bug
        } else {
          this.picker.value = this.currentValue;
        }
        this.$nextTick(() => {
          this.updatePopper();//dom
          this.picker.initScrollPos && this.picker.initScrollPos();
        });
      },
      //
      mountPicker(){
        this.panel.defaultValue = this.defaultValue || this.currentValue;
        this.picker = new Vue(this.panel).$mount();//this.panel,$mount
        this.picker.popperClass = this.popperClass;//class
        this.popperElm = this.picker.$el;
        this.picker.width = this.pickerWidth || this.reference.getBoundingClientRect().width; //,
        // this.format
        if (this.format) {
          this.picker.format = this.format;
        }

        const updateOptions = () => {
          const options = this.pickerOptions; //picker
          if(options && options.selectableRange){
            let ranges = options.selectableRange;//
            const parser = TYPE_VALUE_RESOLVER_MAP.datetimerange.parser;//parser
            const format = DEFAULT_FORMATS.timerange;//time

            ranges = Array.isArray(ranges) ? ranges : [ranges];//
            this.picker.selectableRange = ranges.map(ranges => parser(ranges, format, this.rangeSeparator));
          }

          for(const option in options){
            //selectableRangepicker
            if(options.hasOwnProperty(option) && option !== 'selectableRange'){
              this.picker[option]=options[option];
            }
          }
        };
        updateOptions();
        //pickerOptions
        this.unwatchPickerOptions = this.$watch('pickerOptions', () => updateOptions(), { deep: true });
        this.$el.appendChild(this.picker.$el);//this.$el
        this.picker.$on('dodestroy',this.doDestroy);   //pickerdodestroy
        this.picker.$on('pick',(date='',visible=false) => {
          //valuedateemit
          if(!valueEquals(this.value, date)){
            this.$emit('input',this.standardtype?date:(date && date.getTime()));
          }

          //tabescsetTime()tabescbugsetTime()bug
          if(this.keyTabEsc){
            if(visible){//visibletrue
              return;
            }else {
              this.keyTabEsc = false;//
            }
          }
          this.pickerVisible = visible;
        });

        //
        this.picker.$on('select-range',(start,end) => {
//          this.refInput.setSelectionRange(start,end);
          this.refInput.focus();
        });

        //
        this.picker.$on('placementchange',(placement) => {
          if(this.curPlacement==placement) return;// 
          if(!this.inputFocus) return;// 
          this.curPlacement=placement;
          this.handlePlacementChange();
        });
        //
        this.picker.$on('toggle-visible',(isShow,isChange) => {
          if(isShow){//
            this.$emit("show",this.currentValue);
          }else {
            this.$emit("hide",this.currentValue);
            if(isChange){
              this.$emit("inputnative",this.currentValue);
            }
          }
        });

        //
        this.picker.$on('click-now',() => {
          this.$nextTick(()=>{
            this.pickerVisible=false;
          })
          this.$emit("click-now");
        });
        //
        this.picker.$on('click-sure',() => {
          this.$emit("click-sure");
        });
        //
        this.picker.$on('pick-focusin',(focusFlag) => {
          this.otherFocus = focusFlag;
          if(focusFlag){
            this.clearVisibleTimer();
          }else {
            this.setVisibleTimer();
          }
        });

      },
      ummountPicker(){
        if(this.picker){
          this.picker.$destroy();
          this.picker.$off();
          if(typeof this.unwatchPickerOptions === 'function'){
            this.umwatchPickerOptions();
          }
          this.picker.$el.parentNode.removeChild(this.picker.$el);
        }
      },
      //
      handleFocus(){
        //  
        this.visibleTimer && clearTimeout(this.visibleTimer);
        this.inputFocus=true;
        this.otherFocus=false;
        this.pickerVisible = true;
        //
        this.$nextTick(()=>{
          this.handlePlacementChange();
        });
        const type = this.type;
        // 
        if(HAVE_TRIGGER_TYPES.indexOf(type) !==-1 && !this.pickerVisible){
          this.pickerVisible = true;
        }
        //focus
        this.$emit('focus',this);
      },
      //
      handleBlur() {
        this.inputFocus=false;
        //bug
        if(this.refInput.value !== this.displayValue){
          this.$refs.reference.setValue(this.displayValue);
        }
        this.setVisibleTimer();
        this.$emit('blur', this);
      },
      //
      clearVisibleTimer(){
        if(this.visibleTimer) clearTimeout(this.visibleTimer);
      },
      //
      setVisibleTimer(){
        if(this.focusFlag || !this.pickerVisible) return;
        this.hide();
      },
      //
      hide(){
        if(this.visibleTimer){
          clearTimeout(this.visibleTimer);
          this.visibleTimer = null;
        }
        this.visibleTimer = setTimeout(()=>{
          this.pickerVisible=false;
        },20);
      },
      handleKeydown(e){
        const keyCode = e.keyCode;
        //
        if(keyCode>=65 && keyCode<=90){
          e.preventDefault();
          return;
        }

        switch (keyCode){
          case 27://ESC
            this.pickerVisible = false;
            this.refInput.blur(); //
            this.keyTabEsc = true;
            e.stopPropagation();
            break;
          case 9://tab
            e.preventDefault();
            break;
          case 38://up
          case 40://down
            e.preventDefault();
            break;
          default:
        }
      },
      //keyup
      handleKeyup(e){
        var keyCode = e.keyCode;
        let indexStart = this.refInput.selectionStart;//
        let indexEnd = this.refInput.selectionEnd;//
        if(keyCode==38){//up
          //
          e.stopPropagation();
          this.changeValueByUpDown(indexStart,indexEnd,true);
        }else if(keyCode==40){//down
          //
          e.stopPropagation();
          this.changeValueByUpDown(indexStart,indexEnd,false);
        }else if((keyCode<58 && keyCode>47) || (keyCode<106 && keyCode>95) || (e.shiftKey && keyCode==186) || keyCode==8){
          //48-5796-105backspace
          this.doDealDigit(e,indexStart);
        }else if(keyCode == 9){ //tab

          //tabInner truetab
          let time = this.getHourMinSec();
          if(this.tabInner && time){
            //""
            if(time.cursor === "hour"){
              this.setInputSelect(time.hourIndex+1, time.minuteIndex);
            }else if(time.cursor === "minute" && time.second !==''){
              this.setInputSelect(time.minuteIndex+1, time.secondIndex);
            }else {
              this.pickerVisible = false;
              this.keyTabEsc = true;
              this.refInput.blur(); //
              //emit => tab  1: 2:
              //$nextTicktab
              this.$nextTick(function(){
                this.$emit('tab',this.currentValue,this.displayValue);
              });
            }
          }else {
            this.pickerVisible = false;
            this.keyTabEsc = true;
            this.refInput.blur(); //
            //emit => tab  1: 2:
            //$nextTicktab
            this.$nextTick(function(){
              this.$emit('tab',this.currentValue,this.displayValue);
            });
          }
        }else if(keyCode == 13){ //enter
          this.pickerVisible = false;
          this.refInput.blur(); //
          this.$nextTick(function(){
            this.$emit('enter',this.currentValue,this.displayValue);
          });
        }else if(keyCode==229){//229   229
          let reg = /^(Digit|Numpad)[0-9]$/;
          if(reg.test(e.code) || e.code=="Semicolon"){//  
            this.doDealDigit(e,indexStart);
          }

        }
      },
      //
      doDealDigit(e,indexStart){
        let time = this.getHourMinSec();
        let hour,minute,second ;
        //
        const showSecond = (this.format || DEFAULT_FORMATS[this.type]).indexOf("ss") !== -1;
        if(!time){
          // 6
          let nVal = e.target.value;
          if(nVal.indexOf(":") ===-1 && nVal.length>3){
            hour = nVal.substring(0,2);
            minute = nVal.substring(2,4);
            second = nVal.substring(4,6);
          }else {
            return;
          }
        }else {
          hour = time.hour;
          minute = time.minute;
          second = time.second;
        }
        let hL = hour.length;
        let mL = minute.length;
        let sL = second.length;

        //2
        if(hL <2 || mL<2 || (showSecond && sL<2)){
          return;
        }
        //2
        if((hL == 2 && indexStart ==2) || (mL == 2 && indexStart == 5) || (showSecond && sL == 2 && indexStart == 8)){
          indexStart ++;
        }else {
          if(hL>2){
            hour = hour.substring(0,2);
            if(indexStart>=2){
              indexStart = 3;
            }
          }
          if(mL>2){
            minute = minute.substring(0,2);
            if(indexStart>=5){
              indexStart = 6;
            }
          }
          if(showSecond && sL>2){
            second = second.substring(0,2);
            if(indexStart>=8){
              indexStart = 9;
            }
          }
        }
        let newVal = hour + ":" + minute;//
        if(showSecond){
          newVal += ":" + second;
        }
        if(this.displayValue == newVal){
          this.$refs.reference.setValue(this.displayValue);
        }else {
          this.displayValue = newVal;
        }
        //
        this.$nextTick( () => {
          this.setInputSelect(indexStart, indexStart);
        });
      },
      //
      changeValueByUpDown(keyStart,keyEnd,isUp){
        let time = this.getHourMinSec();
        if(!time) return;
        let accVal = isUp ? -1 : 1;
        let nowValue = this.currentValue;
        let hour = time.hour;
        let minute = time.minute;
        if(time.cursor==="hour"){
          nowValue.setHours(nowValue.getHours()+accVal);
          keyStart = 0;
          keyEnd = time.hourIndex;
        }else if(time.cursor === "minute"){
          nowValue.setMinutes(nowValue.getMinutes()+accVal);
          keyStart = time.hourIndex+1;
          keyEnd = time.minuteIndex;
        }else {
          nowValue.setSeconds(nowValue.getSeconds()+accVal);
          keyStart = time.minuteIndex+1;
          keyEnd = time.secondIndex;
        }
        this.displayValue = this.timeToFormatter(nowValue);
        this.$nextTick( () => this.setInputSelect(keyStart, keyEnd) );
      },
      //
      handleClick(){
        if(!this.pickerVisible){
          this.pickerVisible = true;
        }
        let time = this.getHourMinSec();
        if(!time) return;
        let start = 0;
        let end = 0;
        if(time.cursor === "hour"){
          start = 0;
          end = time.hourIndex;
        }else if(time.cursor === "minute"){
          start = time.hourIndex + 1;
          end = time.minuteIndex;
        }else {
          start = time.minuteIndex + 1;
          end = time.secondIndex;
        }
        this.setInputSelect(start, end);
      },
      //
      getHourMinSec(){
        let nVal = this.refInput.value;
        nVal=nVal.replace(//g,":");// 
        nVal=nVal.replace(/::/g,":");// ::  :
        //""
        let firstIndex = nVal.indexOf(":");
        if(firstIndex === -1) return null; //""
        let  hour = nVal.substring(0,firstIndex) || "00";

        //
        const showSecond = (this.format || DEFAULT_FORMATS[this.type]).indexOf("ss") !== -1;
        //""
        let secIndex = showSecond ? nVal.indexOf(":",firstIndex+1) : nVal.length;
        if(secIndex === -1) return null; //""
        let minute = nVal.substring(firstIndex+1,secIndex) || "00";//
        //""
        let second = showSecond ? (nVal.substring(secIndex+1) || "00") : '';//

        // 
        let hL = hour.length;
        let mL = minute.length;
        let end = this.refInput.selectionEnd;//
        let cursor = ""; //
        if(end<hL+1){    //""
          cursor = "hour";
        }else if(end<hL+mL+2){ //""
          cursor = "minute";
        }else {       //""
          cursor = "second";
        }
        return {
          hour: hour,
          minute: minute,
          second: second,
          hourIndex: hL,
          minuteIndex: hL+mL+1,
          secondIndex: nVal.length,
          cursor: cursor,
        }
      },
      //
      setInputSelect(start,end){
        this.refInput.setSelectionRange(start, end);
      },
      /**
      *  (picker)
      * @param nowTime 
      */
      handleNow(nowTime){
        this.picker && this.picker.handleNow(nowTime);
      },
      /**
       * 
       *

      /**
       * 
       * @param value 
       */
      setTime(value){
        // handleNow
        if(this.picker){
          //  keyTebEsctrue 
          if(!this.pickerVisible){
            this.keyTabEsc = true;//bug
          }
          this.handleNow(value)
        }else {
          this.keyTabEsc = true;//bug
          //isDate:   10:16:16
          this.currentValue = isDate(value) ? new Date(value) : value; //
          this.displayValue = isDate(value) ? this.timeToFormatter(new Date(value)) : value;
        }
      },
    }
  }

</script>



