<template>
  <button
    class="zoehis_button"
    :disabled="disabled"
    @click="handleClickEnter"
    @keydown.enter.prevent
    @keyup.enter="handleClickEnter"
    @focus="handleFocus"
    @blur="handleBlur"
    :class="[
      type ? 'zoehis_button_' + type : '',
      size ? 'zoehis_button_' + size : '',
      {
        'zoehis_disabled':disabled,
        'has_icon':icon,
      }
    ]"

  >
    <i :class="'zoehis_button_icon zoeIconfont ' + icon" v-if="icon"></i><template v-if="icon">&nbsp;</template><span v-if="keyboard" class="zoehis_button_keyboard">{{keyboard}}</span><span v-if="$slots.default" class="zoehis_button_desc"><slot></slot></span>
  </button>
</template>
<script>
  export default {
    name: 'ZoehisButton',
    props: {
      type: {    //  default/primary/features()
        type: String,
        default: "default"
      },
      size: {   // normal | small/
        type:String,
        default:'normal'
      },
      icon: String,//
      keyboard: [String,Number],//
      disabled:Boolean
    },
    methods: {
      handleClickEnter:function(evt){
        this.$emit('clickenter', evt); //
      },
      handleFocus:function(evt){
        this.$emit('focus', evt); //
      },
      handleBlur:function(evt){
        this.$emit('blur', evt); //
      },
      //
      focus:function(evt){
        this.$el.focus()
      },
      //  true
      getDisabled:function(){
        return this.disabled;
      }
    }
  };
</script>



