<template>
  <div class="zoehis_table" ref="comTable"
       :class="[
        `com_scrolling_${scrollPosition}`,
        {   fixed_hover:fixedHoverFlag,
            fixed_has:leftVisible||rightVisible,
            page_flag:pageflag,
            two_row_th:tableDownConfig.length,
        }]">
    <zoehis-dropdown v-if="hideColumnFlag" trigger="click" :hide-on-click="false" class="zoehis_table_col_list">
      <i class="zoeIconfont z_arrowARM_normal zoehis_table_dropdown_icon"></i>
      <zoehis-dropdown-menu slot="dropdown">
        <zoehis-dropdown-item v-for="d in temp_tableTopConfig" :key="d.dataItemCode" class="zoehis_table_drop_item">
          <zoehis-checkbox @inputnative="inputnativeEn($event,d)" trueflag='1' falseflag='0' v-model="d.validFlag">{{d.dataItemName}}</zoehis-checkbox>
        </zoehis-dropdown-item>
      </zoehis-dropdown-menu>
    </zoehis-dropdown>
    <div class="com_table__main">
      <!--  -->
      <div class="com_table__header" ref="comTableHeader">
        <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__header-inner"><thead>
        <tr>
          <!--  -->
          <th :rowspan="thRowspan" v-if='checkbox' :style="{width:checkboxWidth,visibility:leftVisible ? 'hidden' : 'visible' }">
            <zoehis-checkbox v-model="allSelectVal" @inputnative="selectAllEn" :before-select="beforeAllSelect"></zoehis-checkbox>
          </th>
          <!--  -->
          <th :rowspan="d.uiColspan>1 ? 1 : thRowspan" :colspan="d.uiColspan" v-for="(d,index) in tableTopConfig" :key="d.dataItemCode"
              :style="{width: d.itemWidth}"
              @mouseout="mouseoutEn"
              @mousemove="mousemoveEn"
              @mousedown="mousedownEn($event,d)"
          >
            {{d.dataItemName}}
            <span class="com_sort_box" v-if="d.uiColspan<2 && d.dataItemSort=='1'">
                <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
            </span><i v-if="hideColumnFlag && !d.uiIndexFlag" @click="handleHideColEn(d)" title="" class="zoehis_table_hidden_col zoeIconfont z_close_normal02" :class="{zoehis_col_two:d.uiColspan>1}"></i>
            <!--  -->
            <!---->
            <slot v-if="d.uiColspan<2 && (!d.fixedFlag || d.fixedFlag=='left' && !leftVisible || d.fixedFlag=='right' && !rightVisible) " :name="d.dataItemCode"></slot>

          </th>
          <!--  -->
          <th :rowspan="thRowspan" v-if='operateflag' :style="operateStyle">{{i18nObj.operate}}</th>
        </tr>
        <tr v-if="tableDownConfig.length">
          <!--  -->
          <th v-for="(d,index) in tableDownConfig" :key="d.dataItemCode"
              :style="{width:d.itemWidth}"
          >
            {{d.dataItemName}}
            <span class="com_sort_box" v-if="d.dataItemSort=='1'">
                                <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                                <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
                            </span>
            <!---->
            <slot v-if="!d.fixedFlag" :name="d.dataItemCode"></slot>
          </th>
        </tr>

        </thead></table>
      </div>
      <!--  -->
      <zoehis-scrollbar class="com_table__body" @scrollafter="bodyScrollEn" ref="comTableBody">
        <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__body-inner"><tbody>
        <tr v-for="(d1,index1) in tableData" :key="d1.uiKeyCode"
            :class="[ getRowClass(d1,index1)]"
            @mouseover="mouseOverEn(index1)" v-show="d1._uiFilterFlag"
            @mouseleave="hoverIndex=-1"
            @click="clickRowEn(d1,$event,index1)" @dblclick="dblclickRowEn(d1,$event,index1)">
          <td v-if='checkbox' :style="{width:checkboxWidth,visibility:leftVisible ? 'hidden' : 'visible'}">
            <zoehis-checkbox v-model="d1.checkboxFlag" @click.native.stop="checkboxClickEn(d1,index1)" :before-select="doBeforeSelect" @inputnative="checkboxInputEn(d1,index1)"></zoehis-checkbox>
          </td>
          <td v-for="(d2,index2) in tableConfig" :key="d2.dataItemCode"
              @click="clickColEn(d1,d2,$event,index1)" @dblclick="dblclickColEn(d1,d2,$event,index1)"
              :style="{width:d2.itemWidth}" :class="[`common_td_${d2.dataTextAlign||'center'}`]"
          >
            <template v-if="d2.uiIndexFlag">{{index1+1}}</template>
            <!--  -->
            <template v-else-if="(d2.fixedFlag!='left' && d2.fixedFlag!='right')||(d2.fixedFlag=='left'&&!leftVisible)||(d2.fixedFlag=='right'&&!rightVisible)">
              <slot :name="`td_${d2.dataItemCode}`" :item="d1" :index="index1">
                <div v-if="renderAttr.indexOf(d2.dataItemCode)!=-1 && renderHtml" v-html="renderHtml(d1[d2.dataItemCode],d1,d2)"></div>
                <div v-else :title="d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)"> {{d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)}}</div>
              </slot>
            </template>

          </td>
          <!--  -->
          <td class="zoehis_table_operator" @click="operateClickEn(d1,index1)" v-if="operateflag" :style="operateStyle" v-html="operateHtml(d1)"></td>
        </tr>
        <tr v-if="!tableData.length">
          <td v-if='checkbox' :style="{width:checkboxWidth,visibility:leftVisible ? 'hidden' : 'visible'}"></td>
          <td v-for="(d2,index2) in tableConfig" :key="d2.dataItemCode"
              :style="{width:d2.itemWidth}"
          ></td>
          <!--  -->
          <td v-if="operateflag" :style="operateStyle"></td>
        </tr>
        </tbody></table>
      </zoehis-scrollbar>
      <!--  -->
      <div class="com_table__fixed com_table__fixed-left" v-if="leftVisible" :style="leftFixedStyle">
        <div class="com_table__fixed-header">
          <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__fixed-header-inner"><thead>
          <tr>
            <th :rowspan="thRowspan" v-if='checkbox' :style="{width:checkboxWidth}"><zoehis-checkbox v-model="allSelectVal" @inputnative="selectAllEn" :before-select="beforeAllSelect"></zoehis-checkbox></th>
            <th :rowspan="d.uiColspan>1 ? 1 : thRowspan" :colspan="d.uiColspan" v-for="(d,index) in tableTopConfig" :key="d.dataItemCode"
                :style="{width:d.itemWidth}"
                @mouseout="mouseoutEn"
                @mousemove="mousemoveEn"
                @mousedown="mousedownEn($event,d)"
            >
              {{d.dataItemName}}
              <span class="com_sort_box" v-if="d.uiColspan<2 && d.dataItemSort=='1'">
                  <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                  <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
              </span><i v-if="hideColumnFlag && !d.uiIndexFlag" @click="handleHideColEn(d)" title="" class="zoehis_table_hidden_col zoeIconfont z_close_normal02" :class="{zoehis_col_two:d.uiColspan>1}"></i>
              <!---->
              <slot v-if="d.fixedFlag=='left' && d.uiColspan<2" :name="d.dataItemCode"></slot>
            </th>
            <!--  -->
            <th :rowspan="thRowspan" v-if='operateflag' :style="{width:operateWidth,visibility:'hidden'}"></th>
          </tr>
          <tr v-if="tableDownConfig.length">
            <!--  -->
            <th v-for="(d,index) in tableDownConfig" :key="d.dataItemCode"
                :style="{width:d.itemWidth}"
            >
              {{d.dataItemName}}
              <span class="com_sort_box" v-if="d.dataItemSort=='1'">
                  <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                  <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
              </span>
              <!---->
              <slot v-if="d.fixedFlag=='left'" :name="d.dataItemCode"></slot>
            </th>
          </tr>
          </thead></table>
        </div>
        <div class="com_table__fixed-body" ref="leftFixedBody" @mousewheel='fixedMousewheelEn("leftFixedBody")'
             @mouseenter="fixedMouseenterEn" @mouseleave="fixedMouseleaveEn"
        >
          <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__fixed-body-inner"><tbody>
          <tr v-for="(d1,index1) in tableData" :key="d1.uiKeyCode"
              :class="[ getRowClass(d1,index1)]"
              @mouseover="mouseOverEn(index1)" v-show="d1._uiFilterFlag"
              @mouseleave="hoverIndex=-1"
              @click="clickRowEn(d1,$event,index1)" @dblclick="dblclickRowEn(d1,$event,index1)">
            <td v-if='checkbox' :style="{width:checkboxWidth}">
              <zoehis-checkbox v-model="d1.checkboxFlag" @click.native.stop="checkboxClickEn(d1,index1)" :before-select="doBeforeSelect" @inputnative="checkboxInputEn(d1,index1)"></zoehis-checkbox>
            </td>
            <td v-for="(d2,index2) in tableConfig" :key="d2.dataItemCode"
                @click="clickColEn(d1,d2,$event,index1)" @dblclick="dblclickColEn(d1,d2,$event,index1)"
                :style="{width:d2.itemWidth}" :class="[`common_td_${d2.dataTextAlign||'center'}`]"
            >
              <!--  -->
              <template v-if="d2.fixedFlag=='left'">
                <template v-if="d2.uiIndexFlag">{{index1+1}}</template>
                <slot v-else :name="`td_${d2.dataItemCode}`" :item="d1" :index="index1">
                  <div v-if="renderAttr.indexOf(d2.dataItemCode)!=-1 && renderHtml" v-html="renderHtml(d1[d2.dataItemCode],d1,d2)"></div>
                  <div v-else :title="d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)"> {{d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)}}</div>
                </slot>
              </template>
            </td>
            <td v-if='operateflag' :style="{width:operateWidth,visibility:'hidden'}"></td>
          </tr>
          </tbody></table>
        </div>
      </div>
      <!--  -->
      <div class="com_table__fixed com_table__fixed-right" v-if="rightVisible" :style="rightFixedStyle">
        <div class="com_table__fixed-header" ref="rightFixedHeader">
          <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__fixed-header-inner"><thead>
          <tr>
            <th :rowspan="thRowspan" v-if='checkbox' :style="{width:checkboxWidth}"><zoehis-checkbox v-model="allSelectVal" @inputnative="selectAllEn" :before-select="beforeAllSelect"></zoehis-checkbox></th>
            <th :rowspan="d.uiColspan>1 ? 1 : thRowspan" :colspan="d.uiColspan" v-for="(d,index) in tableTopConfig" :key="d.dataItemCode"
                :style="{width:d.itemWidth}"
                @mouseout="mouseoutEn"
                @mousemove="mousemoveEn"
                @mousedown="mousedownEn($event,d)"
            >
              {{d.dataItemName}}
                 <span class="com_sort_box" v-if="d.uiColspan<2 && d.dataItemSort=='1'">
                    <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                    <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
                </span><i v-if="hideColumnFlag && !d.uiIndexFlag" @click="handleHideColEn(d)" title="" class="zoehis_table_hidden_col zoeIconfont z_close_normal02" :class="{zoehis_col_two:d.uiColspan>1}"></i>
                <!---->
                <slot v-if="d.fixedFlag=='right' && d.uiColspan<2" :name="d.dataItemCode"></slot>
            </th>
            <th :rowspan="thRowspan" v-if='operateflag' :style="operateStyle">{{i18nObj.operate}}</th>
          </tr>
          <tr v-if="tableDownConfig.length">
            <!--  -->
            <th v-for="(d,index) in tableDownConfig" :key="d.dataItemCode"
                :style="{width:d.itemWidth}"
            >
              {{d.dataItemName}}
                 <span class="com_sort_box" v-if="d.dataItemSort=='1'">
                    <i :class="{active:sortFlag==`${d.dataItemCode}up`}" class="sort_item sort_up" @click="sortUpEn(d)"></i>
                    <i :class="{active:sortFlag==`${d.dataItemCode}down`}" class="sort_item sort_down" @click="sortDownEn(d)"></i>
                </span>
                <!---->
                <slot v-if="d.fixedFlag=='right'" :name="d.dataItemCode"></slot>
            </th>
          </tr>
          </thead></table>
        </div>
        <div class="com_table__fixed-body" ref="rightFixedBody" @mousewheel='fixedMousewheelEn("rightFixedBody")'
             @mouseenter="fixedMouseenterEn" @mouseleave="fixedMouseleaveEn"
        >
          <table cellspacing="0" cellpadding="0" border="0" class="zoehis_table_box com_table__fixed-body-inner"><tbody>
          <tr v-for="(d1,index1) in tableData" :key="d1.uiKeyCode"
              :class="[ getRowClass(d1,index1)]"
              @mouseover="mouseOverEn(index1)" v-show="d1._uiFilterFlag"
              @mouseleave="hoverIndex=-1"
              @click="clickRowEn(d1,$event,index1)" @dblclick="dblclickRowEn(d1,$event,index1)">
            <td v-if='checkbox' :style="{width:checkboxWidth,visibility:'hidden'}"></td>
            <td v-for="(d2,index2) in tableConfig" :key="d2.dataItemCode"
                @click="clickColEn(d1,d2,$event,index1)" @dblclick="dblclickColEn(d1,d2,$event,index1)"
                :style="{width:d2.itemWidth}" :class="[`common_td_${d2.dataTextAlign||'center'}`]"
            >
              <!--  -->
              <template v-if="d2.fixedFlag=='right'">
                <slot :name="`td_${d2.dataItemCode}`" :item="d1" :index="index1">
                  <div v-html="renderHtml(d1[d2.dataItemCode],d1,d2)" v-if="renderAttr.indexOf(d2.dataItemCode)!=-1 && renderHtml"></div>
                  <div v-else :title="d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)"> {{d1[d2.dataItemCode] | _comTableFilters(d1,d2,doFilterFunc)}}</div>
                </slot>
              </template>
            </td>
            <td class="zoehis_table_operator" @click="operateClickEn(d1,index1)" v-if="operateflag" :style="operateStyle" v-html="operateHtml(d1)"></td>
          </tr>
          </tbody></table>
        </div>
      </div>
      <div class="zoehis_table_resize" ref="resizeProxy" v-show="resizeProxyVisible"></div>
    </div>
    <zoehis-page
      v-if="pageflag" class="zoehis_table_page_box"
      :strip-count="pageTotal" @changeStrip="changeStripEn" @changePage="changePageEn"
      :set_strip.sync="pageParam.pageSize" :current_page.sync="pageParam.page"
      :strip-arr="stripArr">
    </zoehis-page>
    <!-- (  ) -->
    <span class="zoehis_table_select_box" v-if="selectNum && pageflag && checkbox">
      <slot name="tableSelectNum" :num="tableSelectedNum"><strong>{{tableSelectedNum}}</strong></slot>
    </span>
    <!---->
    <div class="main_data_none" v-if="showNoneText" @mouseenter="fixedMouseenterEn" @mouseleave="fixedMouseleaveEn">{{i18nObj.dataNone}}</div>

    <zoehis-dialog :visible.sync="visible" title="" width="990" height="540"
                   append-to-body class="zoehis_table_filter_dlg">

      <div class="filter_inner">
        <div class="left_wrap">
          <zoehis-scrollbar class="filter_inner_order">
            <ul class="order_ul">
              <li v-for="(item,index) in filterData.advanceQuery.orders">
                <zoehis-select
                  v-model="item.key" :selectdata="tableConfig" itemcode="dataItemCode" itemtext="dataItemName"
                  width="46%" :textvalue.sync="item.dataItemName" placeholder=""
                ></zoehis-select>
                <zoehis-select
                  v-model="item.sort" :selectdata="sortList" itemcode="code" itemtext="text"
                  width="46%" :textvalue.sync="item.text" placeholder=""
                ></zoehis-select>
              </li>
            </ul>
            <div class="footer_btn">
              <!---->
              <zoehis-button size="small" @clickenter="addOrders">{{i18nObj.addButtonText}}</zoehis-button>
              <!---->
              <zoehis-button size="small" @clickenter="deleteOrders" :disabled="filterData.advanceQuery.orders.length<=1">{{i18nObj.deleteButtonText}}</zoehis-button>
            </div>
          </zoehis-scrollbar>
        </div>
        <div class="right_wrap">
          <zoehis-scrollbar class="filter_inner_conditions">
            <div class="condition_list_main">
              <div v-for="(item,index) in filterData.advanceQuery.conditions" class="listItem" :class="{'listBr':item.firSt}">
                <span v-show="item.firSt" class="seqSpan">{{Math.floor(index/3)+1}}</span>
                <zoehis-select class="item_sel" placeholder=""
                  v-model="item.key" :selectdata="tableConfig" itemcode="dataItemCode" itemtext="dataItemName"
                  width="98" :textvalue.sync="item.dataItemName"
                ></zoehis-select>
                <zoehis-select placeholder=""
                  v-model="item.op" :selectdata="opList" itemcode="code" itemtext="text"
                  width="98" :textvalue.sync="item.text"
                ></zoehis-select>
                <zoehis-input v-model="item.value" width="40%" placeholder=""></zoehis-input>
                <div class="icon_btn">
                  <i class='zoeIconfont z_add_normal02' @click="addConList(item,index)"></i>
                  <i class='zoeIconfont z_wrong_normal' v-show="!item.firSt" @click="selConList(item,index)"></i>
                </div>
                <zoehis-select :class="{'rela_sel':item.lastSt}" v-show="index!=filterData.advanceQuery.conditions.length-1"
                  v-model="item.relation" :selectdata="relateList" itemcode="code" itemtext="text"
                  width="74" :textvalue.sync="item.text" placeholder=""
                ></zoehis-select>
              </div>
            </div>
            <div class="footer_btn">
              <!---->
              <zoehis-button size="small" @clickenter="addConditions">{{i18nObj.addButtonText}}</zoehis-button>
              <!---->
              <zoehis-button size="small" @clickenter="delConditions" :disabled="filterData.advanceQuery.conditions.length<=1">{{i18nObj.deleteButtonText}}</zoehis-button>
            </div>
          </zoehis-scrollbar>
        </div>
      </div>
      <span slot="footer">
        <!---->
        <zoehis-button icon="z_examine_normal" type="primary" @clickenter="filterConfirmEn">{{i18nObj.confirmButtonText}}</zoehis-button>
        <!---->
        <zoehis-button icon="z_cancellationOA_normal02" @clickenter="visible=false">{{i18nObj.cancelButtonText}}</zoehis-button>
      </span>
    </zoehis-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
  import normalizeWheel from "normalize-wheel";
  import { isNull,trim,downloadToExcel } from '../../../src/utils/util';
  import dateUtil from '../../../src/utils/date';
  import debounce from '../../../src/utils/throttle-debounce.js';
  import {addResizeListener,removeResizeListener} from '../../../src/utils/resize-event';//resize()

  export default {
    name: 'zoehisTable',
    componentName: 'zoehisTable',
    data () {
      return {
        allSelectVal:false,
        //filterType: 'other'.;  ''. - ''.
        tableConfig:[],
        tableTopConfig:[],
        tableDownConfig:[],
        temp_tableTopConfig:[],
        tableData:[],
        //
        leftFixedAttr:{
          width:"",
          validFlag:false,
        },
        //
        rightFixedAttr:{
          width:"",
          validFlag:false,
        },
        hoverIndex:-1,//hoevr
        selectKeyCode:"",//
        scrollPosition:"left",//
        fixedHoverFlag:false,//
        sortFlag:"",
        resizeProxyVisible:false,//
        realBoxWidth:300,//
        boxWidth:300,//
        //=====
        visible:false,
        relateList:[{id:"1",text:"",code:"and"},{id:"2",text:"",code:"or"}],
        filterSelectIndex:-1,
        showNoneData:false,//
        filterData:{ //
          advanceQuery:{
            orders:[],
            conditions:[]
          }
        },
        sortList:[{code:"asc",text:""},{code:"desc",text:""}],
        opList:[
          {code:"eq",text:"",value:"1"},
          {code:"gte",text:"",value:"1"},
          {code:"lte",text:"",value:"1"},
          {code:"between",text:"",value:"2"},
          {code:"in",text:"",value:"1"},
          {code:"notin",text:"",value:"1"},
          {code:"no",text:"",value:""},
          {code:"yes",text:"",value:""},
          {code:"like",text:"",value:" 1  value  OR "},
        ],


        i18nObj:{
          confirmButtonText:"",
          addButtonText:"",
          deleteButtonText:"",
          cancelButtonText:"",
          rowName:"",
          operate:"",
          operateName:"",
          rowValue:"",
          relateName:"",
          dataNone:"",
          excelDataNone:"",
        },
        //
        tableSelectedNum:0
      }
    },
    props:{
      checkbox:Boolean,// 
      checkboxfixed:Boolean,// 
      checkboxWidth: {
        type: String,
        default: "50px"
      },
      indexflag:Boolean,//  
      indexfixed:Boolean,// 
      indexWidth: {
        type: String,
        default: "50px"
      },
      operateflag:Boolean,//
      operatefixed:Boolean,// 
      operateWidth: {//
        type: String,
        default: "100px"
      },
      operateHtml:Function,
      operateText:String,
      operateAlign:{type:String,default:"left"},//

      // checkboxWidth:{type:String,defualt:"100px"}
      doFilterFunc:Function,//: 1.  2.  3.
      renderAttr:{type:Array,default(){return [];}},//  ['name','age']
      renderHtml:Function,
      pageflag:Boolean,
      //
      pageParam:{
        type:Object,
        default(){
          return { total:0, page:1, pageSize:30 };
        }
      },
      data:{
        type:Array,
        default(){ return []; }
      },
      configs:{
        type:Array,
        default(){ return []; }
      },//
      sortAttr:{type:Array,default(){return [];}},//  ['name','age'] dataItemSort=1, doSortFunc
      doSortFunc:Function,
      keycode:{type:[Array,String],required:true},
      //
      stripArr:{
        type:Array, default(){
          return [10,20,30,40];
        }
      },
      beforeRowClick:Function,//
      beforeSelect:Function,//
      beforeSelectAll:Function,//

      operatorList:{type:Array,default(){
        return [{id:"=",text:""}];
      }},
      samefilter:{type:Boolean,default:true},// samefilter true
      rowClassName:[Function,String],
      hideColumnFlag:Boolean,//   false,true
      selectNum:Boolean,//
    },
    computed:{
      //
      leftFixedStyle(){
        return {
          width:this.leftFixedAttr.width+'px'
        };
      },
      //
      rightFixedStyle(){
        return {
          width:this.rightFixedAttr.width+'px'
        };
      },
      // rowspan
      thRowspan(){
        return this.tableDownConfig.length?2:1;
      },
      //
      leftVisible(){
        //
        return this.realBoxWidth>this.boxWidth && this.leftFixedAttr.validFlag;
      },
      //
      rightVisible(){
        return this.realBoxWidth>this.boxWidth && this.rightFixedAttr.validFlag;
      },
      leftRightFixedChange(){
        return (this.rightVisible || this.leftVisible)
      },
      //
      pageTotal(){
        let flag = isNaN(this.pageParam.total);//true
        if(flag) return 0;
        let t = Number(this.pageParam.total);// total   0
        if(t<=0) return 0;
        return t;
      },
      //style
      operateStyle(){
        return {
          width:this.operateWidth,
          textAlign:this.operateAlign
        }
      },
      //  
      showNoneText(){
        let isShow = false;
        for(let i=0,l=this.tableData.length; i<l; i++){
          if(this.tableData[i]._uiFilterFlag){
            isShow = true;
            break;
          }
        }
        return !isShow;
      }
    },
    watch:{
      //
      data:{
        immediate:true,
        handler(val){
          this.selectKeyCode="";
          let arr = [];
          let d = {};
          val.forEach((item,index)=>{
            //key  checkboxFlag
            let key = "";
            if(typeof this.keycode=="string"){
              key = item[this.keycode];
            }else {
              this.keycode.forEach((res)=>{
                key+=item[res]
              });
            }
            d=Object.assign({},item,{_uiFilterFlag:true,checkboxFlag:false,uiKeyCode:key||index});
            arr.push(d);
          });
          this.allSelectVal=false;//false
          this.tableSelectedNum = 0;
          this.tableData=arr;
          this.$nextTick(()=>{
            if(this.$refs.comTableBody && this.$refs.comTableBody.$el){
              this.doBodyScroll(this.$refs.comTableBody.$el);
              this.$refs.comTableBody.move({left:0,top:0});//  
            }
          });
        }
      },
      configs:{
        immediate:true,
        handler(val){
          this.initFormatData(val);
        }
      },
      //
      leftRightFixedChange(val){
        // 
        if(val){
          this.updateScrollbar();
        }
      },
      //
      tableData:{
        deep:true,
        handler(val){
          this.$emit("change",val);

        }
      },
      //
      filterData:{
        deep:true,
        handler(val){
          let condi=this.filterData.advanceQuery.conditions;
          let group=1;
          for(let i=0,l=condi.length; i<l; i++){
            condi[i].lastSt=false;
            if(condi[i].group==group){
              condi[i].firSt=true;
              group++;
              if(i>=1){
                condi[i-1].lastSt=true;
              }
            }
          }
        }
      },
      //
      tableSelectedNum(val){
        this.$emit("select-num",val);
      },
    },
    filters:{
      //1.  2.  3.
      _comTableFilters(val,item,config,doFilterFunc){
        if(isNull(config.filterType)){
          return isNull(val) ? "-" : val;
        }
        let showStr = "";
        if(config.filterType == 'other'){
          showStr = (typeof doFilterFunc=='function') ? doFilterFunc(val,item,config) : isNull(val) ? "-" : val;
        }else{
          showStr = isNull(val) ? "-" :dateUtil.format(val,config.filterType);
        }
        return showStr;
      }
    },
    methods:{
      /**
       * 
       * @param {String}  "1"/"0"
       * @param {Object} 
       */
      inputnativeEn(val,col){
        this.doToggleCol(col,val);
      },
      /**
       * 
       * @param {Object} col: 
       */
      handleHideColEn(col){
        this.doToggleCol(col,"0");
      },
      /**
       * 
       * @param {Object} col: 
       */
      doToggleCol(col,validFlag="0"){
        //
        if(col.uiColspan<2){
          let dataItemCode = col.dataItemCode;
          for(let i=0;i<this.temp_tableConfig.length; i++){
            if(this.temp_tableConfig[i].dataItemCode==dataItemCode){
              this.temp_tableConfig[i] = Object.assign({},this.temp_tableConfig[i],{validFlag:validFlag});
              break;
            }
          }
          for(let i=0;i<this.temp_tableTopConfig.length; i++){
            if(this.temp_tableTopConfig[i].dataItemCode==dataItemCode){
              this.temp_tableTopConfig[i] = Object.assign({},this.temp_tableTopConfig[i],{validFlag:validFlag});
              break;
            }
          }
        }else{
          let patientItemName = col.patientItemName;
          if(!patientItemName) return;// 
          for(let i=0;i<this.temp_tableConfig.length; i++){
            if(this.temp_tableConfig[i].patientItemName==patientItemName){
              this.temp_tableConfig[i] = Object.assign({},this.temp_tableConfig[i],{validFlag:validFlag});
            }
          }
          for(let i=0;i<this.temp_tableTopConfig.length; i++){
            if(this.temp_tableTopConfig[i].patientItemName==patientItemName){
              this.temp_tableTopConfig[i] = Object.assign({},this.temp_tableTopConfig[i],{validFlag:validFlag});
              break;
            }
          }
          for(let i=0;i<this.temp_tableDownConfig.length; i++){
            if(this.temp_tableDownConfig[i].patientItemName==patientItemName){
              this.temp_tableDownConfig[i] = Object.assign({},this.temp_tableDownConfig[i],{validFlag:validFlag});
            }
          }
        }
        let tableConfig1=JSON.parse(JSON.stringify(this.temp_tableConfig));
        let tableTopConfig1=JSON.parse(JSON.stringify(this.temp_tableTopConfig));
        let tableDownConfig1=JSON.parse(JSON.stringify(this.temp_tableDownConfig));
        this.tableConfig=tableConfig1.filter(res=>(res.validFlag=='1'));
        this.tableTopConfig=tableTopConfig1.filter(res=>(res.validFlag=='1'));
        this.tableDownConfig=tableDownConfig1.filter(res=>(res.validFlag=='1'));
        this.getFixedWidth();
        this.updateScrollbar();
      },
      /**
       *  excel
       * @param config: config,
       * @return {Array}
       */
      getExcelHead(config=[]){
        let head = [];
        // 
        if(config.length){
          head.push("<tr>");
          config.forEach(item=>{
            head.push(`<th>${item.dataItemName}</th>`);
          });
          head.push("</tr>");
          return head;
        }
         //
        let downConfig = JSON.parse(JSON.stringify(this.tableDownConfig));
        head = ["<tr>"];
        let downArr=[];
        this.tableTopConfig.forEach(item=>{
          //
          if(!item.uiIndexFlag){
            let u = parseInt(item.uiColspan||1);
            if(u>1){
              for(let i=0; i<u;i++){
                downArr.push(downConfig.shift().dataItemName);
              }
              head.push(`<th colspan='${item.uiColspan}'>`);
            }else{
              downArr.push("");
              head.push("<th>");
            }
            head.push(item.dataItemName);
            head.push("</th>");
          }
        });
        head.push("</tr>");
        if(downArr.length){
          head.push("<tr>");
          downArr.forEach(item=>{
            head.push(`<th>${item}</th>`);
          });
          head.push("</tr>");
        }
        return head;
      },
      /**
       * excel
       * @param {Array} excelData: 
       * @param {String} fileName: excel
       * @param {String} worksheet: exceltab
       * @param {Array} tableConfig: table
       */
      tableToExcel(excelData,fileName="",worksheet="Sheet1",tableConfig=[]){
        if(!excelData.length){
          //
          this.$messageBox.warn(this.i18nObj.excelDataNone);
          return;
        }
        //
        let head = this.getExcelHead(tableConfig);
   
        let allConfig = JSON.parse(JSON.stringify(this.tableConfig));
        allConfig = allConfig.filter(res=>(!res.uiIndexFlag));
        allConfig = tableConfig.length ? tableConfig : allConfig;
        //
        excelData.forEach(item=>{
          head.push("<tr>");
          allConfig.forEach(item2=>{
            head.push(`<td>${item[item2.dataItemCode]}</td>`);
          });
          head.push("</tr>");
        });
        downloadToExcel(head.join(""),fileName,worksheet);
      },
      //
      // left:0/top:0
      comTableMove(obj={left:0,top:0}){
        if(this.$refs.comTableBody){
          this.$refs.comTableBody.move(obj);
        }
      },
      /**  start **/
      //
      focusTb(refName="filterTd0",index=0){
        this.$nextTick(()=>{
          let refVms = this.$refs[refName];
          refVms && refVms[index] && refVms[index].focus();
        });
      },
      //
      filterConfirmEn(){
        //
        let advan=this.filterData.advanceQuery;
        for(var i=advan.orders.length-1;i>=0;i--){
          if(advan.orders[i].key==''){
            advan.orders.splice(i,1);
          }
        }
        for(var i=advan.conditions.length-1;i>=0;i--){
          if(advan.conditions[i].key==''){
            advan.conditions.splice(i,1);
          }
        }
        this.$emit("confirm-filter",this.filterData);
        this.closeFilter();
      },
      //
      addOrders(){
        let item = {key:"",sort:""};
        this.filterData.advanceQuery.orders.push(item);
      },
      //
      deleteOrders(){
        if(this.filterData.advanceQuery.orders.length<=1)return;
        this.filterData.advanceQuery.orders.pop();
      },
      //
      addConditions(){
        let group=0;
        let listnum=this.filterData.advanceQuery.conditions.length;
        if(listnum<=0){
          group=1;
        }else{
          group=this.filterData.advanceQuery.conditions[listnum-1].group+1;
        }
        let item = [
          {key:"",name:"",op:"",value:"",relation:"",group:group},
          {key:"",name:"",op:"",value:"",relation:"",group:group},
          {key:"",name:"",op:"",value:"",relation:"",group:group}
        ];
        this.filterData.advanceQuery.conditions=this.filterData.advanceQuery.conditions.concat(item);
      },
      //
      delConditions(){
        let condi=this.filterData.advanceQuery.conditions;
        let listnum=condi.length;
        let conArr=condi;
        let group=condi[listnum-1].group;
        if(group<=1)return;
        for(let i=conArr.length-1;i>=0;i--){
          if(conArr[i].group==group){
            this.filterData.advanceQuery.conditions.pop();
          }else{
            break
          }
        }
      },
      //li
      addConList(d,index){
        let list = {key:"",name:"",op:"",value:"",relation:"",group:d.group};
        this.filterData.advanceQuery.conditions.splice(index+1,0,list);
      },
      //li
      selConList(d,index){
        this.filterData.advanceQuery.conditions.splice(index,1);
      },
      //
      openFilter(){
        this.visible = true;
        this.filterData={ //
          advanceQuery:{
            orders:[],
            conditions:[]
          }
        };
        this.addOrders();
        this.addConditions();
        this.filterSelectIndex = 0;
      },
      //
      closeFilter(){
        this.visible = false;
      },
      clearFilterList(){
        this.filterData={ //
          advanceQuery:{
            orders:[],
            conditions:[]
          }
        };
        this.filterSelectIndex=-1;
      },
      /**  end **/


      //
      mouseoutEn(){
        this.$refs.comTable.style.cursor = '';
      },
      //
      mousemoveEn(event){
        //currentTarget target
        let rect = event.currentTarget.getBoundingClientRect();
        if (rect.width > 12 && rect.right - event.pageX < 8 && !this.dragging) {
          //
          this.$refs.comTable.style.cursor = 'col-resize';
          this.moveFlag = true;//
        }else {
          this.$refs.comTable.style.cursor = '';
          this.moveFlag = false;//false
        }
      },
      mousedownEn(event, column) {
        if(!this.moveFlag) return;// return
        this.dragging = true;//
        this.resizeProxyVisible = true;
        const tableEl = this.$el;
        const {left:tableLeft,width:tableWidth} = tableEl.getBoundingClientRect();
        const columnRect = event.currentTarget.getBoundingClientRect();
        const minLeft = columnRect.left - tableLeft + 30;
//        const maxLeft = tableWidth - 30;
        this.dragState = {
          tableWidth,//
          startMouseLeft: event.clientX,//X
          startLeft: columnRect.right - tableLeft,// 
          startColumnLeft: columnRect.left - tableLeft,//
          tableLeft,//left
        };
        const resizeProxy = this.$refs.resizeProxy;
        resizeProxy.style.left = this.dragState.startLeft + 'px';
        document.onselectstart = function() { return false; };//
        document.ondragstart = function() { return false; };

        const handleMouseMove = (event) => {
          const deltaLeft = event.clientX - this.dragState.startMouseLeft;
          const proxyLeft = this.dragState.startLeft + deltaLeft;

          resizeProxy.style.left = Math.max(minLeft, proxyLeft) + 'px';
        };

        const handleMouseUp = () => {
          const {
            startColumnLeft,
            startLeft
            } = this.dragState;
          const finalLeft = parseInt(resizeProxy.style.left, 10);
          this.changeWidthByColumn(column,finalLeft-startColumnLeft,startLeft-startColumnLeft);
          this.dealTwoHead();
          this.getFixedWidth();
          this.updateScrollbar();

          this.$refs.comTable.style.cursor = '';
          this.dragging = false;
          this.dragState = {};

          this.resizeProxyVisible = false;

          document.removeEventListener('mousemove', handleMouseMove);
          document.removeEventListener('mouseup', handleMouseUp);
          document.onselectstart = null;
          document.ondragstart = null;
        };

        document.addEventListener('mousemove', handleMouseMove);
        document.addEventListener('mouseup', handleMouseUp);
      },
      changeWidthByColumn(column,width,oldWidth){
        let setW = width,curIndex = -1;
        for(let i=0,l=this.tableConfig.length;i<l; i++){
          if(column.dataItemCode==this.tableConfig[i].dataItemCode){
            let curWidth = parseFloat(this.tableConfig[i].itemWidth);
            if(this.realBoxWidth-oldWidth+width < this.boxWidth){
              // / = (itemWidth-width)/(itemWidth-width)
              // width/this.boxWidth = (curWidth-x)/(this.realBoxWidth-x)   realBoxWidthboxWidth
              let x = (this.realBoxWidth*(width/this.boxWidth)-curWidth)/(width/this.boxWidth-1);
              setW = curWidth-x;
              this.realBoxWidth = this.realBoxWidth-x;
            }
            curIndex = i;
            break;
          }
        }
        if(curIndex==-1) return;
        if(column.uiColspan>1){
          let w = setW/column.uiColspan;
          for(let i=0,l=this.tableConfig.length;i<l; i++){
            if(column.patientItemName==this.tableConfig[i].patientItemName){
              this.tableConfig[i].itemWidth = w+"px";
            }
          }
          for(let i=0,l=this.tableTopConfig.length;i<l; i++){
            if(column.patientItemName==this.tableTopConfig[i].patientItemName){
              this.tableTopConfig[i].itemWidth = w+"px";
            }
          }
          for(let i=0,l=this.tableDownConfig.length;i<l; i++){
            if(column.patientItemName==this.tableDownConfig[i].patientItemName){
              this.tableDownConfig[i].itemWidth = w+"px";
            }
          }
        }else {
          this.tableConfig[curIndex].itemWidth = setW+"px";
        }

      },
      //
      sortUpEn(d){
        if(!this.tableData.length) return;
        let code=d.dataItemCode;
        this.sortFlag=code+"up";
        if(this.sortAttr.length && (this.sortAttr.indexOf(code)!=-1)){
          if(typeof this.doSortFunc == 'function'){
            this.tableData = this.doSortFunc(this.tableData,d,"up");
          }
          this.$emit("sort-change",d,"up");
          return;
        }
        this.tableData.sort((a,b)=>{
          return (""+a[code]).localeCompare(b[code]);
        });
      },
      //
      sortDownEn(d){
        if(!this.tableData.length) return;
        let code=d.dataItemCode;
        this.sortFlag=code+"down";
        if(this.sortAttr.length && (this.sortAttr.indexOf(code)!=-1)){
          if(typeof this.doSortFunc == 'function'){
            this.tableData = this.doSortFunc(this.tableData,d,"down");
          }
          this.$emit("sort-change",d,"down");
          return;
        }
        this.tableData.sort((a,b)=>{
          return (""+b[code]).localeCompare(a[code]);
        });
      },
      //
      operateClickEn(item,index1){
        this.$emit("operate-click",event,item,index1);
      },
      //
      clickRowEn(item,event,index1){
        // return false 
        if(this.beforeRowClick && typeof this.beforeRowClick=="function" && (this.beforeRowClick(item,event,index1)===false)){
          return ;
        }
        this.selectKeyCode=item.uiKeyCode;
        this.$emit("row-click",item,event,index1)
      },
      //
      dblclickRowEn(item,event,index1){
        this.$emit("row-dblclick",item,event,index1)
      },
      //
      clickColEn(row,column,event,index){
        let data = {row,column,event,index};
        this.$emit("col-click",data)
      },
      //
      dblclickColEn(row,column,event,index){
        let data = {row,column,event,index};
        this.$emit("col-dblclick",data)
      },
      //
      selectAllEn(val){
        this.tableData.forEach(item=>{
          item.checkboxFlag=val;
        });
        this.tableSelectedNum = val?this.tableData.length:0;
        this.$emit("select-all",val?this.tableData:[]);
      },
      //
      beforeAllSelect(){
        if(this.beforeSelectAll && typeof this.beforeSelectAll=="function"){
          return this.beforeSelectAll(this.allSelectVal);
        }
        return true;
      },

      //
      checkboxInputEn(item,index1){
        let selectArr = this.getSelectArr();
        //
        this.allSelectVal = selectArr.length==this.tableData.length;
        this.tableSelectedNum = selectArr.length;
        this.$emit("select",selectArr,item,index1);
      },
      checkboxClickEn(item,index1){
        this._curIndex = index1;//
        this._curItem = Object.assign({},item);//
      },
      doBeforeSelect(){
        if(this.beforeSelect && typeof this.beforeSelect=="function"){
          let selectArr = this.getSelectArr();
          return this.beforeSelect(selectArr,this._curItem,this._curIndex);
        }
        return true;
      },
      //
      changeStripEn(val){
        this.$emit("change-strip",val);
      },
      //
      changePageEn(val){
        this.$emit("change-page",val);
      },
      fixedMouseleaveEn(){
        this.fixedHoverFlag=false;
      },
      fixedMouseenterEn(){
        this.fixedHoverFlag=true;
      },
      mouseOverEn(index){
        this.hoverIndex=index;
      },
      //
      bodyScrollEn(e){
        this.doBodyScroll(e);
        this.$emit("scrollafter",e);
      },
      //
      doBodyScroll(e){
        const { comTableHeader,leftFixedBody, rightFixedBody } = this.$refs;
        const {scrollLeft,scrollTop,scrollWidth} = e;
        //
        if(comTableHeader) comTableHeader.scrollLeft = scrollLeft;
        if(leftFixedBody) leftFixedBody.scrollTop = scrollTop;
        if(rightFixedBody) rightFixedBody.scrollTop = scrollTop;
        if(scrollLeft==0 && scrollWidth-scrollLeft<=this.$el.scrollWidth){
          this.scrollPosition='none';
        }else if(scrollLeft==0){
          this.scrollPosition='left';
        }else if(scrollWidth-scrollLeft<=this.$el.scrollWidth){
          this.scrollPosition='right';
        }else{
          this.scrollPosition='middle';
        }
      },
      //
      getFixedWidth(){
        let leftW = 0;
        let rightW = 0;
        let boxW = 0;
        this.tableConfig.forEach(element => {
          if(element.fixedFlag=='left'){
            leftW+=parseFloat(element.itemWidth);
          }else if(element.fixedFlag=='right'){
            rightW+=parseFloat(element.itemWidth);
          }
          boxW+=parseFloat(element.itemWidth);
        });
        //
        if(this.checkbox){
          boxW+=parseFloat(this.checkboxWidth);
        }
        if(this.operateflag){
          boxW+=parseFloat(this.operateWidth);
        }
        this.realBoxWidth=boxW;
        //  parseFloast '200px'=>200; "20%"=>20
        //checkboxWidth 
        // checkboxfixedtrue  

        if(this.checkbox && (this.checkboxfixed || leftW!=0 )){
          leftW+=parseFloat(this.checkboxWidth);
        }
        // operatefixedtrue  
        if(this.operateflag && (this.operatefixed || rightW!=0)){
          rightW+=parseFloat(this.operateWidth);
        }
        this.leftFixedAttr={
          width:leftW,
          validFlag:leftW!=0
        };
        this.rightFixedAttr={
          width:rightW,
          validFlag:rightW!=0
        };
      },
      //
      fixedMousewheelEn(refName){
        const normalized = normalizeWheel(event);
        const bodyWrapper = this.$refs[refName];
        if (Math.abs(normalized.spinY) > 0) {
          const currentScrollTop = bodyWrapper.scrollTop;

          //,
          if (normalized.pixelY < 0 && currentScrollTop !== 0) {
            event.preventDefault();
          }
          if (normalized.pixelY > 0 && bodyWrapper.scrollHeight - bodyWrapper.clientHeight > currentScrollTop) {
            event.preventDefault();
          }

          bodyWrapper.scrollTop += Math.ceil(normalized.pixelY / 5);
          this.$refs.comTableBody.move({top:bodyWrapper.scrollTop})
        } else {
          bodyWrapper.scrollLeft += Math.ceil(normalized.pixelX / 5);
          this.$refs.comTableBody.move({left:bodyWrapper.scrollLeft})

        }
      },
      //
      initFormatData(arr=[]){
        if(!arr.length){
          this.tableConfig=[];
          this.tableTopConfig=[];
          this.tableDownConfig=[];
          
          this.temp_tableConfig=[];
          this.temp_tableTopConfig=[];
          this.temp_tableDownConfig=[];
          return;
        }
        this.initSourceConfig(arr);
        //
        this.dealTwoHead();
        //
        this.getFixedWidth();
        this.updateScrollbar();
        this.temp_tableConfig=JSON.parse(JSON.stringify(this.tableConfig));
        this.temp_tableTopConfig=JSON.parse(JSON.stringify(this.tableTopConfig));
        this.temp_tableDownConfig=JSON.parse(JSON.stringify(this.tableDownConfig));
        
      },
      // configs
      initSourceConfig(arr){
        let t = JSON.parse(JSON.stringify(arr));
        // itemWidth 80
        //
        let temp=[];
        if(this.indexflag){
          temp = [{uiIndexFlag:true,sortNo:-1,validFlag:"1", dataItemSort:"", dataTextAlign:"",patientItemName:"",dataItemCode:"zoehis_table_index",dataItemName:"",filterType:"",itemWidth:this.indexWidth,fixedFlag:this.indexfixed?"left":""}];
        }
        t.forEach((item)=>{
          item.fixedFlag = item.fixedFlag || "";//undefined
          if(item.validFlag=='1'){
            temp.push(Object.assign(item,{itemWidth:(parseFloat(item.itemWidth) || 80)+'px',uiIndexFlag:false,fixedFlag:trim(item.fixedFlag)}))
          }
        });
        //  sort 
        temp.sort(function(a,b){
          return a.sortNo - b.sortNo;
        });

        // fixedFlag 
        let arrL = [];
        let arrM = [];
        let arrR = [];
        temp.forEach((item)=>{
          if(item.fixedFlag=="left") {
            arrL.push(item);
          }else if(item.fixedFlag=="right"){
            arrR.push(item);
          }else {
            arrM.push(item);
          }
        });
        this.tableConfig = [...arrL,...arrM,...arrR];
      },
      //
      dealTwoHead(){
        let l= this.tableConfig.length;
        let topConfig = [], downConfig=[];
        for(let i=0; i<l; i++){
          let d = Object.assign({},this.tableConfig[i],{uiColspan:1});
          if(isNull(d.patientItemName)){
            topConfig.push(d);
          }else{
            let isTheSame = [];
            let f = i+1;
            let sameWidth = parseFloat(d.itemWidth);
            //patientItemName 
            for(let j=i+1; j<l; j++){
              let d2 = Object.assign({},this.tableConfig[j]);
              if(d.patientItemName==d2.patientItemName){
                isTheSame.push(d2);
                sameWidth+=parseFloat(d2.itemWidth);
                // patientItemName 

                if(isTheSame.length+i<j){
                  this.tableConfig.splice(j,1);
                  this.tableConfig.splice(isTheSame.length+i,0,d2);
                }
                f=isTheSame.length+i;
              }
            }
            //
            if(isTheSame.length){
              // push
              downConfig.push(d);
              downConfig = downConfig.concat(isTheSame);
              i=f;// patientItemNameitem
              // uiColspan  tablecolspan
              let d2 = Object.assign({},d,{dataItemName:d.patientItemName,uiColspan:isTheSame.length+1,itemWidth:sameWidth+"px"});
              topConfig.push(d2);//  patientItemName
            }else{
              topConfig.push(d);// 
            }
          }
        }
        this.tableTopConfig = topConfig;
        this.tableDownConfig = downConfig;
      },
      // 
      updateScrollbar(){
        this.$nextTick(()=>{
          let {comTableBody} = this.$refs;
          //
          if(comTableBody){
            comTableBody.update();
            comTableBody.handleScroll();
            // zoehis_scrollbar__wrap 
            this.doBodyScroll(comTableBody.$el.firstElementChild);
          }
        });
      },
      initBoxWidth(){
        this.boxWidth=this.$el.getBoundingClientRect().width;
      },
      //=======================================
      //======= start========
      //=======================================

      /**
       *   
       * flagtruefalse
       */
      toggleAllRowSelection(flag=false){
        this.allSelectVal=flag;//false
        this.tableData.forEach(item=>{
          item.checkboxFlag=flag;
        });
        this.tableSelectedNum = flag?this.tableData.length:0;
      },
      /**
       * selected  true 
       * @param row 
       * @param selected 
       */
      toggleRowSelection(row,selected){
        // row key
        let key = "";
        if(typeof this.keycode=="string"){
          key = row[this.keycode];
        }else {
          this.keycode.forEach((res)=>{
            key+=row[res]
          });
        }
        for(let i=0,l=this.tableData.length; i<l; i++){
          if(this.tableData[i].uiKeyCode==key){
            //selectedBoolean
            if(typeof selected=="boolean"){
              this.tableData[i].checkboxFlag=selected;
            }else {
              this.tableData[i].checkboxFlag=!this.tableData[i].checkboxFlag;
            }
            this.updateAllSelect();
            this.updateSelectNum();
            break;
          }
        }
      },
      /**
       * 
       */
      inverseSelection(){
        for(let i=0,l=this.tableData.length; i<l; i++){
          this.tableData[i].checkboxFlag = !this.tableData[i].checkboxFlag;
        }
        this.updateAllSelect();
        this.updateSelectNum();
      },
      /**
       *  
       */
      setCurrentRow(row){
        //
        if(!row){
          this.selectKeyCode="";
          return;
        }
        // row key
        let key = "";
        if(typeof this.keycode=="string"){
          key = row[this.keycode];
        }else {
          this.keycode.forEach((res)=>{
            key+=row[res]
          });
        }
        this.selectKeyCode=key;
      },
      /**
       * 
       * @return {Array}
       **/
      getData(){
        return this.tableData;
      },
      /**
       *   
       * @param newRow:  Array() / Object()
       * @param curRow: rowindex
       * @param isCheckFlag:  true:false
       *
       */
      insertBefore(newRow,curRow,isCheckFlag){
        this.insertBase(newRow,curRow,isCheckFlag,false)
      },
      /**
       *   
       * @param newRow:  Array() / Object()
       * @param curRow: rowindex
       * @param isCheckFlag:  true:false
       *
       */
      insertAfter(newRow,curRow,isCheckFlag){
        this.insertBase(newRow,curRow,isCheckFlag,true)
      },
      /**
       * 
       * @param data:{Array()/Object()/Number()}
       **/
      deleteRow(data){
        //
        let dataType = Object.prototype.toString.call(data);
        switch (dataType){
          case "[object Array]":
            data.forEach(item=>{
              let curIndex = this.getCurIndex(item);
              if(curIndex!=-1){
                this.tableData.splice(curIndex,1);
              }
            });
            break;
          case "[object Object]":
            let curIndex = this.getCurIndex(data);
            if(curIndex!=-1){
              this.tableData.splice(curIndex,1);
            }
            break;
          default:
            data = parseInt(data);
            if(isNaN(data) || data<0 || data>=this.tableData.length) break;
            this.tableData.splice(data,1);
        }
        this.updateAllSelect();
        this.updateSelectNum();
      },
      /**
       * 
       * @param curRow:{ Object(keycode) / Number/String()}
       * @param newRow: Object
       **/
      editRow(curRow,newRow){
        let dataType = Object.prototype.toString.call(curRow);
        let curIndex=-1;
        if(dataType==="[object Object]"){
          curIndex = this.getCurIndex(curRow);//
        }else{
          curIndex = parseInt(curRow);
        }
        if(isNaN(curIndex) || curIndex<0 || curIndex>=this.tableData.length) return;
        Object.assign(this.tableData[curIndex],newRow);
      },
      /**
       * 
       **/
      getSelectArr(){
        let arr=[];
        this.tableData.filter((item,index)=>{
          if(item.checkboxFlag){
            arr.push(this.data[index]);
          }
        });
        return arr;
      },
      /**
       * 
       **/
      doFilterData(fn){
        if(!fn) return;
        for(let i=0,l=this.tableData.length; i<l; i++){
          this.tableData[i]._uiFilterFlag = (fn(this.tableData[i])===true);
        }
      },
      /**
       * class
       */
      getRowClass(row,rowIndex){
        const rowClassName = this.rowClassName;
        let classs = [];
        if(this.hoverIndex == rowIndex){
          classs.push("tr_hover");
        }
        if(this.selectKeyCode == row.uiKeyCode){
          classs.push("tr_selected");
        }
        if(typeof rowClassName === "string"){
          classs.push(rowClassName);
        }else if(typeof rowClassName === "function"){
          classs.push(rowClassName(row,rowIndex));
        }
        return classs.join(" ");
      },
      /**
       * 
       */
      updateSelectNum(){
        this.tableSelectedNum = this.tableData.reduce((prev, item) => { 
          return item.checkboxFlag ? prev + 1 : prev;
        }, 0)
      },
      //=======================================
      //======= end========
      //=======================================

      //
      updateAllSelect(){
        let select = true;
        for(let i=0,l=this.tableData.length; i<l; i++){
          if(!this.tableData[i].checkboxFlag){
            select=false;
            break;
          }
        }
        this.allSelectVal=select;
      },
      /**
       *  uiKeyCode 
       **/
      getUiKeyCodeByRow(curRow){
        let key ="";
        if(typeof this.keycode=="string"){
          key = curRow[this.keycode];
        }else {
          this.keycode.forEach((res)=>{
            key+=curRow[res]
          });
        }
        return key;
      },
      /**
       *  curRow tableData
       *  -1
       * @param curRow: rowindex()
       */
      getCurIndex(curRow=""){
        let curIndex = -1;
        if(typeof curRow=="object"){
          let key = this.getUiKeyCodeByRow(curRow);
          for(let i=0,l=this.tableData.length;i<l;i++){
            if(this.tableData[i].uiKeyCode==key){
              curIndex=i;
              break;
            }
          }
        }else {
          curIndex = trim(""+curRow);//
          //
          if(!curRow){
            curIndex = this.tableData.length-1;
          }else{
            curIndex=curRow;
          }
          curIndex = parseInt(curIndex);
          if(isNaN(curIndex)) return -1;
          if(curIndex>=this.tableData.length) curIndex = this.tableData.length-1;
          if(curIndex<0) curIndex = 0;
        }
        return curIndex;
      },
      /**
       * 
       * @param newRow:  Array() / Object()
       * @param curRow: rowindex
       * @param isCheckFlag:  true:false
       *
       */
      insertBase(newRow,curRow,isCheckFlag=false,isAfter=true){
        let curIndex = this.getCurIndex(curRow);
        if(curIndex==-1) return;

        //
        let arr = [];
        let isString = typeof this.keycode=="string";
        //
        if(Object.prototype.toString.call(newRow) === "[object Array]"){
          newRow.forEach((item,index)=>{
            //key  checkboxFlag
            let key = "";
            if(isString){
              key = item[this.keycode];
            }else {
              this.keycode.forEach((res)=>{
                key+=item[res]
              });
            }
            arr.push(Object.assign({},item,{_uiFilterFlag:true,checkboxFlag:!!isCheckFlag,uiKeyCode:key||index}));
          });
        }else {
          let key = "";
          if(isString){
            key = newRow[this.keycode];
          }else {
            this.keycode.forEach((res)=>{
              key+=newRow[res]
            });
          }
          arr=[Object.assign({},newRow,{_uiFilterFlag:true,checkboxFlag:!!isCheckFlag,uiKeyCode:key||this.tableData.length})]
        }
        this.tableData.splice(isAfter?curIndex+1:curIndex,0,...arr);
        this.updateAllSelect();
        this.updateSelectNum();
      }

    },
    created(){
      //resizeBox
      this.resizeBox= debounce(300,(value)=>{
        this.initBoxWidth();
      });
      //
      if(this.$t && (typeof this.$t == "function")){
        Object.assign(this.i18nObj,{
          confirmButtonText:this.$t('zoehisUI.table.confirmButtonText'),
          addButtonText:this.$t('zoehisUI.table.addButtonText'),
          deleteButtonText:this.$t('zoehisUI.table.deleteButtonText'),
          cancelButtonText:this.$t('zoehisUI.table.cancelButtonText'),
          rowName:this.$t('zoehisUI.table.rowName'),
          operate:this.$t('zoehisUI.table.operate'),
          operateName:this.$t('zoehisUI.table.operateName'),
          rowValue:this.$t('zoehisUI.table.rowValue'),
          relateName:this.$t('zoehisUI.table.relateName'),
          dataNone:this.$t('zoehisUI.table.dataNone'),
          excelDataNone:this.$t('zoehisUI.table.excelDataNone'),
        })
      }
      // operateText
      this.i18nObj.operate = this.operateText || this.i18nObj.operate;

    },
    mounted(){
      this.$nextTick(()=>{
        this.initBoxWidth();
      });
      addResizeListener(this.$el,()=>{
        this.resizeBox();
      });
    },
    beforeDestroy(){
      removeResizeListener(this.$el);
    },
  }
</script>



