<template>
  <div class="component_example">
    <h1>DateEditor </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal"></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal&quot;&gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
             v-model
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal2" @save="saveTime" :nowtime="false" :autocheck="false" value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd HH:mm" inputformat="yyyy-MM-dd" initfocus="month" endfocus="hour"
        ></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal2&quot; value-format=&quot;yyyy-MM-dd HH:mm:ss&quot; :nowtime=&quot;false&quot; :autocheck=&quot;false&quot;
            format=&quot;yyyy-MM-dd HH:mm&quot; inputformat=&quot;yyyy-MM-dd&quot; initfocus=&quot;month&quot; endfocus=&quot;hour&quot;
            &gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          yyyy-MM-dd HH:mm:ss  <strong>HH</strong><strong>h12</strong> <br>
          value-formatv-model  <br>
          format: yyyy-MM-dd   <br>
          inputformat:format  format <br>
          initfocus   <br>
          endfocus tab-end save <br>
          nowtimev-model true<br>
          autocheck true<br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal3" mode="range" @save="saveTime" :nowtime="false" value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd HH:mm" inputformat="yyyy-MM-dd"></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal3&quot; mode="range" @save="saveTime" :nowtime="false" value-format=&quot;yyyy-MM-dd HH:mm:ss&quot;
            format=&quot;yyyy-MM-dd HH:mm&quot; inputformat=&quot;yyyy-MM-dd&quot;&gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          mode:
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr><td>v-model='value'</td><td></td><td>2019-1-1 /  / </td><td>-</td><td></td></tr>
        <tr><td>mode</td><td>,v-model</td><td>string</td><td>normal/range</td><td>normal</td></tr>
        <tr><td>value-format</td><td>v-model1.""   2.timestamp:  3.yyyy-MM-dd HH:mm:ss / yyyy-MM-dd</td><td>string</td><td>""/timestamp/yyyy-MM-dd..</td><td>-</td></tr>
        <tr><td>format</td><td> </td><td>String</td><td>yyyy-MM-dd/yyyy-MM-dd HH:mm:ss </td><td>yyyy-MM-dd HH:mm:ss</td></tr>
        <tr><td>inputformat</td><td> format</td><td>String</td><td>yyyy-MM-dd/yyyy-MM-dd HH:mm:ss </td><td>-</td></tr>
        <tr><td>initfocus</td><td>:year()</td><td>String</td><td>year/month/date/hour/minute/seconds</td><td>year</td></tr>
        <tr><td>endfocus</td><td>tab/enter tab-end save</td><td>String</td><td>year/month/date/hour/minute/seconds</td><td>seconds</td></tr>
        <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>nowtime</td><td>v-model true</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>autocheck</td><td> true</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>width</td><td>,auto</td><td>number/string</td><td>-</td><td>180</td></tr>
        <tr><td>focus()</td><td></td><td></td><td>-</td><td>-</td></tr>
        <tr><td>hide()</td><td></td><td></td><td>-</td><td>-</td></tr>
        <tr><td>@focus</td><td></td><td></td><td>-</td><td>-</td></tr>
        <tr><td>@clear</td><td>()</td><td>1.</td><td>-</td><td>-</td></tr>
        <tr><td>@change</td><td></td><td>1.valueFormat2.type, confirm/enter</td><td>-</td><td>-</td></tr>
        <tr><td>@save</td><td>enter/</td><td>1.valueFormat2.type, confirm/enter</td><td>-</td><td>-</td></tr>
        <tr><td>@tab-end</td><td>tabtabtab-end endfocus tab-end</td><td>1.</td><td>-</td><td>-</td></tr>
        <tr><td>@after-prev</td><td> ctrl+tab / ctrl+enter</td><td>1.</td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'DateEditor',
    data() {
      return {
        dateVal:new Date(),
        dateVal2:"2018-10-26 10:10:10",
        dateVal3:[new Date(),new Date()],
      }
    },
    watch:{
      dateVal2(val){
        console.log("dateVal2",val)
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="";
        this.featuresName="";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("/enter");
      },
      //
      saveTime(value,type){
        console.log('====',value,type);
      },
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

