<template>
  <div class="zoehis_page">
    <div class="zoehis_page_content" :class="{'zoehis_page_content_width' : !isSetStrip}">
      <div class="zoehis_page_size" v-if="isSetStrip">
        <span>{{pagesize}}</span>
        <!-- <div class="zoehis_page_size_select" @click="showDropList">
        {{setStrip}}
        <ul class="zoehis_page_list" v-show="isStripShow">
          <li class="zoehis_page_li" v-for="item in stripArr" @click="setStripFunc">{{item}}</li>
        </ul>
      </div> -->
      <span class="dropdown_box">
        <zoehis-select :clearable="false" width="45" v-model="setStrip" :selectdata="stripArrAddId"></zoehis-select>
      </span>
      <span>{{unitText}}</span>
      </div>
      <div class="zoehis_page_total">
        {{count}}<span>{{stripCount}}</span>{{unitText}}
      </div>
      <div class="zoehis_page_current" onselectstart="return false">
        <div class="zoehis_page_current_wrap">
          <span class="endsPage" :class="currentPage!=1?'isPage':''" @click="toHome">{{home}}</span>
          <i class="zoeIconfont z_left_normal zoehis_page_prev"
             :class="{'zoehis_page_prev_false':isPrev}"
             @click="prevPage"></i>
          <!---->
          <input type="text" v-model="setPage" @keyup.enter="enterChangePage" @blur="inputBlur"
                 :class="{'zoehis_page_input_invariant' : !isPrev && !isNext}" :disabled="!isPrev && !isNext"
                 onkeyup="value=value.replace(/[^\d]/g,'') " :style="{width:inputWidth}"
                 onbeforepaste="clipboardData.setData('text',clipboardData.getData('text').replace(/[^\d]/g,''))"
                 ID="zoehis_page_input" NAME="zoehis_page_input" ref="pageInput">
          <span class="zoehis_page_bias">/</span>
          <span class="zoehis_page_count" ref="pageCount">{{pageCount}}</span>
          <i class="zoeIconfont z_right_normal1 zoehis_page_prev"
             :class="{'zoehis_page_prev_false':isNext}"
             @click="nextPage"></i>
          <span class="endsPage" :class="currentPage!=pageCount?'isPage':''" @click="toEnd">{{end}}</span>
        </div>
      </div>
      <div style="clear: both;"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {on} from '../../../src/utils/dom.js';
  export default{
    name: "ZoehisPage",
    componentName: "ZoehisPage",

    data: function () {
      return {
        currentPage: this.current_page,  // --
        setStrip: this.set_strip,  //
        setPage: this.current_page,  //  --

        isStripShow: false,  //
      }
    },

    props: {
      stripArr: {  //
        type: Array,
        default: function () {
          return [10,20,30,40]
        }
      },
      stripCount: Number,  //
      current_page: {  //----datacurrentPage
        type: Number,
        default: 1
      },
      set_strip: {  //----datasetStrip
        type: Number,
        default: 10
      },
      isSetStrip: {
        type: Boolean,
        default: true
      },
      beforeClick:"",//
      unitText: {//
        type:String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.page.strip');
          }else {
            return "";
          }
        }
      },
    },

    computed: {
      inputWidth(){
        let l = (""+this.setPage).length;
        if(l>2){
          return 9*l + "px";
        }else {
          return "18px";
        }
      },
      stripArrAddId(){//id
        let arrs = [];
        this.stripArr.map((item,index)=>{
          arrs.push({
            text:item,
            id:item
          })
        })
        return arrs
      },
//      
      pageCount: function () {
        var pageCount = this.stripCount/this.setStrip;
//          +1
          if(String(pageCount).indexOf(".")>-1){
            pageCount = parseInt(pageCount,10);
            pageCount++;
          }else if(pageCount==0){
            pageCount = 1;
          }
        return pageCount;
      },

      //
      isPrev: function () {
        if(this.currentPage == 1) return false
        return true
      },
      //
      isNext: function () {
        if(this.currentPage == this.pageCount) return false
        return true
      }
    },

    watch: {
      current_page(val){
        if(this.currentPage != val){
          this.currentPage = val;
          this.setPage = val;
        }
      },
      // set_strip
      set_strip(val){
        if(this.setStrip != val){
          this.setStrip = val;
        }
      },
      currentPage: function (currentPage) {
        let submitPage = parseInt(currentPage);
        this.$emit("update:current_page",submitPage);
        this.$emit('changePage',submitPage);
      },
      setStrip: function (setStrip) {
        this.$emit("update:set_strip",setStrip);
        this.$emit('changeStrip',setStrip);
      },
//      
      pageCount: function () {
        if(this.pageCount<this.currentPage || this.pageCount<this.setPage){
          this.currentPage = this.pageCount;
          this.setPage = this.pageCount;
        }
      }
    },

    mounted: function(){
//      let pageCountW = this.$refs.pageCount.offsetWidth;
//      this.$refs.pageInput.style.width = pageCountW+'px';
    },

    created: function () {
      var _this = this;
//    document
      on(document,'click',function () {
        _this.isStripShow = false;
      });
      //i18n
      if(this.$t && (typeof this.$t == "function")){
        this.pagesize = this.$t('zoehisUI.page.pagesize');  //
        this.count = this.$t('zoehisUI.page.count');  //
        this.home = this.$t('zoehisUI.page.home');  //
        this.end = this.$t('zoehisUI.page.end');  //
      }else {
        this.pagesize = "";
        this.count = "";
        this.home = "";
        this.end = "";
      }

    },

    methods: {
      setStripFunc: function (e) {
        var setStrip = e.currentTarget.innerHTML;
        setStrip = parseInt(setStrip,10);
        this.setStrip = setStrip;
      },

      showDropList: function (e) {
        this.isStripShow = !this.isStripShow;
        e.stopPropagation();
      },

      prevPage: function () {//
        if(!this.isPrev) return false
        this.before(this.currentPage-1,this.currentPage);
      },

      nextPage: function () {//
        if(!this.isNext) return false
        this.before(this.currentPage+1,this.currentPage);
      },

      toHome(){//
        if(this.currentPage==1) return false
        this.before(1,this.currentPage);
      },

      toEnd(){//
        if(this.currentPage==this.pageCount) return false
        this.before(this.pageCount,this.currentPage);
      },

      before(newPage,oldPage,type=0){//inputBlurtype=1 
        if(this.beforeClick&&typeof this.beforeClick=="function"){
          let isTrue =  this.beforeClick(newPage,oldPage);
          if(isTrue){
            this.goOn(newPage);
          }else{
            type == 1 && (this.setPage = oldPage);//
          }
        }else{
          this.goOn(newPage);
        }
      },

      goOn(newPage){//  newPage
        this.currentPage = newPage;
        this.setPage = newPage;
      },
      // 
      inputBlur() {
        var _this = this;
        let toPage = 1;
        if(!isNaN(_this.setPage)){//
          switch(true){
            case _this.setPage>_this.pageCount://
              toPage = _this.pageCount;
              break;
            case _this.setPage<1://11
              toPage = 1;
              break;
            default:
              toPage = parseInt(_this.setPage,10);
              break;
          }
        }
        this.before(toPage,this.currentPage,1);
      },
      // 
      enterChangePage(e){
        e.currentTarget.blur();
      },
    }
  }
</script>



