<template>
  <div class="zoehis_vertical_menu">
    <div class="c-new" :class="{com_fix:menuFixFlag}">
      <!--<zoehis-scrollbar ref="scrollbar" wrap-class="menu_scrollbar_wrap" view-class="menu_scrollbar_view" class="menu_scrollbar_container">-->
        <div class="c-new-menu-items">
          <ul v-if="data" class="item_con_ul">
            <li v-for="( item, index ) in data"
                :configId="item.configId"
                :key="item.configId"
                @mouseover="changeHoverId(item,index)"
                @mouseleave="changeHoverId()"
                :class="{ selected: item.configId == currFirstId, active:item.configId==firstSelectId, hovered:item.configId==hoverId}"
                class="c-menu-new clearfix">
              <div class="c-new-menu-all" :title="item.menuName" @click="handleFirstMenuClick(item.configId)">
                <span class="c-menu-new-num"></span>
                <i class="c-menu-new-icon zoeIconfont" :class="item.iconUrl||''"></i>
                <span class="c-menu-new-name">{{ item.menuName }}</span>
                <i class="com_first_menu_toggle zoeIconfont" :class="item.configId==currFirstId?'z_dropU_form':'z_dropD_form'"></i>
              </div>
              <ul v-show="item.configId == currFirstId" class="com_ordinary_second_box" v-if="menuFixFlag && item.items.length">
                <li v-for="( item2, index2 ) in item.items"
                    :key="item2.configId"
                    :configId="item2.configId"
                    class="com_ordinary_second_item"
                    :title="`${item2.menuName}`"
                    @click="handleSecondMenuClick(item2.configId)"
                    :class="{ selected: item2.configId ==  currSecondId }"
                >
                  <span>{{item2.menuName}}</span>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      <!--</zoehis-scrollbar>-->
    </div>
    <div class="com_ordinary_toggle" @click="toggleclick" :title="menuFixFlag?'':''" v-show="noShrink" :class="menuFixFlag?'comFix':'comHam'">
      <i class="zoeIconfont" :class="menuFixFlag?'com_fix z_more_normal':'z_hamburgerL_normal'"></i>
    </div>

    <div v-if="!menuFixFlag"
         class="c-ff-left-second-menu" :class="{ slide: map2[hoverId] }"
         :style="{top:secondMenuDom.top+'px',height:secondMenuDom.height+'px'}">
      <div class="c-second-menu-items"
           v-for="( item, index ) in data"
           :key="item.configId"
           @mouseover="changeHoverId(item,index)"
           @mouseleave="changeHoverId()"
           v-show="item.configId == hoverId">
        <ul class="c-second-menu-ul">
          <li v-for="( item2, index2 ) in item.items"
              :key="item2.configId"
              :configId="item2.configId"
              class="c-second-menu-item"
              :title="`${item2.menuName}`"
              @click="handleSecondMenuClick(item2.configId)"
              :class="{ selected: item2.configId ==  currSecondId }"
          >
            <span>{{item2.menuName}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Emitter from '../../../src/mixins/emitter';
  import normalizeWheel from "normalize-wheel";
  import { isNull,trim } from '../../../src/utils/util';
  import dateUtil from '../../../src/utils/date';
  import debounce from '../../../src/utils/throttle-debounce.js';
  import {addResizeListener,removeResizeListener} from '../../../src/utils/resize-event';//resize()

  export default {
    name: 'ZoehisVerticalMenu',
    componentName: 'ZoehisVerticalMenu',
    mixins: [Emitter],
    data () {
      return {
        hoverId:"",
        hoverIndex:-1,//hover
        menuFixFlag:true,// falsetrue
//        data: null,
        currFirstId: null,//
        firstSelectId:"",//configId
        currSecondId: null,//
        map:{},//
        map2:{},//
        windowH:600,//
      }
    },
    props:{
      data:{//
        type:Array,
        default(){
          return  [];
        }
      },
      noShrink: {    //
        type: Boolean,
        default: true
      },
    },
    computed:{
      //
      secondMenuDom(){
        if(this.menuFixFlag || !this.data.length || this.hoverIndex<0 || !this.data[this.hoverIndex]){
          return {top:0,height:0};
        }
        let top = 47*this.hoverIndex;
        let contentH = 40*this.data[this.hoverIndex].items.length;
        let windowH = this.windowH;


        //
        if(windowH>=top+contentH){
          return {top:top,height:contentH};
        }else if(windowH>=contentH){//
          return {top:windowH-contentH,height:contentH};
        }else {
          return {top:0,height:windowH};
        }

      }
    },
    watch:{
      data:{
        immediate:true,
        handler(val){
          let map = {},map2 = {};
          for (let i = 0,len = val.length;i < len;i ++) {
            let pItem = val[i];
            let item = pItem.items;
            map2[pItem.configId] = pItem;
            for (let j = 0, len2 = item.length;j < len2;j ++){
              item[j].parentId=pItem.configId;
              map[item[j].configId] = {
                pid: val[i].configId,
                data: item[j]
              };
            }
          }
          this.map = map;//key: id, value: { pid: pid, data: data }
          this.map2 = map2;//key: pid, value: { Boolean }
        }
      }
    },
    filters:{
    },
    methods:{
      //hoverId
      changeHoverId(d={},i=-1){
        this.hoverId=d.configId;
        this.hoverIndex=i;
      },
      //
      handleFirstMenuClick(configId){
        if(this.currFirstId==configId){//
          this.currFirstId = "";
        }else{
          this.currFirstId = configId;
        }
      },
      //
      handleSecondMenuClick(configId){
        let data = Object.assign({},this.map[configId].data);
        this.currSecondId = data.configId;
//        this.currFirstId = data.configId.substring(0,4);
        this.currFirstId = data.parentId;
        this.$emit('menu-click',data,configId);  //
      },
      //
      getFixflag(){
        return this.menuFixFlag;
      },
      //
      setFixflag(flag){
        this.menuFixFlag=flag;
      },
      //
      toggleclick(){
        this.menuFixFlag=!this.menuFixFlag;
        this.$emit('fix-change',this.menuFixFlag);
      },
    },
    created(){
    },
    mounted(){
      this.windowH=$(window).height();//
    },
  }
</script>



