<template>
  <li
    class="zoehis_select_dropdown__item"
    :class="{
      'zoehis_selected':itemSelected,
      'zoehis_disabled':disabled,
      'zoehis_hover':parent.hoverIndex === index,
      'zoehis_has_icon':item.showDetailBtn
    }"
    v-show="visible"
    @mouseenter="hoverItem" @mouseleave="mouseleaveEvn"
    @click.stop="selectOptionClick"
  >
    <!--@mouseenter="hoverItem"-->
    <!--@click.stop="selectOptionClick"-->
    <!--class="el-select-dropdown__item"-->
    <!--v-show="visible"-->
    <!--:class="{-->
    <!--'selected': itemSelected,-->
    <!--'is-disabled': disabled || groupDisabled || limitReached,-->
    <!--'hover': parent.hoverIndex === index-->
    <!--}">-->
    <span v-if="item.showDetailBtn" class="icon zoeIconfont z_arrowTTR_normal" @click.stop="showDetailClick"></span>
    <i v-if="parent.multiple" class="zoeIconfont zoehis_multiple_check" :class="itemSelected?'z_checkboxS_form':'z_checkbox_form'"></i>
    <slot>
      <span>{{currentLabel}}</span>
    </slot>
  </li>
</template>

<script type="text/babel">
  import Emitter from '../../../src/mixins/emitter';
  import { getValueByPath } from '../../../src/utils/util';

  export default {
    mixins: [Emitter],

    name: 'ZoehisOption',

    componentName: 'ZoehisOption',

    props: {
      //
      multiple:{
        type:Boolean,
        default:false
      },
      //
      value: {
        required: true
      },
      label: [String, Number],
      created: Boolean,
      disabled: {
        type: Boolean,
        default: false
      },
      noselect:{//false
        type:Boolean,
        default:false
      },
      itemindex:Number, //
      item:null, //
      showDetailItemName: {//
        type: String,
        default:''
      },
      showDetailFlag:{//
        type: Boolean,
        default: false
      },
      selectdata:{//
        type:Array,
        default:function(){
          return [];
        }
      },
    },

    data() {
      return {
        index: -1,
//        groupDisabled: false,
        visible: true,
//        hitState: false
      };
    },

    computed: {

      //
      //.call this.valueObjecttoString
      isObject() {
        return Object.prototype.toString.call(this.value).toLowerCase() === '[object object]';
      },
      //label
      currentLabel() {
        return this.label || (this.isObject ? '' : this.value);
      },
      //
      currentValue() {
        return this.value || this.label || '';
      },
      //Select
      parent() {
        let result = this.$parent;
        while (!result.isSelect) {
          result = result.$parent;
        }
        return result;
      },

      //
      itemSelected() {
        //
        if (!this.parent.multiple) {
          return this.isEqual(this.value, this.parent.value);
        } else {
          return this.contains(this.parent.selectcode, this.value);
        }
      },
      //itemindexindex
//      index(){
//        return this.itemindex;
//      }
//      limitReached() {
//        if (this.parent.multiple) {
//          return !this.itemSelected &&
//            this.parent.value.length >= this.parent.multipleLimit &&
//            this.parent.multipleLimit > 0;
//        } else {
//          return false;
//        }
//      }
    },

    watch: {
      //currentLabel todo created/remote
      currentLabel() {
//        if (!this.created ) this.dispatch('ZoehisSelect', 'setSelected');
      },
      //value
      value() {
//        if (!this.created || !this.parent.remote) this.dispatch('ZoehisSelect', 'setSelected');
      },
      //itemindexindex
      itemindex:{
        handler(val){
          this.index=val;
        },
        immediate: true
      },
      selectdata:{//createdselect
        deep:true,
        immediate: true,
        handler(val){
          if(this.showDetailItemName && this.item[this.showDetailItemName]) {
            this.$set(this.item, 'showDetailBtn', true);
          }
        }
      }
    },

    methods: {
      isEqual(a, b) {
        if (!this.isObject) {
          return a === b;
        } else {
          const valueKey = this.parent.valueKey;
          return getValueByPath(a, valueKey) === getValueByPath(b, valueKey);
        }
      },
      contains(arr = [], target) {
        return arr.indexOf(target) > -1;
      },
//
//      handleGroupDisabled(val) {
//        this.groupDisabled = val;
//      },
      //hoverIndex
      hoverItem() {
          // 
        this.$emit('showDetail',false,this.item);
        if (!this.disabled) {
          this.parent.uiSelectDetail = this.item;
        }
      },
      mouseleaveEvn(){
        this.parent.uiSelectDetail = null;
      },
      //
      selectOptionClick(e) {
//        e.preventDefault();
        if(this.noselect) return;  //
        if (this.disabled !== true) {
          if(!this.multiple){
            this.dispatch('ZoehisSelect', 'handleOptionClick', this); //Select
          }else {
            this.dispatch('ZoehisMultipleSelectDropdown', 'handleOptionClick', this); //multiple-select-dropdown
          }
        }
      },

      queryChange(query,isVisible) {

        if(isVisible){ //filterRowData
          if (typeof this.parent.filterRowData === 'function') {  // filterRowData
            this.visible = this.parent.filterRowData(query,this.item) || this.created;
          }
        }else { //inputkeyup
          // query 
          let parsedQuery = String(query).replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1');
          let reg = new RegExp(parsedQuery, 'i');
          this.visible = reg.test(this.optionQueryStr) || this.created;

          //  filterRowData
          if (this.visible && typeof this.parent.filterRowData === 'function') {  // filterRowData
            this.visible = this.parent.filterRowData(query,this.item) || this.created;
          }
        }
        if (!this.visible) {
          this.parent.filteredOptionsCount--;
        }
      },
      setOptionQueryStr(){
        this.optionQueryStr = "";//  (1) 
        for(let i=0,l=this.parent.filterField.length; i<l; i++){
          let queryField = this.item[this.parent.filterField[i]];
          if(queryField!=undefined && queryField!==""){
            this.optionQueryStr += "~" + queryField;
          }
        }
      },
      //
      showDetailClick(){
        let showDetailFlag = !this.showDetailFlag;
        this.$emit('showDetail',showDetailFlag,showDetailFlag? this.item:'');
        this.$emit('focus',this.itemindex); //input
      },
    },
    created() {
      this.parent.options.splice(this.itemindex,0,this);//optionoptionspush
      this.parent.cachedOptions.splice(this.itemindex,0,this);//optionoptionspush
      this.parent.optionsCount++;
      this.parent.filteredOptionsCount++;
      this.setOptionQueryStr();//
      this.$on('queryChange', this.queryChange); //

//      this.$on('handleGroupDisabled', this.handleGroupDisabled);
    },
    beforeDestroy() {
      if(!this.multiple){
        this.dispatch('ZoehisSelect', 'onOptionDestroy', this);
      }else {
        this.dispatch('ZoehisMultipleSelectDropdown', 'onOptionDestroy', this);
      }
    }
  };
</script>



