        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot;
              :do-filter-func=&quot;doFilterFunc&quot; :render-attr=&quot;[&#x27;quantity&#x27;,&#x27;drugName&#x27;]&quot; :render-html=&quot;renderHtml&quot;
              :sort-attr=&quot;[&#x27;quantity&#x27;]&quot; :do-sort-func=&quot;doSortFunc&quot;
            &gt;&lt;/zoehis-table&gt;
            &lt;div&gt;
              &lt;zoehis-button @clickenter=&quot;inverseSelectionEn&quot;&gt;&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;toggleAllRowSelection&quot;&gt;/&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;setCurrentRow&quot;&gt;&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;doFilterData&quot;&gt;&lt;/zoehis-button&gt;
            &lt;/div&gt;
            &lt;script&gt;
            //
            doFilterData(){
              this.$refs.zoeTable.doFilterData((d)=&gt;{
                //d: {Object}
                //table.length,return true
                return d.packageSpec &gt; 5;
              });
            },
            renderHtml(val,item,config){
              if(config.dataItemCode==&#x27;quantity&#x27;){
                return &quot;&lt;span style=&#x27;color: red;&#x27;&gt;render-htmlquantity&lt;/span&gt;&quot;+val;
              }else {
                return &quot;&lt;span style=&#x27;color: red;&#x27;&gt;render-htmldrugName&lt;/span&gt;&quot;+val;
              }
            },
            //
            doSortFunc(arr,config,flag){
              //  :sort-attr
              if(config.dataItemCode==&quot;quantity&quot;){
                if(flag==&quot;up&quot;){
                  console.log(&quot;,code&quot;,config.dataItemCode,&quot;&quot;,flag);
                  return arr.sort((a,b)=&gt;{
                    return (&quot;&quot;+a[config.dataItemCode]).localeCompare(b[config.dataItemCode]);
                  });
                }else {
                  console.log(&quot;,code&quot;,config.dataItemCode,&quot;&quot;,flag);
                  return arr.sort((a,b)=&gt;{
                    return (&quot;&quot;+b[config.dataItemCode]).localeCompare(a[config.dataItemCode]);
                  });
                }
              }else {//

              }
            },
            doFilterFunc(val,item,config){
              return val + &quot;&quot;;
            },
            //
            inverseSelectionEn(){
              this.$refs.zoeTable.inverseSelection();
            },
            ///
            toggleAllRowSelection(){
              this.allSelectFlag=!this.allSelectFlag;
              this.$refs.zoeTable.toggleAllRowSelection(this.allSelectFlag);
            },
            //
            setCurrentRow(){
              this.$refs.zoeTable.setCurrentRow();
            },
            &lt;/script&gt;
          </code></pre>
