    <div class="item">
      <h4>
        
        <strong></strong>
      </h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData3"
          :configs="demoConfig2"
          ref="pageTable"
          @scrollafter="editScrollEn"
        >
          <template slot="td_drugName" slot-scope="{item,index}">
            <zoehis-input v-model="item.drugName" width="100%"></zoehis-input>
          </template>
        </zoehis-table>
        <div style="padding: 10px 5px;">
          <zoehis-button @clickenter="handelEditReset">data</zoehis-button>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>
                
                <strong>insertAfter</strong>
              </li>
              <li></li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
        &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData3&quot; :configs=&quot;demoConfig2&quot;
          ref=&quot;pageTable&quot; @scrollafter=&quot;editScrollEn&quot;&gt;
          &lt;template slot=&quot;td_drugName&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-input v-model=&quot;item.drugName&quot; width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-input&gt;
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;div style=&quot;padding: 10px 5px;&quot;&gt;
          &lt;zoehis-button @clickenter&quot;handelEditReset&quot;&gt;data&lt;/zoehis-button&gt;
        &lt;/div&gt;
        &lt;script&gt;
        //
        editScrollEn(e){
          let {clientHeight,scrollHeight,scrollTop} = e;
          if(this.oldScrollTop===scrollTop) return;//top
          this.oldScrollTop=scrollTop;
          //
          if(scrollHeight-clientHeight-scrollTop<10){
            this.getDataByPage();
          }
        },
        //
        getDataByPage(){
          if(this.editTableDoingFlag) return;//
          this.editTableDoingFlag=true;
          let loading = this.$loading({text:&quot;&quot;});
          //ajax
          new Promise((resolve,reject)=&gt;{
            let l = this.$refs.pageTable.getData().length;//
            setTimeout(()=&gt;{
              let arr = [];
              for(let i=l;i&lt;l+5;i++){
                let item = {
                  drugCode:&quot;1_&quot;+i,drugName:&quot;_&quot;+i,packageSpec:1+i,
                  packageUnit:&quot;&quot;,packageUnitCode:i,
                  drugSpec:&quot;1ml:50IU/10/1&quot;,modifiedTime:new Date().getTime()+i*1000*60*60*24
                }
                arr.push(item);
              }
              resolve(arr);
            },500);
          }).then(res=&gt;{
            this.$refs.pageTable.insertAfter(res,&quot;&quot;);//
            this.$nextTick(_=&gt;{
              loading &amp;&amp; loading.close();
              this.editTableDoingFlag=false;
            });
          }).catch(e=&gt;{
            this.$nextTick(_=&gt;{
              loading &amp;&amp; loading.close();
              this.editTableDoingFlag=false;
            });
          });
        },
        //
        handelEditReset(){
          let curTime = new Date().getTime();
          let arr = [];
          for(let i=0;i&lt;10;i++){
            let item = {
              drugCode:&quot;1_&quot;+i,drugName:&quot;_&quot;+i,packageSpec:1+i,
              packageUnit:&quot;&quot;,packageUnitCode:i,
              drugSpec:&quot;1ml:50IU/10/1&quot;,modifiedTime:curTime+i*1000*60*60*24
            }
            arr.push(item);
          }
          this.$refs.pageTable.comTableMove({top:0});//
          this.demoData3=arr;//
        },
        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
