        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>
                
                <strong>  @change keycode index</strong>
              </li>
              <li>
                <strong></strong>
              </li>
              <li>
                1.
                <strong></strong> insertBefore(data,curRow,isCheckFlag)1.data
                <strong>Array()</strong>/
                <strong>Object()</strong>
                2.curRowrowindex3().
              </li>
              <li>
                2.
                <strong></strong> insertAfter(data,curRow,isCheckFlag)insertBefore
              </li>
              <li>
                <strong></strong>
              </li>
              <li>
                1.deleteRow(data)1.data,
                <strong>Array()</strong> /
                <strong>Object()</strong> /
                <strong>Number/String()</strong>
              </li>
              <li>
                <strong></strong>
              </li>
              <li>
                1.editRow(row,newRow)1.row,
                <strong>Object(keycode)</strong> /
                <strong>Number/String()</strong>2.newRow:
              </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
        &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData2&quot; :configs=&quot;demoConfig2&quot;
        @change=&quot;handleChange&quot; ref=&quot;editTable1&quot;&gt;
          &lt;template slot=&quot;td_drugName&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-input v-model=&quot;item.drugName&quot; width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-input&gt;
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;script&gt;
        /**
        * 
        * @param type: 12:
        */
        insertRow(type=1){
          let curTime = new Date().getTime();
          let newRow;
          //1.
          if(type==1){
            //
            newRow = {
            drugCode:curTime,drugName:""+curTime,packageSpec:curTime,
            packageUnit:"",packageUnitCode:curTime,
            drugSpec:"1ml:10/1",modifiedTime:curTime
            }
          }else{
            newRow = [];
            for(let i=0; i<3; i++){
              newRow.push({
                drugCode:curTime+i,drugName:""+curTime,packageSpec:curTime,
                packageUnit:"",packageUnitCode:curTime+i,
                drugSpec:"1ml:10/1",modifiedTime:curTime
              });
            }
          }
          //2.
          let curRow="";
          // keycode(keycode drugCodedrugName)
          curRow = {drugCode:"1_1",drugName:"_1",packageUnit:""};//table:data
          //
//        curRow=2;//index2

          //3.newRow()
          let isCheckFlag = false;
          this.$refs.editTable1.insertBefore(newRow,curRow,isCheckFlag);
        },
        /**
        * 
        * @param type: 12:
        */
        deleteRow(type){
          //1. keycode
          let curRow="";
          if(type==1){
            // Object
            curRow = {drugCode:"1_1",drugName:"_1",packageUnit:""};//table:data
            //Number 
//          curRow = 0;//
          }else{
            curRow = [
              {drugCode:"1_0",drugName:"_0",packageUnit:""},
              {drugCode:"1_1",drugName:"_1",packageUnit:""},
            ]
          }
          this.$refs.editTable1.deleteRow(curRow);
        },
        //
        editRow(){
          let curRow = {drugCode:"1_2",drugName:"_2",packageUnit:""};
          this.$refs.editTable1.editRow(curRow,{drugSpec:""+new Date().getTime()});
        },

        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
