              ];
              //
              this.demoConfig2=[
                {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"drugName",dataItemName:"",filterType:"",itemWidth:"260px",fixedFlag:"left"},
                {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"packageSpec",dataItemName:"",filterType:"",itemWidth:"100px",fixedFlag:""},
