                {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"drugSpec",dataItemName:"",filterType:"",itemWidth:"200px",fixedFlag:""},
                {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"modifiedTime",dataItemName:"",filterType:"yyyy-MM-dd hh:mm:ss",itemWidth:"300px",fixedFlag:""},
              ];

              //
              let curTime = new Date().getTime();
              for(let i=0;i<10;i++){
                let item = {
                  drugCode:"1_"+i,//
                  drugName:"_"+i,//
                  packageSpec:1+i,//
                  packageUnit:"",//
                  drugSpec:"1ml:50IU/10/1",//
                  modifiedTime:curTime+i*1000*60*60*24 //
                }
                //30
                this.demoData1.push(item);
                this.demoData3.push(item);
              }
              for(let i=0;i<3;i++){
                let item = {
                  drugCode:"1_"+i,drugName:"_"+i,packageSpec:1+i,
                  packageUnit:"",packageUnitCode:i,
                  drugSpec:"1ml:50IU/10/1",modifiedTime:curTime+i*1000*60*60*24
