      console.log("",val,"");
    },
    /**
     * excel
     */
    handleToExcel() {
      //
      let tem = [];
      for (let i = 0; i < 4500; i++) {
        tem.push({
          drugCode: "1_" + i,
          drugName: "_" + i,
          packageSpec: 1 + i,
          packageUnit: "",
          packageUnitCode: i,
          drugSpec: "1ml:50IU/10/1",
          modifiedTime: i * 1000 * 60 * 60 * 24
        });
      }
      this.$refs.zoeTableHide.tableToExcel(tem,"","tab");
    },
    //
    doFilterData() {
      this.$refs.zoeTable.doFilterData(d => {
        //d: {Object}
        //table.length,return true
        return d.packageSpec > 5;
      });
    },
    //
    filterOpenEn() {
      this.$refs.filterRef.openFilter();
