    },
    ///
    toggleAllRowSelection() {
      this.allSelectFlag = !this.allSelectFlag;
      this.$refs.zoeTable.toggleAllRowSelection(this.allSelectFlag);
    },
    //
    setCurrentRow() {
      this.$refs.zoeTable.setCurrentRow();
    },

    //
    operateClickEn(event, item) {
      this.$messageBox.success(
        item.drugName + "data-id:" + event.target.dataset.id
      );
    },
    //
    operateHtml(item) {
      return "<i data-id='1' class='zoeIconfont z_tableR_normal'></i><i data-id='2' class='zoeIconfont z_print_normal'></i>";
    },
    //
    rowClickEn(item) {
      console.log("", item);
    },
    //
    rowDblClickEn(item) {
      console.log("", item);
    },
    //
    colClickEn(data) {
      console.log("", data);
    },
    //
    colDblClickEn(data) {
      console.log("", data);
    },
    //
    scrollafterEn(e) {
      let { clientHeight, scrollHeight, scrollTop } = e;
      //
      if (scrollHeight - clientHeight - scrollTop < 10) {
        console.log("");
      }
    },
    renderHtml(val, item, config) {
      if (config.dataItemCode == "quantity") {
        return (
          "<span style='color: red;'>render-htmlquantity</span>" + val
        );
      } else {
        return (
          "<span style='color: red;'>render-htmldrugName</span>" + val
        );
      }
    },
    //
    doSortFunc(arr, config, flag) {
      //  :sort-attr
      if (config.dataItemCode == "quantity") {
        if (flag == "up") {
          console.log(
            ",code",
            config.dataItemCode,
            "",
            flag
          );
          return arr.sort((a, b) => {
            return ("" + a[config.dataItemCode]).localeCompare(
              b[config.dataItemCode]
            );
          });
        } else {
          console.log(
            ",code",
            config.dataItemCode,
            "",
            flag
          );
          return arr.sort((a, b) => {
            return ("" + b[config.dataItemCode]).localeCompare(
              a[config.dataItemCode]
            );
          });
        }
      } else {
        //
      }
    },
    doFilterFunc(val, item, config) {
      return val + "";
    },
    /**
     * 
     */
    handleChange(data) {
      if (data[this.curIndex]) {
        data[this.curIndex].uiRowChangeFlag = true;
      }
    },
    /**
     * 
     * @param type: 12:
     */
    insertRow(type = 1) {
      let curTime = new Date().getTime();
      let newRow;
      //1.
      if (type == 1) {
        //
        newRow = {
          drugCode: curTime,
          drugName: "" + curTime,
          packageSpec: curTime,
          packageUnit: "",
          packageUnitCode: curTime,
          drugSpec: "1ml:10/1",
          modifiedTime: curTime
        };
      } else {
        newRow = [];
        for (let i = 0; i < 3; i++) {
          newRow.push({
            drugCode: curTime + i,
            drugName: "" + curTime,
            packageSpec: curTime,
            packageUnit: "",
            packageUnitCode: curTime + i,
            drugSpec: "1ml:10/1",
            modifiedTime: curTime
          });
        }
      }

      //2.
      let curRow = "";
      // keycode(keycode drugCodedrugName)
      curRow = {
        drugCode: "1_1",
        drugName: "_1",
        packageUnit: ""
      }; //table:data
      //
      //        curRow=2;//index2

      //3.newRow()
      let isCheckFlag = false;
      this.$refs.editTable1.insertBefore(newRow, curRow, isCheckFlag);
    },
    /**
     * 
     * @param type: 12:
     */
    deleteRow(type) {
      //1. keycode
      let curRow = "";
      if (type == 1) {
        // Object
        curRow = {
          drugCode: "1_1",
          drugName: "_1",
          packageUnit: ""
        }; //table:data
        //Number 
        //          curRow = 0;//
      } else {
        curRow = [
          {
            drugCode: "1_0",
            drugName: "_0",
            packageUnit: ""
          },
          {
            drugCode: "1_1",
            drugName: "_1",
            packageUnit: ""
          }
        ];
      }
      this.$refs.editTable1.deleteRow(curRow);
    },
    //
    editRow() {
      let curRow = {
        drugCode: "1_2",
        drugName: "_2",
        packageUnit: ""
      };
      this.$refs.editTable1.editRow(curRow, {
        drugSpec: "" + new Date().getTime()
      });
    },
    //
    editScrollEn(e) {
      let { clientHeight, scrollHeight, scrollTop } = e;
      if (this.oldScrollTop === scrollTop) return; //top
      this.oldScrollTop = scrollTop;
      //
      if (scrollHeight - clientHeight - scrollTop < 10) {
        this.getDataByPage();
      }
    },
    getDataByPage() {
      if (this.editTableDoingFlag) return; //
      this.editTableDoingFlag = true;
      let loading = this.$loading({ text: "" });
      //ajax
      new Promise((resolve, reject) => {
        let l = this.$refs.pageTable.getData().length; //
        setTimeout(() => {
          let arr = [];
          for (let i = l; i < l + 5; i++) {
            let item = {
              drugCode: "1_" + i,
              drugName: "_" + i,
              packageSpec: 1 + i,
              packageUnit: "",
              packageUnitCode: i,
              drugSpec: "1ml:50IU/10/1",
              modifiedTime: new Date().getTime() + i * 1000 * 60 * 60 * 24
            };
            arr.push(item);
          }
          resolve(arr);
        }, 500);
      })
        .then(res => {
          this.$refs.pageTable.insertAfter(res, ""); //
          this.$nextTick(_ => {
            loading && loading.close();
            this.editTableDoingFlag = false;
          });
        })
        .catch(e => {
          this.$nextTick(_ => {
            loading && loading.close();
            this.editTableDoingFlag = false;
          });
        });
    },
    //
    handelEditReset() {
      let curTime = new Date().getTime();
      let arr = [];
      for (let i = 0; i < 10; i++) {
        let item = {
          drugCode: "1_" + i,
          drugName: "_" + i,
          packageSpec: 1 + i,
          packageUnit: "",
          packageUnitCode: i,
          drugSpec: "1ml:50IU/10/1",
          modifiedTime: curTime + i * 1000 * 60 * 60 * 24
        };
        arr.push(item);
      }
      this.$refs.pageTable.comTableMove({ top: 0 }); //
      this.demoData3 = arr; //
    },
    handleToggle(e) {
      $(e.target)
        .next()
        .find("pre code")
        .toggle();
    }
  },
  mounted: function() {
    //
    this.demoConfig1 = [
      {
        sortNo: 0,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "left",
        patientItemName: "",
        dataItemCode: "drugName",
        dataItemName: "",
        filterType: "",
        itemWidth: "260px",
        fixedFlag: " "
      },
      {
        sortNo: 1,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "packageSpec",
        dataItemName: "",
        filterType: "",
        itemWidth: "100px",
        fixedFlag: ""
      },
      {
        sortNo: 2,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "packageUnit",
        dataItemName: "",
        filterType: "",
        itemWidth: "100px",
        fixedFlag: ""
      },
      {
        sortNo: 3,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "drugSpec",
        dataItemName: "",
        filterType: "",
        itemWidth: "200px",
        fixedFlag: ""
      },
      {
        sortNo: 4,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "modifiedTime",
        dataItemName: "",
        filterType: "yyyy-MM-dd h:mm:ss",
        itemWidth: "300px",
        fixedFlag: ""
      }
    ];
    this.demoConfig2 = [
      {
        sortNo: 0,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "left",
        patientItemName: "",
        dataItemCode: "drugName",
        dataItemName: "",
        filterType: "",
        itemWidth: "260px",
        fixedFlag: "left"
      },
      {
        sortNo: 1,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "packageSpec",
        dataItemName: "",
        filterType: "",
        itemWidth: "100px",
        fixedFlag: ""
      },
      {
        sortNo: 2,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "packageUnit",
        dataItemName: "",
        filterType: "",
        itemWidth: "100px",
        fixedFlag: ""
      },
      {
        sortNo: 3,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "drugSpec",
        dataItemName: "",
        filterType: "",
        itemWidth: "200px",
        fixedFlag: ""
      },
      {
        sortNo: 4,
        validFlag: "1",
        dataItemSort: "1",
        dataTextAlign: "",
        patientItemName: "",
        dataItemCode: "modifiedTime",
        dataItemName: "",
        filterType: "yyyy-MM-dd HH:mm:ss",
        itemWidth: "300px",
        fixedFlag: ""
      }
    ];
    this.allSelectFlag = false;
    setTimeout(() => {
      let curTime = new Date().getTime();
      for (let i = 0; i < 10; i++) {
        let item = {
          drugCode: "1_" + i,
          drugName: "_" + i,
          packageSpec: 1 + i,
          packageUnit: "",
          packageUnitCode: i,
          drugSpec: "1ml:50IU/10/1",
          modifiedTime: curTime + i * 1000 * 60 * 60 * 24
        };
        this.unitList.push({ id: i, text: "" + i });
        this.demoData1.push(item);
        this.demoData3.push(item);
      }
      for (let i = 0; i < 3; i++) {
        let item = {
          drugCode: "1_" + i,
          drugName: "_" + i,
          packageSpec: 1 + i,
          packageUnit: "",
          packageUnitCode: i,
          drugSpec: "1ml:50IU/10/1",
          modifiedTime: curTime + i * 1000 * 60 * 60 * 24
        };
        this.demoData2.push(item);
      }
    }, 1000);
    $("pre code").each(function(i, block) {
      hljs.highlightBlock(block);
    });
  }
};
</script>
<style scoped>
.component_example .zoehis_table {
  height: 300px;
}
ul {
  margin: 0 0 0 20px;
  padding: 0;
}
.component_example .clearflex {
  display: block;
}
.component_example .item-toggle {
  cursor: pointer;
  background: #8fcae5;
  text-align: center;
  font-weight: bold;
}
.component_example .item-desc {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code pre code {
  display: none;
}
</style>



