<template>
  <transition name="zoehis_zoom_in_top" @after-leave="$emit('dodestroy')">
    <div
      class="zoehis_time_panel" tabindex="-1"
      :class="popperClass"
      :style="{width: width + 'px'}"
      v-show="currentVisible"
      @mousedown="mousedownDrop(1)"
      @focusin="mousedownDrop(2)"
      @focusout="focusoutDrop"
    >
      <div class="zoehis_time_panel__title" :class="{'has_seconds' : showSeconds }">
        <div class="zoehis_time_panel__title-inner hour_title"></div>
        <div class="zoehis_time_panel__title-inner minute_title"></div>
        <div class="zoehis_time_panel__title-inner seconds_title"></div>
      </div>
      <div class="zoehis_time_panel__content" :class="{'has_seconds' : showSeconds }">
        <zoehis-time-spinner
          ref="spinner"
          :show-seconds="showSeconds"
          @select-range="setSelectionRange"
          @change="handleChange"
          :hours="hours"
          :minutes="minutes"
          :seconds="seconds"
        ></zoehis-time-spinner>
      </div>
      <div class="zoehis_time_panel__footer">
        <div
          class="zoehis_time_panel__btn"
          @click="handleClickNow"
        ></div>
        <div
          class="zoehis_time_panel__btn"
          @click="handleClickSure"
        ></div>
      </div>
    </div>
  </transition>
</template>

<script type="text/babel">
  import {limitRange} from '../util'
  import ZoehisTimeSpinner from '../basic/timer-spinner'
  export default {
    components: {ZoehisTimeSpinner},
    created(){
      this.hours = this.currentDate.getHours();
      this.minutes = this.currentDate.getMinutes();
      this.seconds = this.currentDate.getSeconds();
    },
    mounted(){
      this.$nextTick(() => {
        this.handleConfirm(true,true);
      });
      this.$emit('mounted');
      this.listenDomAttr();
    },
    methods: {
      // x-placement bug
      listenDomAttr(){
        if(this.listenDomObj || !this.$el){
          return;
        }
        let _this = this;
        this.listenDomObj=new MutationObserver(function(res){
          let placement = res[0].target.getAttribute("x-placement");
          if(!placement) return;//
          _this.$emit("placementchange",placement)
        });
        let  options = {
          attributes:true,//
          attributeFilter:["x-placement"]
        };
        this.listenDomObj.observe(this.$el, options)
      },

      // 
      //focusin 
      mousedownDrop(a){
        this.$emit('pick-focusin',true);
      },
      focusoutDrop(){
        this.$emit('pick-focusin',false);
      },

      handleClear(){
        this.$emit('pick');
      },
      //
      handleClickSure(){
        this.handleConfirm();
        this.$emit('click-sure');
      },
      //select-range
      setSelectionRange(start, end){
        this.$emit('select-range', start, end);
      },
      //
      handleClickNow(){
        let curDate = new Date();
        //
        if(this.currentDate instanceof Date){//
          curDate.setFullYear(this.currentDate.getFullYear());
          curDate.setMonth(this.currentDate.getMonth(),this.currentDate.getDate());
        }

        this.handleNow(curDate,false);
        this.$nextTick(_ =>{
          this.$emit('click-now');// click-now

        });
      },
      //
      handleNow(nowTime,isVisible=true){
        try {
          let isTrue = (nowTime.getHours() == this.currentDate.getHours() && nowTime.getMinutes() == this.currentDate.getMinutes() && nowTime.getSeconds() == this.currentDate.getSeconds())
          if(isTrue) return;
        }catch (e){
          this.currentDate = nowTime || new Date();
        }
        this.currentDate = nowTime || new Date();
        this.hours = this.currentDate.getHours();
        this.minutes = this.currentDate.getMinutes();
        this.seconds = this.currentDate.getSeconds();
        this.handleConfirm(isVisible);
        //
        this.$nextTick(_ =>{
          this.ajustScrollTop();
        });
      },
      //
      handleConfirm(visible=false,first){
        if(first) return;//
        const date = new Date(limitRange(this.currentDate, this.selectableRange, 'HH:mm:ss'));
        this.$emit('pick',date,visible,first);
      },
      //
      handleChange(date){
        if(date.hours !== undefined){
          this.currentDate.setHours(date.hours);
          this.hours = this.currentDate.getHours();
        }
        if(date.minutes !== undefined){
          this.currentDate.setMinutes(date.minutes);
          this.minutes = this.currentDate.getMinutes();
        }
        if(date.seconds !== undefined){
          this.currentDate.setSeconds(date.seconds);
          this.seconds = this.currentDate.getSeconds();
        }
        //
        this.handleConfirm(true);
      },
      //
      ajustScrollTop() {
        return this.$refs.spinner.ajustScrollTop();
      },
      //
      initScrollPos() {
        return this.$refs.spinner.initScrollPos();
      }
    },
    watch:{
      pickerWidth(val) {
        this.width = val;
      },
      //visible/
      visible(val) {
        this.currentVisible = val;
        let isChange = false;
        if(val){
          this.oldValue = this.currentDate?this.currentDate.getTime():"";
        }else {
          let curVal = this.currentDate?this.currentDate.getTime():"";
          if(this.oldValue!=curVal){//
            isChange=true;
          }
        }
        this.$emit("toggle-visible",val,isChange);
      },
      selectableRange(val){
        this.$refs.spinner.selectableRange = val;
      },
      value(newVal,oldVal){

        //   enter bug
        if(newVal && oldVal && (newVal.getTime() == oldVal.getTime())){
          return;
        }

        let date;
        if(newVal instanceof Date){
          date = limitRange(newVal, this.selectableRange);
        }else if(!newVal){//
          date = new Date();
        }
        this.handleChange({
          hours: date.getHours(),
          minutes: date.getMinutes(),
          seconds: date.getSeconds()
        });
        //
        this.$nextTick(_ => this.ajustScrollTop());
      }
    },
    computed:{
      // ''
      showSeconds(){
        return (this.format || '').indexOf('ss') !== -1;
      }
    },
    data(){
      return {
        popperClass: '',//class
        width: this.pickerWidth || 0,  //width
        currentVisible: this.visible || false,
        format: 'HH:mm:ss',//time
        selectableRange: [],  //
        currentDate: this.$options.defaultValue||new Date(),//()
        value: "",//
        hours: 0,
        minutes: 0,
        seconds: 0,
        oldValue:''//
      }
    },
    props:{
      pickerWidth: '',//width
      visible: Boolean,//
      date: {
        default(){
          return new Date();
        }
      }
    },
    beforeDestroy(){
      //dom
      if(this.listenDomObj){
        this.listenDomObj.disconnect();
        this.listenDomObj=null;
      }
    }
  }

</script>


