            &lt;zoehis-time-picker v-model=&quot;value3&quot; show-error-tip error-tip&quot;showErrorTiptrue&quot;&gt;&lt;/zoehis-time-picker&gt;
          </code></pre></div>
          <div class="item-desc">
            error-tip"" <br>
            1.showErrorTip  false, error-tip <br>
            2.showErrorTip: true, errorTip
          </div>
        </div>
