          <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
          <tr><td>popper-class</td><td>class</td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>tab-inner</td><td>tab()</td><td>boolean</td><td>true/false</td><td>false</td></tr>
          <tr><td>getTime()</td><td>getTime(isDisplay)</td><td>1.boolean(true() false)</td><td>-</td><td>-</td></tr>
          <tr><td>setTime(time)</td><td></td><td>1.time(new Date()/)</td><td></td><td>-</td></tr>
          <tr><td>hide()</td><td></td><td></td><td>-</td><td>-</td></tr>
          <tr><td>@click-now</td><td></td><td></td><td></td><td>-</td></tr>
          <tr><td>@click-sure</td><td></td><td></td><td></td><td>-</td></tr>
          <tr><td>@tab</td><td>tab</td><td>1.</td><td></td><td>-</td></tr>
          <tr><td>@enter</td><td>enter</td><td>1.</td><td></td><td>-</td></tr>
          <tr><td>@inputnative</td><td></td><td>1.v-model</td><td>-</td><td>-</td></tr>
          <tr><td>@show</td><td></td><td>1.v-model</td><td>-</td><td>-</td></tr>
          <tr><td>@hide</td><td></td><td>1.v-model</td><td>-</td><td>-</td></tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    data: function () {
      return {
        value1:new Date("2016-6-6 10:10:10"),
        value2:new Date("2016-6-6"),
        value3:new Date(),
        value4:new Date()
      };
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {
      handleClickEnter(){
        this.$refs.nowTime.setTime(new Date("2018-2-2 21:22:1"));
      },
      getPickerValue(){
        console.log('v-model=value1',this.value1);
        console.log('',this.$refs.picker1.getTime("HH:mm:ss"));
        console.log('',this.$refs.picker1.getTime());
      },
      setPickerValue(){
        this.$refs.picker1.setTime(new Date("2016-6-6 10:10:10"));
      },
      tabEven(curVal){
        console.log("tab",curVal);
      }
    }
  }
</script>



