<template>
  <div :class="['zoehis_picker',{'zoehis_picker_onlypanel':onlyPanel}]">
    <!--inputcheck-type="int"input-->
    <zoehis-input :placeholder="placeholder"
                  :class="{'date_onlypanel':onlyPanel}"
                  :icon="iconFont"
                  type="text"
                  showclose
                  :readonly="(type==='daterange' && !rangeedit)||type==='datetime'||readonly||hasTime|| (Boolean(format) && format!='MM-dd')"
                  v-model="showInputDate"
                  :width="inputWidth"
                  @click="clickInput"
                  @clickicon="clickIcon"
                  @focus="focusInput"
                  ref="reference"
                  :clearable="clearable"
                  @clear="clickClearDate(true)"
                  :disabled="disabled"
                  @blur="inputBlur"
                  :clickselect="true"
                  @keydown.native="handleKeydown"
                  :error-tip="errorTip"
                  :show-error-tip="showErrorTip"
                  :beforeClick="beforeClick"
                  @keyup.native="handleKeyup"
                  :ime-mode="imeMode">
    </zoehis-input>

    <!---->
    <div v-if="type==='date' && isShowPanel" @click="eStop">
      <zoehis-datepanel key="date"
                        :isShowPanel="isShowPanel"
                       :initDate="initDate"
                       :prevent-over-id="preventOverId"
                       :type="type"
                        :format="format"
                       :selectYearArr="selectYearArr"
                       @clickGetDate="clickGetDate"
                       @clickClearDate="clickClearDate"
                       @selectedDate="setDate"
                       @timeValue="getChildTime"
                        :date-init-flag="dateInitFlag"
                        :align="align" ref="onePanel"
                        :ime-mode="imeMode">
      </zoehis-datepanel>
    </div>

    <!---->
    <!--divv-clickoutside="handleClose"-->
    <div class="zoehis_pick_range"  v-else-if="type==='daterange' && isShowPanel" @click="eStop">
      <zoehis-datepanel key="daterangeLeft"
                        :isShowPanel="isShowPanel" :initDate="initDate"
                       :prevent-over-id="preventOverId"
                       :type="type" :initEndDate="initEndDate"
                       :selectYearArr="selectYearArr"
                        :format="format" ref="rangeLeftRef"
                        @selectedDate="setDate" :isClickStartDate="isClickStartDate"
                       :initDateHelpSave="initDateHelpSave"
                       @getMouseOverStamp="getMouseOverStamp" :align="align"
                        :ime-mode="imeMode">
      </zoehis-datepanel>
      <zoehis-datepanel key="daterangeRight"
                        :isShowPanel="isShowPanel" :initDate="initEndDate"
                       :prevent-over-id="preventOverId"
                       :type="type" :isRightPanel="true"
                       :selectYearArr="selectYearArr"
                        :format="format" ref="rangeRightRef"
                        @selectedDate="setDate" :initEndDate="initEndDate"
                       :startTimestampOfIndex="startTimestampOfIndex"
                       :isClickStartDate="isClickStartDate" :initDateHelpSave="initDateHelpSave"
                       @clickClearDate="clickClearDate"
                       @clickGetDate="clickGetDate" @getMouseOverStamp="getMouseOverStamp"
                       :align="align"
                        :ime-mode="imeMode">
      </zoehis-datepanel>
    </div>
    <!---->
    <div class="zoehis_pick_range"  v-else-if="type==='datetime' && isShowPanel" @click="eStop">
      <zoehis-datepanel key="datetimeLeft"
                        :isShowPanel="isShowPanel" :initDate="initDate"
                       :prevent-over-id="preventOverId"
                       :type="type" :initEndDate="initEndDate"
                       :selectYearArr="selectYearArr"
                        :format="format"
                        @selectedDate="setDate" :isClickStartDate="isClickStartDate"
                       :initDateHelpSave="initDateHelpSave"
                       @timeValue="getChildTime" @getMouseOverStamp="getMouseOverStamp"
                       :align="align"
                        :ime-mode="imeMode">
      </zoehis-datepanel>
      <zoehis-datepanel key="datetimeRight"
                        :isShowPanel="isShowPanel" :initDate="initEndDate"
                       :prevent-over-id="preventOverId"
                       :type="type" :isRightPanel="true" :align="align"
                       :selectYearArr="selectYearArr"
                        :format="format"
                        @selectedDate="setDate" :initEndDate="initEndDate"
                       :startTimestampOfIndex="startTimestampOfIndex"
                       :isClickStartDate="isClickStartDate" :initDateHelpSave="initDateHelpSave"
                       @clickClearDate="clickClearDate"
                       @clickGetDate="clickGetDate" @timeValue="getChildTime"
                       @getMouseOverStamp="getMouseOverStamp"
                        :ime-mode="imeMode">
      </zoehis-datepanel>
    </div>
  </div>
</template>


<script type="text/ecmascript-6">
  import ZoehisDatepanel from "./date-panel.vue";
  import Clickoutside from '../../../src/utils/clickoutside';
  import {on} from '../../../src/utils/dom.js';
  import {trim} from '../../../src/utils/util.js';
  import dateUtil from '../../../src/utils/date';

  export default{
    name: "ZoehisDatePicker",
    components: {ZoehisDatepanel},
    directives: { Clickoutside },

    data: function () {
      return {
        showInputDate: "",  //input
        isShowPanel: false,//panelpanel
        initDate: {},  //{year: year, month: month, date: date}
        initEndDate: {},  //
        startDate: "",
        endDate: "",
        startTimestampOfIndex: null,  //
        isClickStartDate: true,  //
        initDateHelpSave: {},  //initDate
        initEndDateHelpSave: {},  //initEndDate
        oneDateClear: false,  //

//        panel
        childLeftTime: null,  //
        childRightTime: null,
        childLeftTimePause: null,  //
        childRightTimePause: null,
        leftInterTime: {},  //Json
        rightInterTime: {},  //
        mouseStamp: null,  //
        clickPanelChange: false,  //true

        changeYearByKey: null,//
        changeMonthByKey: null,
        changeDateByKey: null,

        oneDateJson: {},//
        clearState:false,//
      }
    },

    computed: {
      iconFont(){//inputlogo
        if (this.disabled){
        return ""
        }
        return this.icon
      },
      //inputel
      reference(){
        return this.$refs.reference.$el;
      },
      //input
      refInput(){
        if(this.reference) return this.reference.querySelector("input");
        return {};
      },

      inputWidth: function () {
        if(this.width) return this.width;
        // 
        if(this.type==="date"){
          return this.hasTime?175:110;
        }else if(this.type==="daterange"){// 
          return 210;
        }else{// 
          if(this.format === "HH:mm"){//
            return 310;
          }
          return 345;
        }
      },
    },

    props: {
      dateInitFlag: {// 
        type: Boolean,
        default: true
      },

      endDateOver:{//23:59:59   type==="daterange"
        default: false,
        type: Boolean
      },
      nowBtnText:{//text
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.datepicker.now');//
          }else {
            return "";
          }
        },
        type: String
      },
      nowBtn:{//
        default: false,
        type: Boolean
      },
      beforeClick: Function,
      type: {//daterange,date
        type: String,
        default: "date"
      },
      dateType: {// v-model
        type: String,
        default: "worldTime"
      },
      placeholder: {
        type: String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.datepicker.selectDate');
          }else {
            return "";
          }
        }
      },
      align: {
        type: String,
        default: "left"
      },
      disabled: {
        type: Boolean,
        default: false
      },
      preventOverId: {
        type: String,
        default: ""
      },
      width: "",//input
      setGetDateType: {//12
        default: "1"
      },
      originDate: {},
      value: "",//input
      clearable: {
        default: true,
        type: Boolean
      },
      onalign: {
        default: true,
        type: Boolean
      },
      readonly: {
        default: false,
        type: Boolean
      },
      hasTime:{//
        default: false,
        type:Boolean
      },
      oneDateHasTime: {//emit00:00:00
        default: true,
        type:Boolean
      },
      errorTip: { //  "" 
        type: String,
        default: ""
      },
      icon:{
        type: String,
        default: "z_time_normal"
      },
      format:{
        type: String,
        default: ""
      },
      onlyPanel:{
       type:Boolean,
        default:false
      },
      showErrorTip:{   //true 
        type:Boolean,
        default:false
      },
      imeMode:{   // ime-mode 
        type:String,
        default:"disabled"
      },
      rangeedit:{// true:
        type:Boolean,
        default:false
      },
    },

    watch: {
      value(val){
        this.unEqualEmit(val);
      },

      disabled(boo){
        if(!boo){
//          this.initFunc();
        }
      },

      oneDateJson: {
        deep:true,
        handler: function (json) {
          if(this.noTriggerFlag){
            this.noTriggerFlag = false;
            return
          }

          if(!json){
            this.$emit("input","");
            this.$emit("change","");
            return
          }
          let emitDate;
          if(this.hasTime){
            emitDate = new Date(json.year,json.month-1,json.date,json.hour,json.minute,json.second);
          }else{
            let nowDate = this.getNewTimeHMS(new Date());
            emitDate = new Date(json.year,
              json.month-1,json.date,
              nowDate.hour,nowDate.minute,nowDate.second);
          }
          if(this.setGetDateType==1)
            this.$emit("input",emitDate);
          if(this.setGetDateType==2)
            this.$emit("input",emitDate.getTime());
            this.$emit("change",emitDate,emitDate.getTime());
        }
      },

      isShowPanel(boo){
        if(!boo) {
          this.isClickStartDate = true;
          if(!this.onalign){
            this.initDateHelpSave = {};
            this.initEndDateHelpSave = {};
          }
          this.startDate = "";
          this.$emit("hide");
        }else{
          this.$emit("show");
        }
      }
    },

    created: function () {
      //i18n   $t 
      let years = "";
      if(this.$t && (typeof this.$t == "function")){
        years=this.$t('zoehisUI.datepicker.years');
      }

      this.keydownFlag = false;//keyup
      this.selectYearArr = [];
      for(let i=1870;i<=2050;i++){
        this.selectYearArr.push({id: i,text: i+ years});
      }
    },

    mounted: function () {
      if(this.type==="date"){
        if(this.value===false){
          this.initFunc("","",1);
          this.clickClearDate(true);
          return
        }
        this.initFunc(this.value,"",1);

        this.noTriggerFlag = true;
        this.oneDateJson = this.initDate;
      }else{
        if(!this.value){//
          this.initFunc("","",1);
          if(this.value===false){
            this.clickClearDate(true);
          }
          return
        }
        if(this.value.startDate===false&&this.value.endDate===false){//false
          this.initFunc("","",1);
          this.clickClearDate(true);
          return
        }
        if(this.value.startDate||this.value.endDate){//false
          this.initFunc(this.value.startDate,this.value.endDate,1);
        }else{
          this.initFunc("","",1);
        }
      }
    },

    methods: {
      /***
       * 
       * @parmes num0 1input 2
       */
      setCurrentFocus(num){
        this.currentFocusState = num;
        switch(num){
          case 0:
            setTimeout(()=>{
              if(this.currentFocusState === 0){
                this.changePanel(false);
              }
            },50);
            break;
        }
      },

      setErrorTipPlace(placement){
        if(!this.currentPlacement || this.currentPlacement !== placement){
          this.currentPlacement = placement;
          //bottomtop
          if(placement.indexOf("bottom")!=-1){
            placement = placement.replace("bottom","top");
          }else {
            placement = placement.replace("top","bottom");
          }
          this.$refs.reference.setPlacementToError(placement);
        }
      },

      changeClear(boo){//
        this.clearState = boo;
      },
      unEqualEmit(val){//value
        if(this.value===false){
          this.clickClearDate(true);
          return
        }
        if(this.type==="date"){
          let dateVal = new Date(val);
          if(!this.isDate(dateVal)) return
          let getDate = this.getNewTime(dateVal);

          if(this.hasTime){
            let getDateHMS = this.getNewTimeHMS(dateVal);//  
            if(getDate.year==this.initDate.year&&getDate.month==this.initDate.month&&getDate.date==this.initDate.date&&
              getDateHMS.hour==this.initDate.hour&&getDateHMS.minute==this.initDate.minute&&getDateHMS.second==this.initDate.second){//
              return
            }else{
              this.initFunc(dateVal);
            }
          }else{
            if(getDate.year==this.initDate.year&&getDate.month==this.initDate.month&&getDate.date==this.initDate.date){//
              return
            }else{
              this.initFunc(dateVal);
            }
          }
        }else{
          let sDate,eDate,getSDate,getEDate;
          sDate = new Date(val.startDate);
          eDate = new Date(val.endDate);
          if(!this.isDate(sDate)||!this.isDate(eDate)) return
          if(this.type==="daterange"){
            getSDate = this.getNewTime(sDate);
            getEDate = this.getNewTime(eDate);
            if((getSDate.year==this.initDate.year&&getSDate.month==this.initDate.month&&getSDate.date==this.initDate.date)&&
              (getEDate.year==this.initEndDate.year&&getEDate.month==this.initEndDate.month&&getEDate.date==this.initEndDate.date)){//
              return
            }else{
              this.initFunc(sDate,eDate);
            }
          }else{
            getSDate = this.getNewTime(sDate);
            let getSTime = this.getNewTimeHMS(sDate);
            getEDate = this.getNewTime(eDate);
            let getETime = this.getNewTimeHMS(eDate);
            if((getSDate.year==this.initDate.year&&getSDate.month==this.initDate.month&&getSDate.date==this.initDate.date)&&
              (getEDate.year==this.initEndDate.year&&getEDate.month==this.initEndDate.month&&getEDate.date==this.initEndDate.date)&&
              (getSTime.hour==this.initDate.hour&&getSTime.minute==this.initDate.minute&&getSTime.second==this.initDate.second)&&
              (getETime.hour==this.initEndDate.hour&&getETime.minute==this.initEndDate.minute&&getETime.second==this.initEndDate.second)){//
              return
            }else{
              this.initFunc(sDate,eDate);
            }
          }
        }
      },

      inputBlur: function () {
        this.setCurrentFocus(0);
        if(this.type === "date" && !this.hasTime){//
          if(this.refInput.value){//input
            this.keyEnterHandle();//input
          }
        }else if(this.type==="daterange" && this.rangeedit && this.showInputDate){
          let curDate = this.getStartEndDate();
          if(!curDate){
            // 
            this.initFunc(this.value.startDate,this.value.endDate);
            this.$nextTick(()=>{
              // 
              if(this.currentFocusState==2){
                this.$refs.rangeLeftRef.updateInfo(this.value.startDate,this.initDate);
                this.$refs.rangeRightRef.updateInfo(this.value.startDate,this.initEndDate);
              }
            });
          }else{
            //
            if(this.isDate(this.value.startDate) && this.isDate(this.value.endDate)){
              let s1 = new Date(this.value.startDate).setHours(0,0,0);
              let e1 = new Date(this.value.endDate).setHours(0,0,0);
              let s2 = new Date(curDate.start).setHours(0,0,0);
              let e2 = new Date(curDate.end).setHours(0,0,0);
              //
              if((s1 == s2) && (e1 == e2)){
                return;
              }
            }
            this.initFunc(curDate.start,curDate.end);
            this.$nextTick(()=>{
              // 
              if(this.currentFocusState==2){
                this.$refs.rangeLeftRef.updateInfo(curDate.start,this.initDate);
                this.$refs.rangeRightRef.updateInfo(curDate.end,this.initEndDate);
              }
            });
          }
        }
      },

      getDisabled(){//
        return this.disabled;
      },

      focus: function () {//
        // onlyPanel
        if(this.onlyPanel){
          this.refInput.setSelectionRange(0,0);
        }
        this.refInput.focus();
      },

      selectAllInput(){//input
        this.refInput.setSelectionRange(0,this.refInput.value.length);
      },

      focusInput(){//
        this.clickInput();
        this.setCurrentFocus(1);
        if(this.type == "date"){
//          this.refInput.setSelectionRange(0,10);
        }
        this.$emit("focus");
      },

      getMouseOverStamp: function (mouseStamp) {
        this.mouseStamp = mouseStamp;
      },

      getChildTime: function (timeJson) {
        if(this.hasTime){
          timeJson.inputTime = timeJson.inputTime==""? "00:00:00":timeJson.inputTime;
          this.childLeftTimePause = timeJson.inputTime;
          return
        }
        var getInterNationTime = timeJson.internationalTime;
        timeJson.inputTime = timeJson.inputTime==""? "00:00:00":timeJson.inputTime;
        // 
        if(!timeJson.isRightPanel){
          this.childLeftTimePause = timeJson.inputTime;
          //data---------------------
//          
          if(getInterNationTime!=""){
            this.leftInterTime = {
              hour: getInterNationTime.getHours(),
              minute: getInterNationTime.getMinutes(),
              second: getInterNationTime.getSeconds(),
            }
          }else{
            //          
            this.leftInterTime = {hour: 0, minute: 0, second: 0}
          }
        }else{// 
          this.childRightTimePause = timeJson.inputTime;
          //
          if(getInterNationTime!=""){
            this.rightInterTime = {
              hour: getInterNationTime.getHours(),
              minute: getInterNationTime.getMinutes(),
              second: getInterNationTime.getSeconds(),
            }
          }else{
//            
            this.rightInterTime = {hour: 0, minute: 0, second: 0}
          }
        }


      },

      //
      clickGetDate: function (oneDateJson){
        let inputValue = "";
        if(this.type==='date'){
          if(oneDateJson&&JSON.stringify(oneDateJson)!="{}"&&!this.oneDateClear){
            var initDateTime = this.getNewTimeHMS(new Date(oneDateJson.stamp));
            switch(true){
              case this.hasTime && !Boolean(this.format)://  format
                inputValue = this.changeDateType(oneDateJson)+" "+this.getInputHMS(initDateTime);
                break;
              case this.hasTime && (this.format == 'HH:mm' || this.format == 'HH'):// 2011-11-10 12:01
                inputValue = this.changeDateType(oneDateJson)+" "+this.getInputHMS(initDateTime,this.format);
                break;
              case !this.hasTime && this.format == 'MM-dd':// 12-01
                inputValue = dateUtil.format(oneDateJson.stamp, this.format);
                break;
              default:
                inputValue = this.changeDateType(oneDateJson);
                break;
            }
            oneDateJson.hour = initDateTime.hour;
            oneDateJson.minute = initDateTime.minute;
            oneDateJson.second = initDateTime.second;
//            v-model
            var parentModel = this.dateType==="stickTime"?
              this.changeDateType(oneDateJson):
              new Date(oneDateJson.year,oneDateJson.month-1,oneDateJson.date,0,0,0);
            this.handleBeforeClick(inputValue,oneDateJson);
          }else{
            this.changePanel(false);
          }
        }else{
          var initDateHelpSave = this.initDateHelpSave,
            initEndDateHelpSave = this.initEndDateHelpSave;
            // this.initDatethis.initEndDate-----------------------
//          
          if(this.onalign||(JSON.stringify(initDateHelpSave)!="{}"&&JSON.stringify(initEndDateHelpSave)!="{}")){
            if(this.initDateHelpSave.date){// 
             this.initDate = initDateHelpSave;
            }else{// 
              if(this.initEndDateHelpSave.date){
                this.initDate = initEndDateHelpSave;
              }
            }
            if(this.initEndDateHelpSave.date){// 
             this.initEndDate = initEndDateHelpSave;
            }else{// 
              if(this.initDateHelpSave.date){
                this.initEndDate = initDateHelpSave;
              }
            }
            this.childLeftTime = this.childLeftTimePause;
            this.childRightTime = this.childRightTimePause;
            var startDate = new Date(
              this.initDate.year,
              this.initDate.month-1,
              this.initDate.date,
              0,0,0),
              endDate = new Date(
                this.initEndDate.year,
                this.initEndDate.month-1,
                this.initEndDate.date,
                0,0,0),
              daterange = {startDate:startDate,endDate:endDate};
//            input-------------------------------
            if(this.type==="datetime"){
              var leftInterTime = this.leftInterTime;
              var rightInterTime = this.rightInterTime;
              var startDateOfInput = this.changeDateType(this.initDate);
              var endDateOfInput = this.changeDateType(this.initEndDate);
              startDate = new Date(
                this.initDate.year,
                this.initDate.month-1,
                this.initDate.date,
                leftInterTime.hour,
                leftInterTime.minute,
                leftInterTime.second);
              endDate = new Date(
                this.initEndDate.year,
                this.initEndDate.month-1,
                this.initEndDate.date,
                rightInterTime.hour,
                rightInterTime.minute,
                rightInterTime.second);
              daterange = {startDate:startDate,endDate:endDate};
              inputValue = this.dateTimeRangeType(
                startDateOfInput,
                this.childLeftTime,
                endDateOfInput,
                this.childRightTime);
              if(this.setGetDateType==2){
                daterange = this.getTimestampJson(daterange);
              }
              this.handleBeforeClick(inputValue,daterange);
            }else if(this.type==="daterange"){
              if(this.endDateOver){
                daterange.endDate = new Date((new Date(daterange.endDate).setHours(23,59,59)));
              }
              if(this.setGetDateType==2){
                daterange = this.getTimestampJson(daterange);
              }
              inputValue = this.dateRangeType(this.initDate, this.initEndDate);
              this.handleBeforeClick(inputValue,daterange);
            }
          }else{
//            input
//            this.$emit("input","");
          }
        }
      },

      handleBeforeClick(inputValue,activeDate){//
        let beforeClick = this.$props.beforeClick;//propsbeforeClick
        if(typeof beforeClick == "function"){//beforeClick
          let oldDateStamp,activeDateStamp;//v-model
          if(this.type == "date"){//beforeClick
            oldDateStamp = +this.$props.value;
            if(this.hasTime){
              activeDateStamp = +new Date(activeDate.year,activeDate.month-1,activeDate.date,activeDate.hour,activeDate.minute,activeDate.second);
            }else{
              activeDateStamp = +new Date(activeDate.year,activeDate.month-1,activeDate.date);
            }
          }else if(this.type == "datetime" || this.type == "daterange"){//beforeClick
            oldDateStamp = Object.assign({},this.$props.value);//
            oldDateStamp = {
              startDate: +oldDateStamp.startDate,
              endDate: +oldDateStamp.endDate,
            };
            activeDateStamp = {
              startDate: +activeDate.startDate,
              endDate: +activeDate.endDate,
            };
          }
          let isGoOn = beforeClick(activeDateStamp,oldDateStamp);
          if(isGoOn !== false){//beforeClickreturn false
            this.doSureBtn(inputValue,activeDate);
          }
        }else {
          this.doSureBtn(inputValue,activeDate);
        }
      },

      /**
       * @parm inputValue:inputactiveDate
       */
      doSureBtn(inputValue,activeDate){//
//        todo emit
        switch(this.type){
          case "date":
            this.oneDateJson = activeDate;
            this.initDate = activeDate;
            break;
          case "daterange":
            this.$emit("input",activeDate);
            this.$emit("change",activeDate);//change
            break;
          case "datetime":
            this.$emit("input",activeDate);
//              this.$emit("change",activeDate);//change
            break;
        }
        this.showInputDate = inputValue;
        this.changePanel(false);
      },

      getTimestampJson: function (dateJson) {//
        dateJson.startDate = dateJson.startDate.getTime();
        dateJson.endDate = dateJson.endDate.getTime();
        return dateJson;
      },

//      input
      dateTimeRangeType: function (startDate,startTime,endDate,endTime) {
        return startDate+" "+startTime+" - "+endDate+" "+endTime
      },

//      
      clickClearDate: function (boolean) {
        var nowDate = new Date();
        var nowYear = nowDate.getFullYear();
        var nowMonth = nowDate.getMonth();
        this.showInputDate = "";
        if(this.type==="daterange"||this.type==="datetime"){
          this.isClickStartDate = false;
          this.initDateHelpSave = {};
          this.initEndDateHelpSave = {};
//          
          this.initDate = {year:nowYear,month: nowMonth+1, date: null};
          this.initEndDate = {year:nowYear,month: nowMonth+1, date: null};
          this.startTimestampOfIndex = null;
          this.emitDate({startDate:"",endDate:""},this.type);
        }else{
//          --
          this.initDate = {year:nowYear,month: nowMonth+1, date: null};
          if(boolean){
            this.oneDateClear = true;
          }
          this.oneDateJson = "";
          this.$emit("input","");
        }
        this.clearState = true;
      },
      emitDate(date){//
        this.$emit("change",date);
        this.$emit("input",date);
      },
      toSetDate: function (startDate,endDate) {
        this.initFunc(startDate,endDate);
      },

      isDate: function(date) {
        if (date === null || date === undefined) return false;
        if (isNaN(new Date(date).getTime())) return false;
        return true;
      },

//      
      initFunc: function (d,endD,isFirst){//isFirst  1mounted,change
        // if(this.disabled) return false
        if(d){
          if(typeof (d)=="string"){  //
            d = parseInt(d,10);
          }
          if(typeof (endD)=="string"){//
            endD = parseInt(endD,10);
          }
          if(!this.isDate(d)){//
            d = new Date();
          }else{
            d = new Date(d);
          }
        }else if(this.originDate){  //originDate
//          if(!this.originDate.startDate||!this.originDate.endDate)
          var setOriginDateStart= parseInt(this.originDate.startDate,10);
          var setOriginDateEnd= parseInt(this.originDate.endDate,10);
          if(!isNaN(setOriginDateStart))
            d = new Date(setOriginDateStart);
        }else{
          d = new Date();
        }

        var _this = this;
        var submitDate,showInputDate;
//       yyyy-mm-dd
        var initDate = this.initDate = this.getNewTime(d);
        if(this.oneDateHasTime){
          var initDateTime = this.getNewTimeHMS(d);
          this.initDate.hour = initDateTime.hour;
          this.initDate.minute = initDateTime.minute;
          this.initDate.second = initDateTime.second;
          this.startTimestampOfIndex = new Date(
            initDate.year,
            initDate.month-1,
            initDate.date,
            initDateTime.hour,
            initDateTime.minute,
            initDateTime.second).getTime();
        }else{
          this.startTimestampOfIndex = new Date(
            initDate.year,
            initDate.month-1,
            initDate.date,
            0,0,0).getTime();
        }
        if(this.type==="date"){
            submitDate = d;
//          if(this.hasTime){
//            showInputDate = this.changeDateType(this.initDate)+" "+this.getInputHMS(initDateTime);
//          }else{
//            showInputDate = this.changeDateType(this.initDate);
//          }
          switch(true){
            case this.hasTime && !this.format://  format
              showInputDate = this.changeDateType(this.initDate)+" "+this.getInputHMS(initDateTime);
              break;
            case this.hasTime && (this.format == 'HH' || this.format == 'HH:mm'):// 2011-11-10 12:01  2011-11-10 12
              showInputDate = this.changeDateType(this.initDate)+" "+this.getInputHMS(initDateTime,this.format);
              break;
            case !this.hasTime && this.format == 'MM-dd':// 12-01
              showInputDate = dateUtil.format(d, this.format);
              break;
            default:
              showInputDate = this.changeDateType(this.initDate);
              break;
          }

          if(this.setGetDateType==2) submitDate = submitDate.getTime();
          if(isFirst!=1){
              this.oneDateJson = this.initDate;  //
            }
        }
        if(this.type==="daterange"||this.type==="datetime") {
//          gei  endDate
          var originEndDate;
          if(!this.isDate(endD)){ //
            originEndDate = d;
            if(this.originDate&&!isNaN(this.originDate.endDate)) originEndDate = new Date(setOriginDateEnd);
          }else{
            endD = new Date(endD);
            originEndDate = endD
          }
//        
          this.initEndDate =  this.getNewTime(originEndDate);
          if(this.onalign){
            this.initDateHelpSave = this.initDate;
            this.initEndDateHelpSave = this.initEndDate;
          }
          //      input
          if(this.type==="datetime"){
            var initTimeStart = this.getNewTimeHMS(d);
            var initTimeEnd = this.getNewTimeHMS(originEndDate);// 
            this.initDate.hour = initTimeStart.hour;
            this.initDate.minute = initTimeStart.minute;
            this.initDate.second = initTimeStart.second;
            this.initEndDate.hour = initTimeEnd.hour;
            this.initEndDate.minute = initTimeEnd.minute;
            this.initEndDate.second = initTimeEnd.second;
            submitDate = {
              startDate: new Date(
                initDate.year,
                initDate.month-1,
                initDate.date,
                initTimeStart.hour,initTimeStart.minute,initTimeStart.second),
              endDate: new Date(
                this.initEndDate.year,
                this.initEndDate.month-1,
                this.initEndDate.date,
                initTimeEnd.hour,initTimeEnd.minute,initTimeEnd.second
              )};
            if(isFirst!=1){
              this.$emit("change",submitDate);//change
            }
            if(this.setGetDateType==2)
              submitDate = this.getTimestampJson(submitDate);
            var startDateOfInput = this.changeDateType(this.initDate);
            var endDateOfInput = this.changeDateType(this.initEndDate);
            // HH:mm
            let format = this.format == 'HH:mm' ? this.format : "";

            if(this.originDate&&!isNaN(this.originDate.endDate)){
              var startTimeContent = this.getInputHMS(initTimeStart, format);
              var endTimeContent = this.getInputHMS(initTimeEnd, format);
              showInputDate = this.dateTimeRangeType(startDateOfInput,startTimeContent,endDateOfInput,endTimeContent);
            }else{
//              var timeContent = "00:00:00";
              var timeStringS = this.getInputHMS(this.initDate, format);
              var timeStringE = this.getInputHMS(this.initEndDate, format);
              showInputDate = this.dateTimeRangeType(startDateOfInput,timeStringS,endDateOfInput,timeStringE);
            }
          }else{
            let rangeEndDate;
            if(this.endDateOver){//
              rangeEndDate = new Date(
                this.initEndDate.year,
                this.initEndDate.month-1,
                this.initEndDate.date,
                23,59,59
              );
            }else{
              rangeEndDate = new Date(
                this.initEndDate.year,
                this.initEndDate.month-1,
                this.initEndDate.date,
                0,0,0
              );
            }
            submitDate = {
              startDate: new Date(
                initDate.year,
                initDate.month-1,
                initDate.date,
                0,0,0),
              endDate: rangeEndDate
            }
            if(isFirst!=1){
              this.$emit("change",submitDate);//change
            }
            if(this.setGetDateType==2)
              submitDate = this.getTimestampJson(submitDate);

            showInputDate = this.dateRangeType(this.initDate, this.initEndDate);
          }
        }
        this.$emit("input",submitDate);  //   
        this.showInputDate = showInputDate;//input
        this.changeClear(false);
      },

//      inputDate = {year: this.theYear, month: this.theMonth, date: date.day};
      setDate: function (chooseDate,isRightPanel) {
        if(chooseDate.changeType==="clickPanelChange"){
          this.clickPanelChange = true;
        }
//        input
        if(this.type==="date"){
          var setDate = this.changeDateType(chooseDate);
          this.oneDateClear = false;
        }else{
          if(this.onalign){
            var chooseDateBZ = this.jsonToDate(chooseDate).getTime();
            // ----------------------------------------------
            if(isRightPanel){
              var startTimestamp = this.jsonToDate(this.initDateHelpSave).getTime();
              if(!startTimestamp){//initDateHelpSave
                startTimestamp = this.jsonToDate(this.initDate).getTime();
              }
//              datadata
              if(startTimestamp<=chooseDateBZ||this.clearState){//
                this.initEndDateHelpSave = chooseDate;
                this.startTimestampOfIndex = startTimestamp;
                this.startDate = "";
                this.isClickStartDate = true;
              }
            }else{
              var endTimestamp = this.jsonToDate(this.initEndDateHelpSave).getTime();
              if(!endTimestamp){
                endTimestamp = this.jsonToDate(this.initEndDate).getTime();
              }
              if(chooseDateBZ<=endTimestamp||this.clearState){
                this.isClickStartDate = true;
                this.initDateHelpSave = chooseDate;
                this.startDate = this.changeDateType(chooseDate);
              }
            }
          }else{
            //          input
            if(!this.startDate){
  //            
              this.isClickStartDate = false;
              this.initDateHelpSave = chooseDate;
              this.startDate = this.changeDateType(chooseDate);
            }else{
              //            
              var startTimestamp = this.jsonToDate(this.initDateHelpSave).getTime();
              var endTimestamp = this.jsonToDate(chooseDate).getTime();
  //            
              if(startTimestamp>endTimestamp){
                this.isClickStartDate = false;
                this.initDateHelpSave = chooseDate;
              }else{
                this.initEndDateHelpSave = chooseDate;
                this.startTimestampOfIndex = startTimestamp;
                this.startDate = "";
                this.isClickStartDate = true;
              }
            }
          }
        }
      },




      // input
      clickInput: function (e) {
        if(this.disabled) return false;
        if(this.type==="date" && !this.hasTime){
          this.selectionPosition();
        }else if(this.type==="daterange" && this.rangeedit){
          this.selectionPosRange();
        }
        this.changePanel(true);
//        e.stopPropagation();
      },
      //icon
      clickIcon(){
        this.refInput.focus();
      },

      selectionPosition() {// 
        var startPosition = this.refInput.selectionStart;  //
        if(this.format=="MM-dd"){//
          if(startPosition>=0&&startPosition<=2){
            this.refInput.setSelectionRange(0,2);
          }else{
            this.refInput.setSelectionRange(3,5);
          }
          return;
        }
        if(startPosition>=0&&startPosition<=4){
          this.refInput.setSelectionRange(0,4);
        }else if(startPosition>=5&&startPosition<=7){
          this.refInput.setSelectionRange(5,7);
        }else{
          this.refInput.setSelectionRange(8,10);
        }
      },

      /**  **/
      //keyCode,value,indexStart,indexEnd
      handleLRKey(parms){
        let _index1 = parms.value.indexOf("-");
        let _index2 = parms.value.indexOf("-",_index1+1);//'-'
        let valueLength = parms.value.length;
        switch(true){
          case _index1>0 && _index2>0:
          if(parms.keyCode == 37){// 
            // 
            if(parms.indexStart >= _index2){
              this.refInput.setSelectionRange(_index1+1,_index2);
            }else{
              this.refInput.setSelectionRange(0,_index1);
            }
          }else if(parms.keyCode == 39){// 
            // 
            if(parms.indexStart <= 5){
              this.refInput.setSelectionRange(_index1+1,_index2);
            }else{
              this.refInput.setSelectionRange(_index2+1,valueLength);
            }
          }
          break;
          case _index1>0 && _index2<0:
            if(parms.keyCode == 37){// 
              this.refInput.setSelectionRange(0,_index1);
            }else if(parms.keyCode == 39){// 
                this.refInput.setSelectionRange(_index1+1,valueLength);
            }
            break;
          default:
            this.refInput.setSelectionRange(0,valueLength);
            break;
        }

      },

      handleKeyup: function (e) {
        if(this.type==="date"){
          this.doKeyUpDate(e);
        }else if(this.type==="daterange" && this.rangeedit){
          //  rangeedit
          this.doKeyUpRange(e);
        }
      },

      /**
       *  daterange  start
       **/

      //
      selectionPosRange(){
        var startPosition = this.refInput.selectionStart;  //
        let sIndex,eIndex;
        if(startPosition<=4){
          sIndex=0;
          eIndex=4;
        }else if(startPosition<=7){
          sIndex=5;
          eIndex=7;
        }else if(startPosition<=10){
          sIndex=8;
          eIndex=10;
        }else if(startPosition<=15){
          sIndex=11;
          eIndex=15;
        }else if(startPosition<=18){
          sIndex=16;
          eIndex=18;
        }else{
          sIndex=19;
          eIndex=21;
        }
        this.selectInput(sIndex,eIndex);
      },
      // date keyup
      doKeyUpRange(e){
        this.keydownFlag = false;
        let [keyCode,value] = [e.keyCode,this.refInput.value];
        // 
        if(!value){
          //up down  
          if(keyCode==38 || keyCode==40 || !this.oneDateJson){
            return;
          }else {//
            this.clickClearDate(true);
          }
        }
        var indexStart = this.refInput.selectionStart;
        var indexEnd = this.refInput.selectionEnd;
        if(keyCode==38){//up
          if(indexStart<=4){
            var newYear = this.m_getKeyDate(0,4,1);
            this.showInputDate = "" + newYear + this.showInputDate.substring(4);
          }else if(indexStart<=7){
            var newMonth = this.dealMonthUpDown(5,7,1);
            this.showInputDate = this.showInputDate.substring(0,5) + newMonth + this.showInputDate.substring(7);
          }else if(indexStart<=10){
            var newDay = this.dealDayUpDown(8,10,1,this.m_getKeyDate(0,4,0),this.m_getKeyDate(5,7,0));
            this.showInputDate = this.showInputDate.substring(0,8) + newDay + this.showInputDate.substring(10);
          }else if(indexStart<=15){
            this.showInputDate = this.showInputDate.substring(0,11) + this.m_getKeyDate(11,15,1) + this.showInputDate.substring(15);
          }else if(indexStart<=18){
            this.showInputDate = this.showInputDate.substring(0,16) + this.dealMonthUpDown(16,18,1) + this.showInputDate.substring(18);
          }else{
            var newDay = this.dealDayUpDown(19,21,1,this.m_getKeyDate(11,15,0),this.m_getKeyDate(16,18,0));
            this.showInputDate = this.showInputDate.substring(0,19) + newDay;
          }
          this.selectInput(indexStart,indexEnd,true);
        }else if(keyCode==40){//down
          if(indexStart<=4){
            var newYear = this.m_getKeyDate(0,4,-1);
            this.showInputDate = "" + newYear + this.showInputDate.substring(4);
          }else if(indexStart<=7){
            var newMonth = this.dealMonthUpDown(5,7,-1);
            this.showInputDate = this.showInputDate.substring(0,5) + newMonth + this.showInputDate.substring(7);
          }else if(indexStart<=10){
            var newDay = this.dealDayUpDown(8,10,-1,this.m_getKeyDate(0,4,0),this.m_getKeyDate(5,7,0));
            this.showInputDate = this.showInputDate.substring(0,8) + newDay + this.showInputDate.substring(10);
          }else if(indexStart<=15){
            this.showInputDate = this.showInputDate.substring(0,11) + this.m_getKeyDate(11,15,-1) + this.showInputDate.substring(15);
          }else if(indexStart<=18){
            this.showInputDate = this.showInputDate.substring(0,16) + this.dealMonthUpDown(16,18,-1) + this.showInputDate.substring(18);
          }else{
            var newDay = this.dealDayUpDown(19,21,-1,this.m_getKeyDate(11,15,0),this.m_getKeyDate(16,18,0));
            this.showInputDate = this.showInputDate.substring(0,19) + newDay;
          }
          this.selectInput(indexStart,indexEnd,true);
        }else if((keyCode<58 && keyCode>47) || (keyCode<106 && keyCode>95) || (keyCode==189) || (keyCode==109)){
          // this.keyEnterHandle();
          //48-5796-105backspace
          this.doKeyupNum(indexEnd,value);
        }else if(keyCode==13){//()
          this.refInput.blur();
        }else if(keyCode==37){
          // 37  
          this.doKeyupLeft(indexEnd,value);

        }else if(keyCode==39){
          // 39  
          this.doKeyupRight(indexEnd,value);
        }else if(keyCode==229){//229   229
          let reg = /^(Digit|Numpad)[0-9]$/;
          if(reg.test(e.code)){//  
            this.doKeyupNum(indexEnd,value);
          }
        }
        this.$emit("keyup",e);
      },
      /**
       *
       * eIndex: 
       * val
       **/
      doKeyupNum(eIndex,val){
        let lineIndex = val.indexOf("");//  
        let start = val.substring(0,lineIndex);//
        let end = val.substring(lineIndex+1);//
        if(lineIndex==-1) return;//  

        //
        let startFlag = lineIndex>=eIndex; //true  
        let curDay = startFlag ? start : end;

        let newDay = "";//
        let curIndex1,curIndex2;//
        let _i1 = curDay.indexOf("-");//"-"
        let _i2 = curDay.indexOf("-",_i1+1);//"-"
        // "-" "-" ,
        if(_i1 == -1){
          //4 
          if(curDay.length<4) return;
          curDay = curDay.substring(0,4);
          //4  "-"
          newDay = curDay + "-";
          if(startFlag){
            this.showInputDate = newDay+""+end;
            this.selectInput(5,5,true);
          }else {
            this.showInputDate = start+""+newDay;
            this.selectInput(this.showInputDate.length,this.showInputDate.length,true);
          }
        }else if(_i2==-1){
          // "-" 
          //
          let curEndIndex = startFlag ? eIndex : (eIndex - lineIndex -1);

          //
          if(_i1>=curEndIndex){
            let y = curDay.substring(0,_i1);
            //,4 return4
            if(y.length<4){
              return;
            }else if(y.length==4){
              //4
              if(curEndIndex<4){// return
                return;
              }else {
                //
                if(startFlag){
                  curIndex1 = _i1+1;
                  curIndex2 = curDay.length;
                }else{
                  curIndex1 = lineIndex + 1 + _i1+1;
                  curIndex2 = val.length;
                }
                this.selectInput(curIndex1,curIndex2,true);
              }
            }else{
              //44
              newDay = curDay.substring(0,4) + curDay.substring(_i1);
              if(startFlag){
                this.showInputDate = newDay+""+end;
                curIndex1 = 5;
                curIndex2 = newDay.length;
                if(curEndIndex<4) {
                  curIndex1=curEndIndex;
                  curIndex2=curIndex1;
                }
              }else{
                this.showInputDate = start+""+newDay;
                curIndex1 = lineIndex + 1 + 5;
                curIndex2 = this.showInputDate.length;
                if(curEndIndex<4) {
                  curIndex1=lineIndex + 1 + curEndIndex;
                  curIndex2=curIndex1;
                }
              }
              this.selectInput(curIndex1,curIndex2,true);
            }
          }else{
            //
            let m = curDay.substring(_i1+1);//
            //3
            if(m.length<3){
              let t = curDay.substring(_i1+1,curEndIndex);//
              let t_length = t.length;
              t = parseInt(t);
              //(1).t_length=0   "-"; (2).0 1
              if(t<2 && t_length<2 || t_length==0) return;
              t = t % 12;
              if(t==0) t = 12;
              if(t<10) t = "0" + t;
              newDay = curDay.substring(0,_i1+1) + t + "-";

              if(startFlag){
                this.showInputDate = newDay+""+end;
                curIndex1 = _i1 + 1 + 3;
                curIndex2 = curIndex1;
              }else{
                this.showInputDate = start+""+newDay;
                curIndex1 = this.showInputDate.length;
                curIndex2 = curIndex1;
              }
              this.selectInput(curIndex1,curIndex2,true);
            }else{
              let t = curDay.substring(_i1+1,curEndIndex);//
              let t_length = t.length;
              m = m.substring(0,2);
              if(t_length==0){
                newDay = curDay.substring(0,_i1+1) + m+"-";
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = _i1 + 1;
                  curIndex2 = curIndex1;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = lineIndex+1+_i1 + 1;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);
              }else if(t_length==1){
                t = parseInt(t);
                if(t<2) {
                  newDay = curDay.substring(0,_i1+1) + m+"-";
                  if(startFlag){
                    this.showInputDate = newDay+""+end;
                    curIndex1 = _i1 + 2;
                    curIndex2 = curIndex1;
                  }else{
                    this.showInputDate = start+""+newDay;
                    curIndex1 = lineIndex+1+_i1 + 2;
                    curIndex2 = curIndex1;
                  }
                  this.selectInput(curIndex1,curIndex2,true);
                  return;
                }
                //20
                newDay = curDay.substring(0,_i1+1) + "0" + t + "-";
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = newDay.length;
                  curIndex2 = curIndex1;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);
              }else{//t_length=3  t_length=2
                m = m % 12;
                if(m==0) m = 12;
                if(m<10) m = "0" + m;
                newDay = curDay.substring(0,_i1+1) + m + "-";
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = _i1 + 1 + 3;
                  curIndex2 = curIndex1;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);
              }
            }

          }
        }else{
          // "-"
          //
          let curEndIndex = startFlag ? eIndex : (eIndex - lineIndex -1);
          if(_i1>=curEndIndex){//
            let showStr = "";
//            let y = curDay.substring(0,curEndIndex);// 2019 curEndIndex=3t="201"
            let y = curDay.substring(0,_i1);//
            if(y.length<4){
              return;
            }else if(y.length==4){
              //4
              if(curEndIndex<4){// return
                return;
              }else {
                //
                if(startFlag){
                  showStr = val;
                  curIndex1 = 5;
                  curIndex2 = _i2;
                }else{
                  showStr = val;
                  curIndex1 = lineIndex + 1 + 5;
                  curIndex2 = lineIndex + 1 + _i2;
                }
              }
            }else{
              //44
              newDay = curDay.substring(0,4) + curDay.substring(_i1);
              if(curEndIndex<4) {//
                if(startFlag){
                  showStr = newDay+""+end;
                  curIndex1 = curEndIndex;
                  curIndex2 = curEndIndex;
                }else{
                  showStr = start+""+newDay;
                  curIndex1 = lineIndex + 1 + curEndIndex;
                  curIndex2 = lineIndex + 1 + curEndIndex;
                }
              }else{
                if(startFlag){
                  showStr = newDay+""+end;
                  curIndex1 = 5;
                  curIndex2 = 7;
                }else{
                  showStr = start+""+newDay;
                  curIndex1 = lineIndex + 1 + 5;
                  curIndex2 = lineIndex + 1 + 7;
                }
              }
            }
            this.showInputDate=showStr;
            this.selectInput(curIndex1,curIndex2,true);
          }else if(curEndIndex<=_i2){//
            let m = curDay.substring(_i1+1,_i2);//
            let t = curDay.substring(_i1+1,curEndIndex);//
            let d = curDay.substring(_i2+1);//
            let t_length = t.length;
            //3
            if(m.length<3){
              t = parseInt(t);
              //(1).t_length=0   "-"; (2).0 1
              if(t<2 && t_length<2 || t_length==0) return;
              t = t % 12;
              if(t==0) t = 12;
              if(t<10) t = "0" + t;
              newDay = curDay.substring(0,_i1+1) + t + "-" + d;

              if(startFlag){
                this.showInputDate = newDay+""+end;
                curIndex1 = _i1 + 1 + 3;
                curIndex2 = curIndex1+2;
              }else{
                this.showInputDate = start+""+newDay;
                curIndex1 = this.showInputDate.length-2;
                curIndex2 = curIndex1+2;
              }
              this.selectInput(curIndex1,curIndex2,true);

            }else{
              m = m.substring(0,2);
              if(t_length==0){
                newDay = curDay.substring(0,_i1+1) + m + "-" + d;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = _i1 + 1;
                  curIndex2 = curIndex1;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = lineIndex+1+_i1 + 1;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }else if(t_length==1){
                t = parseInt(t);
                if(t<2) {
                  newDay = curDay.substring(0,_i1+1) + m + "-" + d;
                  if(startFlag){
                    this.showInputDate = newDay+""+end;
                    curIndex1 = _i1 + 2;
                    curIndex2 = curIndex1;
                  }else{
                    this.showInputDate = start+""+newDay;
                    curIndex1 = lineIndex+1+_i1 + 2;
                    curIndex2 = curIndex1;
                  }
                  this.selectInput(curIndex1,curIndex2,true);

                  return;
                }
                //20
                newDay = curDay.substring(0,_i1+1) + "0" + t + "-" + d;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = newDay.length-d.length;
                  curIndex2 = newDay.length;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length-d.length;
                  curIndex2 = this.showInputDate.length;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }else{//t_length=3  t_length=2
                m = m % 12;
                if(m==0) m = 12;
                if(m<10) m = "0" + m;
                newDay = curDay.substring(0,_i1+1) + m + "-" + d;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = newDay.length-d.length;
                  curIndex2 = newDay.length;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length-d.length;
                  curIndex2 = this.showInputDate.length;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }

            }
          }else{
            //
            let d = curDay.substring(_i2+1);//
            let t = curDay.substring(_i2+1,curEndIndex);//
            let t_length = t.length;
            //3
            if(d.length<3){
              t = parseInt(t);
              //(1).t_length=0   "-"; (2).0/1/2/3
              if(t<4 && t_length==1 || t_length==0) return;
              t = t % 31;
              if(t==0) t = 31;
              if(t<10) t = "0" + t;
              newDay = curDay.substring(0,_i2+1) + t;
              if(startFlag){
                this.showInputDate = newDay+""+end;
                curIndex1 = newDay.length+1;
                curIndex2 = curIndex1+end.indexOf("-");
              }else{
                this.showInputDate = start+""+newDay;
                curIndex1 = this.showInputDate.length;
                curIndex2 = curIndex1;
              }
              this.selectInput(curIndex1,curIndex2,true);

            }else{
              d = d.substring(0,2);
              if(t_length==0){
                newDay = curDay.substring(0,_i2+1) + d;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = _i2 + 1;
                  curIndex2 = curIndex1;
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = lineIndex+1+_i2 + 1;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }else if(t_length==1){
                t = parseInt(t);
                if(t<4) {
                  newDay = curDay.substring(0,_i2+1) + d;
                  if(startFlag){
                    this.showInputDate = newDay+""+end;
                    curIndex1 = _i1 + 2;
                    curIndex2 = curIndex1;
                  }else{
                    this.showInputDate = start+""+newDay;
                    curIndex1 = lineIndex+1+_i1 + 2;
                    curIndex2 = curIndex1;
                  }
                  this.selectInput(curIndex1,curIndex2,true);

                  return;
                }
                t = "0" + t;
                newDay = curDay.substring(0,_i2+1) + t;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = newDay.length+1;
                  curIndex2 = curIndex1+end.indexOf("-");
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }else{//t_length=3  t_length=2
                d = d % 31;
                if(d==0) d = 31;
                if(d<10) d = "0" + d;
                newDay = curDay.substring(0,_i2+1) + d;
                if(startFlag){
                  this.showInputDate = newDay+""+end;
                  curIndex1 = newDay.length+1;
                  curIndex2 = curIndex1+end.indexOf("-");
                }else{
                  this.showInputDate = start+""+newDay;
                  curIndex1 = this.showInputDate.length;
                  curIndex2 = curIndex1;
                }
                this.selectInput(curIndex1,curIndex2,true);

              }

            }
          }
        }
      },
      //
      //keyStart 
      //keyEnd 
      //keyType +1  -1 
      dealMonthUpDown(keyStart,keyEnd,keyType){
        var newMonth = this.m_getKeyDate(keyStart,keyEnd,keyType);
        if(newMonth===13) newMonth = 1;
        if(newMonth===0) newMonth = 12;
        if(newMonth<10) return "0"+newMonth;
        return ""+newMonth;
      },
      //
      //keyStart 
      //keyEnd 
      //keyType +1  -1 
      dealDayUpDown(keyStart,keyEnd,keyType,year,month){
        var newDay = this.m_getKeyDate(keyStart,keyEnd,keyType);
        var countDayOfMonth = this.getDayCountOfMonth(year,month-1);//
        if(newDay===countDayOfMonth+1) newDay = 1;
        if(newDay===0) newDay = countDayOfMonth;
        if(newDay<10) return "0"+newDay;
        return ""+newDay;
      },
      //
      //  false
      getStartEndDate(){
        let lineIndex = this.showInputDate.indexOf("");//   
        let start = this.showInputDate.substring(0,lineIndex);
        let end = this.showInputDate.substring(lineIndex+1);
        if(this.isDate(start) && this.isDate(end)){
          let sYear = start.substring(0,start.indexOf("-"));//
          sYear = trim(sYear);//
          let eYear = end.substring(0,end.indexOf("-"));//
          eYear = trim(eYear);//
          if(sYear.length!=4 || eYear.length!=4) return false;// 

          let s = new Date(start);
          let e = new Date(end);

          //setHours 
          //  ( s  e  00:00:00)
          if(s.setHours(0,0,0) > e.setHours(0,0,0)) return false;//
          // 23:59:59
          if(this.endDateOver){
            e.setHours(23,59,59);
          }
          return { start:s,end:e }
        }
        return false;
      },
      //
      selectInput(sIndex,eIndex,isMounted=false){
        if(isMounted){
          this.$nextTick( ()=> {
            this.refInput.setSelectionRange(sIndex,eIndex);
          });
        }else{
          this.refInput.setSelectionRange(sIndex,eIndex);
        }
      },
      //
      doKeyupRight(eIndex,val){
        let lineIndex = val.indexOf("");//  
        if(lineIndex==-1) return;//  
        let prevIndex = val.indexOf("-",eIndex-1);//  "-"
        if(prevIndex==-1) return;//
        //""
        if(eIndex==lineIndex+1){
          this.selectInput(lineIndex+1,prevIndex);
          return;
        }
        let beginIndex = val.indexOf("-",prevIndex+1);
        if(beginIndex==-1){
          this.selectInput(prevIndex+1,val.length);
          return;
        }
        let startFlag = lineIndex>=eIndex; //true  
        if(startFlag){
          if(lineIndex<prevIndex){
              this.selectInput(lineIndex+1,prevIndex);
          }else{
            if(lineIndex<beginIndex){
                this.selectInput(prevIndex+1,lineIndex);
            }else{
                this.selectInput(prevIndex+1,beginIndex);
            }
          }
        }else{
            this.selectInput(prevIndex+1,beginIndex);
        }
      },
      //
      doKeyupLeft(eIndex,val){
        let lineIndex = val.indexOf("");//  
        if(lineIndex==-1) return;//  
        let prevIndex = val.lastIndexOf("-",eIndex+1);//  "-"
        if(prevIndex==-1) return;//
        //""
        if(eIndex==lineIndex){
          this.selectInput(prevIndex+1,eIndex);
          return;
        }
        let beginIndex = val.lastIndexOf("-",prevIndex-1);
        let startFlag = lineIndex>=eIndex; //true  
        if(lineIndex>=prevIndex){
          //
          if(startFlag){
            beginIndex = beginIndex==-1 ? 0 : beginIndex+1;
            this.selectInput(beginIndex,prevIndex);
          }else{
            this.selectInput(prevIndex+1,lineIndex);
          }
        }else{
          let d = beginIndex>lineIndex ? beginIndex : lineIndex;
          d = d==-1 ? 0 : d+1;
          this.selectInput(d,prevIndex);
        }
      },
      /**
       *  daterange  end
       **/

      // date keyup
      doKeyUpDate(e){
        this.keydownFlag = false;
        // 
        this.showInputDate = this.refInput.value.replace(/[\u4e00-\u9fa5]/ig,'');
        let [keyCode,value] = [e.keyCode,this.refInput.value];
        // 
        if(!value){
          //up down  
          if(keyCode==38 || keyCode==40 || !this.oneDateJson){
            return;
          }else {//
            this.clickClearDate(true);
          }
        }
        var indexStart = this.refInput.selectionStart;
        var indexEnd = this.refInput.selectionEnd;
        if(keyCode==38){//up
          if(this.format=="MM-dd"){
            this.keyHandleDate2(indexStart,indexEnd,1);
          }else{
            this.keyHandleDate(indexStart,indexEnd,1);
          }
        }else if(keyCode==40){//down
          if(this.format=="MM-dd"){
            this.keyHandleDate2(indexStart,indexEnd,-1);
          }else{
            this.keyHandleDate(indexStart,indexEnd,-1);
          }
        }else if((keyCode<58 && keyCode>47) || (keyCode<106 && keyCode>95) || (keyCode==189) || (keyCode==109)){
          // this.keyEnterHandle();
          //48-5796-105backspace
          if(this.format=="MM-dd"){
            this.m_getNumKeyChange2(indexStart,indexEnd,value);
          }else{
            this.m_getNumKeyChange(indexStart,indexEnd,value);
          }
        }else if(keyCode==9){//tab()
//          this.keyTabHandle(indexStart,indexEnd);
        }else if(keyCode==13){//()
//          this.keyEnterHandle();
          this.refInput.blur();
        }else if(keyCode==37 || keyCode==39){
          // 37 39 
          this.handleLRKey({keyCode,value,indexStart,indexEnd});
        }else if(keyCode==229){//229   229
          let reg = /^(Digit|Numpad)[0-9]$/;
          if(reg.test(e.code)){//  
            if(this.format=="MM-dd"){
              this.m_getNumKeyChange2(indexStart,indexEnd,value);
            }else{
              this.m_getNumKeyChange(indexStart,indexEnd,value);
            }
          }

        }
       // this.keyEnterHandle();
        this.$emit("keyup",e);
      },

      keyEnterHandle(){//
        if(Boolean(this.format) && this.format!="MM-dd")return;
        let year,month,date;
        let value = this.refInput.value;
        if(!this.format){//
          year = value.substring(0,4);
          month = value.substring(5,7);
          date = value.substring(8,10);
        }else{//
          year = this.oneDateJson.year;
          month = value.substring(0,2);
          date = value.substring(3,5);
        }
        if(!value||(!this.format && value.length>10) || (this.format && value.length>5)||!year||!month||!date||isNaN(Number(year))||isNaN(Number(month))||isNaN(Number(date))){//
          this.initFunc();
          return false
        }
        year = parseInt(year,10);//
        month = parseInt(month,10);
        date = parseInt(date,10);
        if(year<1800||year>2200||month>12||month<1||date<1||date>31){ //
          this.initFunc();
          return false
        }
        this.changeYearByKey = year;//
        this.changeMonthByKey = month;
        this.changeDateByKey = date;
        this.initDate = {//
          year: year,
          month: month,
          date: date
        };
        if(!this.format){//
          this.showInputDate = this.enterSpecialInput(value);//input
        }else{
          this.showInputDate = value;
        }
        if(this.oneDateJson.year==this.initDate.year &&
          this.oneDateJson.month==this.initDate.month &&
          this.oneDateJson.date==this.initDate.date){
          return;//return
        }
        this.oneDateJson = this.initDate;
//        this.changePanel(false);
      },
      enterSpecialInput(value){//        input
        if(value.length>10){//
          value = value.substring(0,10);
        }
        let _index = value.indexOf("-",0);//-
        if(_index>=0){
          let _index2 = value.indexOf("-",_index+1);//-
          if(_index2 == -1){//'-' 
            value = value.substring(0,7) + "-" + value.substring(7,9);
          }
          if(!value[_index2+2]){
            value = value.substring(0,8) + "0" + value.substring(8,9);
          }
        }
        return value;
      },

      keyTabHandle: function (keyStart,keyEnd){//tab
        var setKeyStart,setKeyEnd;
        if(keyStart>=0&&keyStart<=4){
          setKeyStart = 5;
          setKeyEnd = 7;
        }else if(keyStart>=5&&keyStart<=7){
          setKeyStart = 8;
          setKeyEnd = 10;
        }
//        else if(keyStart>=8&&keyStart<=10){
//          setKeyStart = 0;
//          setKeyEnd = 4;
//        }
        this.$nextTick(function () {
          this.refInput.setSelectionRange(setKeyStart,setKeyEnd);
        })
      },
      /**
       * substring
       * @param keyStart
       * @param keyEnd
       * @param inputVal
         */
      m_getNumKeyChange: function (keyStart,keyEnd,inputVal){//input
        var _index1 = inputVal.indexOf("-");
        var _index2 = inputVal.indexOf("-",_index1+1);//'-'
        var inputLong = inputVal.length;
        // '-'
        switch(true){
          case _index1 < 0 && _index1 != 4 && inputLong >= 4:
            inputVal = inputVal.substring(0,4) + "-" + inputVal.substring(4,9);
            break;
          case _index2 < 0 && _index2 != 7 && inputLong >= 7:
            inputVal = inputVal.substring(0,7) + "-" + inputVal.substring(7,9);
            break;
          case inputLong < 4:
            return
        }

        // 
        let numSplitArr = inputVal.split("-");
        let [numSplit0,numSplit1,numSplit2] = [numSplitArr[0],numSplitArr[1],numSplitArr[2]];
        let [selectionStart,selectionEnd] = [keyEnd,keyEnd];
        _index1 = inputVal.indexOf("-");
        _index2 = inputVal.indexOf("-",_index1+1);//'-'
        inputLong = inputVal.length;
        !numSplit0 && (numSplit0 = "");
        !numSplit1 && (numSplit1 = "");
        !numSplit2 && (numSplit2 = "");

        // -  
        if(numSplit0 && numSplit0.length==4){
          if(_index1 == keyEnd){
            selectionStart = _index1 + 1;
            selectionEnd = _index2;
            if(_index1 + 1>_index2){
              selectionEnd = _index1 + 1;
            }
          }else if(_index1 == keyEnd - 1){
            selectionStart = _index1 + 2;
            selectionEnd = _index1 + 2;
          }
        }
        //
        if(numSplit1 && numSplit1.length==2){
          if(_index2 == keyEnd){
            selectionStart = _index2 + 1;
            selectionEnd = inputLong;
            numSplit1 = parseInt(numSplit1)%12;
            if(numSplit1==0){numSplit1=12;}
            if((""+numSplit1).length<2){
              numSplit1 = "0" + numSplit1;
            }
            inputVal = `${numSplit0}-${numSplit1}-${numSplit2}`;
          }else if(_index2 == keyEnd -1){
            selectionStart = _index2 + 2;
            selectionEnd = _index2 + 2;
          }
        }
        //
        if(numSplit2 && numSplit2.length==2){
          //
          if(inputVal.length == keyEnd){
            //
            let curDay = this.getDayCountOfMonth(parseInt(numSplit0),parseInt(numSplit1)-1);
            numSplit2 = parseInt(numSplit2)%curDay;
            if(numSplit2==0){numSplit2=curDay;}
            if((""+numSplit2).length<2){
              numSplit2 = "0" + numSplit2;
            }
            inputVal = `${numSplit0}-${numSplit1}-${numSplit2}`;
          }
        }
        switch(true){
          case numSplit0 && numSplit0.length>4:
            if(_index1 == keyEnd){
              numSplit1 = numSplit0.substring(numSplit0.length-1,numSplit0.length) + numSplit1.substring(0,1);
              selectionStart = _index1 + 1;
              selectionEnd = _index1 + 1;
            }
            inputVal = `${numSplit0.substring(0,4)}-${numSplit1}-${numSplit2}`;
            break;
          case numSplit1 && numSplit1.length>2:
            if(_index2 == keyEnd){
              numSplit2 = numSplit1.substring(numSplit1.length-1,numSplit1.length) + numSplit2.substring(0,1);
              selectionStart = _index2 + 1;
              selectionEnd = _index2 + 1;
            }
            inputVal = `${numSplit0}-${numSplit1.substring(0,2)}-${numSplit2}`;
            break;
          case numSplit2 && numSplit2.length>2:
            inputVal = `${numSplit0}-${numSplit1}-${numSplit2.substring(0,2)}`;
            break;
        }

        if(inputVal.length > 10){
          inputVal = inputVal.substring(0,10);
        }
        this.showInputDate = inputVal;
        this.$nextTick(()=>{
          this.refInput.setSelectionRange(selectionStart,selectionEnd);
        })
      },
      m_getNumKeyChange2: function (keyStart,keyEnd,inputVal){//input
        let firstIndex = inputVal.indexOf("-");
        if(inputVal.length<2){//2 
          return;
        }
        if(firstIndex<0 && inputVal.length>=2){
          let t = inputVal.substring(0,2);
          t = parseInt(t)%12;
          if(t==0){t=12;}
          if((""+t).length<2){
            t = "0" + t;
          }
          inputVal = `${t}-${inputVal.substring(2,4)}`;
          this.showInputDate = inputVal;
          this.$nextTick(()=>{
            this.refInput.setSelectionRange(3,5);
          });
          return;
        }

        // 
        let numSplitArr = inputVal.split("-");
        let [numSplit0,numSplit1] = [numSplitArr[0],numSplitArr[1]];
        let [selectionStart,selectionEnd] = [keyEnd,keyEnd];
        // -  
        if(numSplit0 && numSplit0.length==2){
          if(firstIndex == keyEnd){
            selectionStart = firstIndex + 1;
            selectionEnd = 5;
            numSplit0 = parseInt(numSplit0)%12;
            if(numSplit0==0){numSplit0=12;}
            if((""+numSplit0).length<2){
              numSplit0 = "0" + numSplit0;
            }
            inputVal = `${numSplit0}-${numSplit1}`;
          }
        }
        if(numSplit1 && numSplit1.length>2){
          inputVal = `${numSplit0}-${numSplit1.substring(0,2)}`;
        }
        //
        if(numSplit1 && numSplit1.length==2){
          //
          if(inputVal.length == keyEnd){
            //
            let curDay = this.getDayCountOfMonth(parseInt(this.oneDateJson.year),parseInt(numSplit0)-1);
            numSplit1 = parseInt(numSplit1)%curDay;
            if(numSplit1==0){numSplit1=curDay;}
            if((""+numSplit1).length<2){
              numSplit1 = "0" + numSplit1;
            }
            inputVal = `${numSplit0}-${numSplit1}`;
          }
        }

        this.showInputDate = inputVal;
        this.$nextTick(()=>{
          this.refInput.setSelectionRange(selectionStart,selectionEnd);
        })
      },
      //
      keyHandleDate: function (keyStart,keyEnd,keyType) {//up/down
        var inputVal = this.refInput.value;//input
        if(keyStart>=0&&keyStart<=4){//
          var newYear = this.m_getKeyDate(0,4,keyType);
          var otherDate = inputVal.substring(4,10);
          this.initDate.year = newYear;
          this.changeYearByKey = newYear;//
          this.oneDateJson.year = newYear;//
//          this.oneDateJson = {year:2010,month: 10,date: 10}
          this.showInputDate = newYear+otherDate;
        }else if(keyStart>=5&&keyStart<=7){//
          var newMonth = this.m_getKeyDate(5,7,keyType);
          if(newMonth===13) newMonth = 1;
          if(newMonth===0) newMonth = 12;
          this.initDate.month = newMonth;
          this.changeMonthByKey = newMonth;//
          this.oneDateJson.month = newMonth;
            newMonth = newMonth/10>=1? newMonth : "0"+newMonth;
          var otherDateYear = inputVal.substring(0,5);
          var otherDateDay = inputVal.substring(7,10);
          this.showInputDate = otherDateYear+newMonth+otherDateDay;
        }else if(keyStart>=8&&keyStart<=10){//
          var newDate = this.m_getKeyDate(8,10,keyType);
          var newYear = this.m_getKeyDate(0,4,0);
          var newMonth = this.m_getKeyDate(5,7,0);
          var countDayOfMonth = this.getDayCountOfMonth(newYear,newMonth-1);//
          if(newDate===countDayOfMonth+1) newDate = 1;
          if(newDate===0) newDate = countDayOfMonth;
          this.initDate.date = newDate;
          this.changeDateByKey = newDate;//
          this.oneDateJson.date = newDate;
            newDate = newDate/10>=1? newDate : "0"+newDate;
          var otherDate = inputVal.substring(0,8);
          this.showInputDate = otherDate+newDate;
        }
        this.$nextTick( () => {//
          this.refInput.setSelectionRange(keyStart,keyEnd);
//          this.keyEnterHandle();
        });
      },
      //
      keyHandleDate2: function (keyStart,keyEnd,keyType) {//up/down
        var inputVal = this.refInput.value;//input
        if(keyStart>=0&&keyStart<=2){//
          var newMonth = this.m_getKeyDate(0,2,keyType);
          if(newMonth===13) newMonth = 1;
          if(newMonth===0) newMonth = 12;
          this.initDate.month = newMonth;
          this.changeMonthByKey = newMonth;//
          this.oneDateJson.month = newMonth;
            newMonth = newMonth/10>=1? newMonth : "0"+newMonth;
          var otherDateDay = inputVal.substring(2,5);
          this.showInputDate = newMonth+otherDateDay;
        }else if(keyStart>=3&&keyStart<=5){//
          var newDate = this.m_getKeyDate(3,5,keyType);
          var newYear = this.oneDateJson.year;
          var newMonth = this.m_getKeyDate(0,2,0);
          var countDayOfMonth = this.getDayCountOfMonth(newYear,newMonth-1);//
          if(newDate===countDayOfMonth+1) newDate = 1;
          if(newDate===0) newDate = countDayOfMonth;
          this.initDate.date = newDate;
          this.changeDateByKey = newDate;//
          this.oneDateJson.date = newDate;
            newDate = newDate/10>=1? newDate : "0"+newDate;
          var otherDate = inputVal.substring(0,3);
          this.showInputDate = otherDate+newDate;
        }
        this.$nextTick( () => {//
          this.refInput.setSelectionRange(keyStart,keyEnd);
//          this.keyEnterHandle();
        });
      },

      m_getKeyDate: function (keyStart,keyEnd,keyType){//
        var inputVal = this.refInput.value;//input
        var inputYear = parseInt(inputVal.substring(keyStart,keyEnd),10);
        if(typeof (inputYear)=="number"){
          var date = inputYear+keyType;
        }
        return date
      },
      handleKeydown(e){//keydown
        const keyCode = e.keyCode;

        if(!this.checkoutWrite(keyCode)){
          e.preventDefault();
        }

        if(keyCode != 8){// 
          if( this.keydownFlag ){
            e.preventDefault();
          }else{
            this.keydownFlag = true;
          }
        }

        switch (keyCode){
          case 27://ESC
            this.changePanel(false);
            this.refInput.blur(); //
            e.stopPropagation();
            break;
          case 9://tab
            this.changePanel(false);
//            e.preventDefault();
            break;
          case 13://tab
            this.changePanel(false);
            break;
          case 38://up
          case 40://down
            e.preventDefault();
            break;
          default:
            break;
        }
        this.$emit("keydown",e);
      },

      /**  **/
      checkoutWrite(keyCode){
        let sourceFlag1 = this.refInput.value.length > 10 && keyCode != 8;// 11
        let sourceFlag2 = this.refInput.value.length > 21 && keyCode != 8;// 22

        if(sourceFlag1 && !(this.type==="daterange" && this.rangeedit) || sourceFlag2 && (this.type==="daterange" && this.rangeedit))return false;
        if((keyCode>=96 && keyCode<=105) || (keyCode>=48 && keyCode<=57) || (keyCode>=37 && keyCode<=40) ||
                keyCode==8 || keyCode==9 || keyCode==13 || keyCode==108 || keyCode==189 || keyCode==17){

          return true;//
        }
        return false;
      },

//      
      eStop: function (e) {
        e.stopPropagation();
      },

      //      199911--1
      getDayCountOfMonth: function(year, month) {
        if (month === 3 || month === 5 || month === 8 || month === 10) {
          return 30;
        }
        if (month === 1) {
          if (year % 4 === 0 && year % 100 !== 0 || year % 400 === 0) {
            return 29;
          } else {
            return 28;
          }
        }
        return 31;
      },
      jsonToDate: function (json) {//
        var date = new Date(
          json.year,
          json.month-1,
          json.date,
          0,0,0);
        return date
      },
      getInputHMS: function (dataJson, format) {//
        if(format){
          let stamp = new Date().setHours(dataJson.hour, dataJson.minute, dataJson.second);
          return dateUtil.format(stamp, format);
        }

        var hour,minute,second,time;
        hour = dataJson.hour>9? dataJson.hour : "0"+dataJson.hour;
        minute = dataJson.minute>9? dataJson.minute : "0"+dataJson.minute;
        second = dataJson.second>9? dataJson.second : "0"+dataJson.second;
        time = hour + ":" + minute + ":" + second;
        return time
      },

      getNewTimeHMS: function (date) {//
        var jsonHMS = {
          hour: date.getHours(),
          minute: date.getMinutes(),
          second: date.getSeconds()
        }
        return jsonHMS;
      },

//      
      dateRangeType: function (startDate, endDate) {
        var start = this.changeDateType(startDate);
        var end = this.changeDateType(endDate);
        let line = this.rangeedit ? "" : " - ";
        return start + line + end;
      },

//       yyyy-mm-dd
      getNewTime: function (d) {
        var year, month, date;
        year = d.getFullYear();
        month = d.getMonth() + 1;
        date = d.getDate();
        return {year: year, month: month, date: date}
      },

//      ==
      changeDateType: function (newDate) {
        var year, month, date;
        year = newDate.year;
        month = (newDate.month/10) < 1 ? "0"+newDate.month : newDate.month;
        date = (newDate.date/10) < 1 ? "0"+newDate.date : newDate.date;
        return year+"-"+month+"-"+date;
      },
      getStandardDate(json){//
        if(json.hour && json.minute && json.second){
          return +new Date(json.year,json.month-1,json.date,json.hour,json.minute,json.second);
        }else{
          return +new Date(json.year,json.month-1,json.date);
        }
      },
      /**
       * emit  
       */
      //
      changePanel: function (isShow) {
        if(isShow){
          //    dateInitFlagtrue  
          if(this.type=='date' && this.hasTime && this.dateInitFlag && !this.showInputDate){
            let curDate = new Date();
            this.initDate = {
              changeType:"clickPanelChange",
              year:curDate.getFullYear(),
              month:curDate.getMonth()+1,
              date:curDate.getDate(),
              hour:curDate.getHours(),
              minute:curDate.getMinutes(),
              second:curDate.getSeconds()
            };
            this.clearState=false;
            this.noTriggerFlag = true;
            this.oneDateJson = this.initDate;
          }

//
//          this.initFunc(this.value,"",1);
//
//          this.noTriggerFlag = true;
//          this.oneDateJson = this.initDate;
        }
        this.isShowPanel = isShow;
      },
      touchActiveEvent(){//
        this.changePanel(false);
        this.$emit("click-now");
      },
      setTime(date){
        this.unEqualEmit(date);
//        setTimeout(()=>{
//          this.changePanel(true);
//        },1);
      },
      //
      show(){
        this.changePanel(true);
      },
      //
      hide(){
        this.changePanel(false);
      },
    }
  }
</script>



