<template>
  <div>
    <div class="component_example">
      <h1>Progress </h1>
      <h2></h2>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <span></span><zoehis-progress :percentage="0"></zoehis-progress>
          <br>
          <span></span><zoehis-progress :percentage="25"></zoehis-progress>
          <br>
          <span></span><zoehis-progress :percentage="100" status="success"></zoehis-progress>
          <br>
          <span></span><zoehis-progress :percentage="50" status="exception"></zoehis-progress>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;0&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;25&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;100&quot; status=&quot;success&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;50&quot; status=&quot;exception&quot;&gt;&lt;/zoehis-progress&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            percentage <strong></strong>Number 0-100  <br>

          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <span></span><zoehis-progress :percentage="100" status="success"></zoehis-progress>
          <br>
          <span></span><zoehis-progress :percentage="50" :show-text="false" color="#000" :stroke-width="24"></zoehis-progress>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;100&quot; status=&quot;success&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;50&quot; :show-text=&quot;false&quot; color=&quot;#000&quot; :stroke-width=&quot;24&quot;&gt;&lt;/zoehis-progress&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            status successexception  percentage 100 percentage 50status
            color<br>
            show-text <br>
            stroke-width12px
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>:percentage</td>
            <td>(*) </td>
            <td>Number</td><td>0  100</td><td>-</td>
          </tr>
          <tr>
            <td>status</td><td>(success100%exception50%)</td><td>string</td><td>success()/exception()</td><td>-</td>
          </tr>
          <tr>
            <td>:stroke-width</td><td></td><td>Number</td><td>-</td><td>12</td>
          </tr>
          <tr>
            <td>:show-text</td><td></td><td>Boolean</td><td>true/false</td><td>true</td>
          </tr>
          <tr>
            <td>:color</td><td> status ,"#ddddd"</td><td>string</td><td>-</td><td>-</td>
          </tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>

    </div>
  </div>
</template>

<script type="text/babel">
  export default{
    data: function () {
      return {
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {

    }
  }
</script>



