<template>
  <div class="component_example">
    <h1>Slider </h1>
    <p></p>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <div class="t_block">
          <span></span>
          <zoehis-slider v-model="numVal"></zoehis-slider>
        </div>
        <div class="t_block">
          <span></span>
          <zoehis-slider v-model="numVal2" disabled></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>v-model</strong><strong>disabled</strong>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal2&quot; disabled&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal:50,
                  numVal2:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <div class="t_block">
          <span></span>
          <zoehis-slider v-model="numVal3" show-input></zoehis-slider>
        </div>
        <div class="t_block">
          <span></span>
          <zoehis-slider v-model="numVal4" show-input :format-tooltip="formatTooltip"></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>show-input</strong><strong>:format-tooltip</strong>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal3&quot; show-input&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal4&quot; show-input :format-tooltip=&quot;formatTooltip&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal3:50,
                  numVal4:50,
                }
              },
              methods:{
                formatTooltip(val){
                  return val/100;
                },
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <span>10</span>
            <zoehis-slider v-model="numVal5" show-input :step='10'></zoehis-slider>
          </div>
          <div class="t_block">
            <span>0.02</span>
            <zoehis-slider v-model="numVal6" show-input :step='0.02'></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>step</strong> Number
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;10&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal5&quot; show-input :step=&quot;10&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;0.02&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal6&quot; show-input :step=&quot;0.02&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal5:50,
                  numVal6:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <zoehis-slider v-model="numVal7" show-input :min='-20' :max='200'></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>min</strong>/<strong>max</strong> Number
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal7&quot; show-input :min=&quot;-20&quot; :max=&quot;200&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal7:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <span>height</span>
        <div class="t_block" style="height:200px;">
          <zoehis-slider v-model="numVal9" show-input vertical></zoehis-slider>
        </div>
        <div class="t_block">
          <span>height</span>
          <zoehis-slider v-model="numVal9" vertical height="160px"></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>vertical</strong><strong>height</strong>vertical=true
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;span&gt;height&lt;/span&gt;
              &lt;div class=&quot;t_block&quot; style=&quot;height:200px;&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal9&quot; show-input vertical&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;height&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal9&quot; vertical height=&quot;160px&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal9:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <zoehis-slider v-model="numVal8" @change="handleChange" @input="handleInput"></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>@change</strong>/<strong>@input</strong>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal8&quot; @change=&quot;handleChange&quot; @input=&quot;handleInput&quot; &gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal8:50,
                }
              },
              methods:{
                handleChange(val){
                  console.log(&quot;&quot;,val);
                },
                handleInput(val){
                  console.log(&quot;&quot;,val);
                },
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item item_table">
        <h4></h4>
        <zoehis-table keycode="code" :data="propData" :configs="demoConfig1"></zoehis-table>
        <h4></h4>
        <zoehis-table keycode="code" :data="eventData" :configs="demoConfig2"></zoehis-table>
      </div>
  </div>
</template>
<script>
  export default {
    name: 'Slider',
    data () {
      return {
        numVal:50,
        numVal2:50,
        numVal3:50,
        numVal4:50,
        numVal5:50,
        numVal6:50,
        numVal7:50,
        numVal8:50,
        numVal9:50,
        propData:[],
        demoConfig1:[],
        demoConfig2:[],
        eventData:[],
      }
    },
    methods:{
      
      handleChange(val){
        console.log("",val);
      },
      handleInput(val){
        console.log("",val);
      },
      formatTooltip(val){
        return val/100;
      },

      handleToggle(e) {
        $(e.target)
          .next()
          .find("pre code")
          .toggle();
      }
    },
    mounted:function() { //
      this.demoConfig1=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"explain",dataItemName:"",filterType:"",itemWidth:"340px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"type",dataItemName:"",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"optional",dataItemName:"",filterType:"",itemWidth:"180px",fixedFlag:""},
        {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"defaultVal",dataItemName:"",filterType:"",itemWidth:"",fixedFlag:""},
      ];
      this.demoConfig2=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"",filterType:"",itemWidth:"80px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"explain",dataItemName:"",filterType:"",itemWidth:"300px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"param",dataItemName:"",filterType:"",itemWidth:"240px",fixedFlag:""},
      ];
      this.propData=[
        {code:1,parameter:"v-model='value'", explain:"", type:"number",optional:"-",defaultVal:"0"},
        {code:2,parameter:"min", explain:"", type:"number",optional:"-",defaultVal:"0"},
        {code:9,parameter:"max", explain:"", type:"number",optional:"-",defaultVal:"100"},
        {code:3,parameter:"disabled", explain:"", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:4,parameter:"step", explain:"", type:"number",optional:"-",defaultVal:"1"},
        {code:5,parameter:"show-input", explain:"", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:6,parameter:"format-tooltip", explain:"", type:"function(value)",optional:"-",defaultVal:""},
        {code:7,parameter:"vertical", explain:"", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:8,parameter:"height", explain:"slidervertical=true", type:"string",optional:"",defaultVal:""},
      ];
      this.eventData=[
        {code:1,parameter:"@change", explain:"", param:"1.(value)"},
        {code:2,parameter:"@input", explain:"", param:"1.(value)"},
      ];

      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style scoped>
ul {
  margin: 0 0 0 20px;
  padding: 0;
}
.component_example .clearflex {
  display: block;
}
.component_example .item-toggle {
  cursor: pointer;
  background: #8fcae5;
  text-align: center;
  font-weight: bold;
}
.component_example .item-desc {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code pre code {
  display: none;
}
</style>


