<template>
  <div
    class="zoehis_select_dropdown" tabindex="-1"
    :style="{minWidth:minWidth,maxWidth:maxWidth+'px'}"
  >
    <slot></slot>
  </div>
</template>

<script type="text/babel">
  import Popper from '../../../src/utils/vue-popper';

  export default {
    name: 'ZoehisSelectDropdown',

    componentName: 'ZoehisSelectDropdown',

    mixins: [Popper],

    props: {
      placement: {
        default: 'bottom-start'
      },

      boundariesPadding: {
        default: 0
      },

      popperOptions: {
        default() {
          return {
            forceAbsolute: true,
            gpuAcceleration: false
          };
        }
      },
      preventOverId:{
        type:String,
        default:""
      },
      //
      multiple:{
        type:Boolean,
        default:false
      },
      minWidth: String,//
      maxWidth: [String,Number],//
    },

    data() {
      return {

      };
    },

    computed: {
      popperClass() {
        return this.$parent.popperClass; //popperClass
      },
    },

    watch: {

    },
    methods:{
      // x-placement bug
      listenDomAttr(){
        if(this.listenDomObj || !this.$el){
          return;
        }
        let _this = this;
        this.listenDomObj=new MutationObserver(function(res){
          _this.$emit("placementchange",res[0].target.getAttribute("x-placement"))
        });
        let  options = {
          attributes:true,//
          attributeFilter:["x-placement"]
        };
        this.listenDomObj.observe(this.$el, options)
      },
    },

    mounted() {
      if(this.multiple) return;
      this.referenceElm = this.$parent.$refs.reference.$el; //inputdom
      this.$parent.popperElm = this.popperElm = this.$el;
      this.$on('updatePopper', () => {
        if (this.$parent.visible) this.updatePopper();
      });
      this.listenDomAttr();
    },
    beforeDestroy(){
      //dom
      if(this.listenDomObj){
        this.listenDomObj.disconnect();
        this.listenDomObj=null;
      }
    }
  };
</script>



