<template>
  <div>
    <div class="component_example">
      <h1>Multiple-Select </h1>
      <h2></h2>
      <div class="item" id="aaaaaa">
        <h4></h4>
        <div class="item-content">
          <zoehis-multiple-select
            v-model="selectVal" :selectdata="selectData"></zoehis-multiple-select>
        </div>
        <div class="item-detail">
  <div class="item-code"><pre><code>
    &lt;zoehis-multiple-select
    v-model=&quot;selectVal&quot; :selectdata=&quot;selectData&quot;&gt;&lt;/zoehis-multiple-select&gt;
    &lt;script&gt;
    export default{
      data: function () {
        return {
          selectVal: [],
          selectdata: [
          {id: 1, text: &quot;&quot;},
          {id: 2, text: &quot;&quot;},
          {id: 3, text: &quot;&quot;}
          ]
        }
      }
    }
    &lt;/script&gt;
  </code></pre></div>
          <div class="item-desc">
            v-modelv-model [{id:1,text:11}]<br>
            selectdata select <br>
            
          </div>
        </div>
      </div>
      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr><td>v-model="value"</td><td>(),</td><td>array</td><td>-</td><td>[]</td></tr>
          <tr><td>selectdata</td><td>select()</td><td>array</td><td>-</td><td>[]</td></tr>
          <tr><td>placeholder</td><td></td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>itemcode</td><td>ID</td><td>string</td><td>-</td><td>id</td></tr>
          <tr><td>itemtext</td><td>Text</td><td>string</td><td>-</td><td>text</td></tr>
          <tr><td>itemkey</td><td>:keyvue:keyitemcode()</td><td>string</td><td>-</td><td>itemcode</td></tr>
          <tr><td>filter-field</td><td>filter-method</td><td>array</td><td>-</td><td>[]</td></tr>
          <tr><td>dropdown-width</td><td></td><td>string/number</td><td>100  20%</td><td>-</td></tr>
          <tr><td>dropdown-max-width</td><td>  500px,</td><td>string/number</td><td>100px </td><td>500px</td></tr>
          <tr><td>width</td><td>select, :append-to-body</td><td>string/number</td><td>100  20%</td><td>115</td></tr>
          <tr><td>render-item</td><td>function(item){}</td><td>function1.item</td><td>-</td><td>-</td></tr>
          <tr><td>disabled</td><td></td><td>boolean</td><td>false/true</td><td>false</td></tr>
          <tr><td>no-match-text</td><td></td><td>string</td><td>-</td><td></td></tr>
          <tr><td>defaultfirst</td><td></td><td>boolean</td><td>true/false</td><td>true</td></tr>
          <tr><td>hide()</td><td></td><td></td><td>-</td><td>-</td></tr>
          <tr><td>delay-time</td><td>filter-methodms</td><td>string/number</td><td>-</td><td>200</td></tr>
          <tr><td>prevent-over-id</td><td>bodyID</td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>clearable</td><td></td><td>boolean</td><td>true/false</td><td>true</td></tr>
          <tr><td>scrollMove()</td><td></td><td>1.{top:Number,left:Number}</td><td>-</td><td>-</td></tr>
          <tr><td>filter-method</td><td></td><td>function1.query()</td><td>-</td><td>-</td></tr>
          <tr><td>filter-row-data</td><td>selectdata</td><td>function1.query:2.item return true</td><td>-</td><td>-</td></tr>
          <tr><td>ime-mode()</td><td>IME disabled </td><td>String</td><td>auto/normal/active/inactive/disabled</td><td>auto</td></tr>
          <tr><td>@scroll-to-top</td><td>  </td><td>1.query2.vm</td><td>-</td><td>-</td></tr>
          <tr><td>@scroll-to-bottom</td><td>  </td><td>1.query2.vm</td><td>-</td><td>-</td></tr>
          <tr><td>@scroll-after</td><td></td><td>1.query2.vm</td><td>-</td><td>-</td></tr>
          <tr><td>@change</td><td></td><td>1.v-model</td><td>-</td><td>-</td></tr>
          <tr><td>@change</td><td></td><td>1.v-model</td><td>-</td><td>-</td></tr>
          <tr><td>@visible-change</td><td>/</td><td>1</td><td>-</td><td>-</td></tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    data: function () {
      return {
        selectData: [
          {id: 1, text: ""},
          {id: 2, text: ""},
          {id: 3, text: ""}
        ],
        selectVal:[{id:1,text:""}]
      }
    },
    watch:{
      selectVal:{
        deep:true,
        handler(val){
          console.log("selectVal1",val)
        }
      },
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });

//      for(let i=0; i<13; i++){
//        this.selectData.push({id:i,text:""+i+i});
//      }
    },
    methods: {

    }
  }
</script>



