<template>
  <!--isShowPanel-->
  <transition name="el-zoom-in-top" @after-leave="doDestroy">
    <!-- <div> -->
      <div class="zoehis_date_panel" ref="zoehisDatePanelRef" popper-deal-flag="1"
           tabindex="-1" @focusin="dropdownFocusin" @focusout="dropdownFocusout"
           onselectstart="return false"
           :class="{'zoehis_date_panel_range' : isRightPanel,
           'zoehis_date_panel_range_isalign' : isRightPanel && align==='right',
           'zoehis_panel_left' : (type==='daterange'||type==='datetime') && !isRightPanel,
           'zoehis_panel_left_isalign' : (type==='daterange'||type==='datetime') && !isRightPanel && align==='right',
           'zoehis_panel_onedate' : (align==='right'&&type==='date')||
           (align==='right'&&type!=='date'&&isRightPanel),
           'zoehis_left_align' : align==='right'&&type!=='date'&&!isRightPanel,
           }">
        <!---->
        <div class="zoehis_panel_time" v-if="type==='datetime'||fatherHasTimeReal">
          <zoehis-time-picker v-model="theTime"
                              :format="currentFormat"
                              @click-sure="handleClickTimePicker"
                              ref="picker1"
                              :prevent-over-id="referePreventOverId"
                              :clearable="fatherClearable"
                              class="zoehis_panel_timeinput"
                              :standardtype="true"
                              :class="{'zoehis_timeinput_left':!isRightPanel,
                              'zoehis_timeinput_right':isRightPanel}"
                              :ime-mode="imeMode">
          </zoehis-time-picker>
          <div class="zoehis_panel_time_line"></div>
        </div>
        <div class="zoehis_panel_head" :id="referePreventOverId">
          <button class="zoeIconfont z_arrowALM_normal zoehis_panel_prevbtn" @click="prevYear"></button>
          <button class="zoeIconfont z_arrowTTL_normal zoehis_panel_prevbtn" @click="prevMonth"></button>
          <zoehis-select v-model="theYear"
                         :selectdata="selectYear"
                         :prevent-over-id="referePreventOverId"
                         @scroll-to-top="yearScrollTop"
                         :width="77"
                         :placeholder="i18nAttr.year"
                         itemcode="id"
                         itemtext="text"
                         ref="yearRef"
                         @scroll-to-bottom="yearScrollBottom"
                         :clearable="false">
          </zoehis-select>
          <zoehis-select v-model="theMonth"
                         :selectdata="selectMonth"
                         :prevent-over-id="referePreventOverId"
                         :clearable="false"
                         :width="62"
                         :placeholder="i18nAttr.month">
          </zoehis-select>
          <button class="zoeIconfont z_arrowARM_normal zoehis_panel_nextbtn" @click="nextYear"></button>
          <button class="zoeIconfont z_arrowTTR_normal zoehis_panel_nextbtn" @click="nextMonth"></button>
        </div>
        <div class="zoehis_panel_content">
          <ZoehisDatetable :theYear="theYear"
                           :theMonth="theMonth"
                           :initDate="initDate"
                           :type="type"
                           :isRightPanel="isRightPanel"
                           @getMouseOverStamp="getMouseOverStamp"
                           @selectedDate="getDateToParent"
                           :initEndDate="initEndDate"
                           :startTimestampOfIndex="startTimestampOfIndex"
                           :isClickStartDate="isClickStartDate"
                           :initDateHelpSave="initDateHelpSave"
                           :date-init-flag="dateInitFlag"
                           ref="oneTable">
          </ZoehisDatetable>
          <!---->
          <div  v-if="type==='date'">
            <div class="zoehis_date_line"></div>

            <div class="zoehis_date_bottom_btn" v-if="fatherHasTimeReal || father.$props.nowBtn || fatherClearable">
              <div><zoehis-button v-if="fatherHasTimeReal" size="small" class="zoehis_sure_button" @clickenter="clickGetDate">{{i18nAttr.confirmButtonText}}</zoehis-button></div>
              <div v-if="father.$props.nowBtn" class="zoehis_clear_button" @click="getActiveDate">{{father.$props.nowBtnText.substring(0,6)}}</div>
              <div v-if="fatherClearable" class="zoehis_clear_button" @click="clickClearDate">{{i18nAttr.clearButtonText}}</div>
              <div style="clear:both;"></div>
            </div>
          </div>
          <!---->
          <div  v-if="type==='daterange'||type==='datetime'">
            <div class="zoehis_date_line zoehis_panel_range_line"></div>
            <div :class="['zoehis_date_bottom_btn',{'zoehis_panel_left_button':!isRightPanel}]">
              <div><zoehis-button size="small" class="zoehis_sure_button" @clickenter="clickGetDate" v-if="isRightPanel">{{i18nAttr.confirmButtonText}}</zoehis-button></div>
              <div v-if="father.$props.nowBtn && isRightPanel" class="zoehis_clear_button" @click="getActiveDate">{{father.$props.nowBtnText.substring(0,6)}}</div>
              <div class="zoehis_clear_button" @click="clickClearDate" v-if="isRightPanel&&fatherClearable">{{i18nAttr.clearButtonText}}</div>
              <div style="clear:both;"></div>
            </div>
          </div>

        </div>
      </div>
    <!-- </div> -->

  </transition>
</template>

<script type="text/ecmascript-6">
  import ZoehisDatetable from "./date-table.vue";
  import Popper from '../../../src/utils/vue-popper';
  export default{
    name: "ZoehisDatePanel",
    components: {ZoehisDatetable},
    mixins: [Popper],
    created: function () {
      //i18n   $t 
      if(this.$t && (typeof this.$t == "function")){
        this.i18nAttr={
          confirmButtonText:this.$t('zoehisUI.datepicker.confirmButtonText'),
          clearButtonText:this.$t('zoehisUI.datepicker.clearButtonText'),
          year:this.$t('zoehisUI.datepicker.year'),
          month:this.$t('zoehisUI.datepicker.month'),
          months:this.$t('zoehisUI.datepicker.months'),
          years:this.$t('zoehisUI.datepicker.years'),
        }
      }

      // domidselectprevent-over-id
      this.referePreventOverId = 'his_date_panel_id_'+(+new Date());

      this.$on('updatePopper', () => {
          if (this.showPopper) this.updatePopper();
      });
//      
      this.addYearJsonToArr(this.selectYear);
      // 
      this.selectMonth = [];
      for(let i=1;i<=12;i++){
        this.selectMonth.push({id: i,text: i+this.i18nAttr.months});
      }
    },

    mounted: function () {
      this.$parent.popperElm = this.popperElm = this.$el;
      this.referenceElm = this.$parent.reference;
      if(this.align==="left"){
        this.currentPlacement = `bottom-start`;
      }else{
        this.currentPlacement = `bottom-end`;
      }
      if(this.type==="datetime"||this.fatherHasTimeReal){
        this.theTime = this.isDate(this.theComTime) ? this.theComTime : new Date();
        let format = "HH:mm:ss";//  
        // hh:ss
        if(this.format == "HH:mm" || this.format == "HH"){
          format = "HH:mm";
        }
        var timeJson = {
          internationalTime: this.theTime,
          inputTime: this.$refs.picker1.getTime(format),
          isRightPanel: this.isRightPanel
        }
        this.$emit("timeValue",timeJson);
      }
      //    dateInitFlagtrue
      if(this.type==="date" && this.fatherHasTimeReal && this.dateInitFlag){
        this.getOneDate = true;
      }

      //  x-placement bug
      this.listenDomAttr();
    },
    // 
    beforeDestroy(){
      this.listenDomObj.disconnect();
    },

    data: function () {
      return {
        theYear: this.initDate.year,
        theMonth: this.initDate.month,
        theTime: new Date(),
        temp: 2019,  //()
        tempTop: 2010,  //()
        selectYear: this.selectYearArr,
        // selectYear: [{id: 2011,text: 2011},{id: 2012,text: 2012},{id: 2013,text: 2013},{id: 2014,text: 2014},
        //   {id: 2015,text: 2015},{id: 2016,text: 2016},{id: 2017,text: 2017},{id: 2018,text: 2018}],
        getOneDate: null,
        isClearOneDateJson: false,  //true
        i18nAttr:{
          confirmButtonText:"",
          clearButtonText:"",
          year:"",
          month:"",
          months:"",
          years:""
        }
      }
    },

    props: {
      selectYearArr: Array,
      isShowPanel: {
        type: Boolean,
        default: false
      },
      dateInitFlag: {// 
        type: Boolean,
        default: true
      },
      initDate: {},  //   
      type: String,
      //
      isRightPanel: {
        type: Boolean,
        default: false
      },
      initEndDate: {},
      startTimestampOfIndex: Number,
      isClickStartDate: Boolean,
      initDateHelpSave: {},
      align: String,
      format:String,
      imeMode:{   // ime-mode 
        type:String,
        default:"disabled"
      },
    },

    computed: {
      //   HH:mm  HH  HH:mm  HH:mm:ss
      currentFormat(){
        if(this.format=="HH:mm" || this.format=="HH"){
          return "HH:mm";
        }else {
          return "HH:mm:ss"
        }
      },
      fatherClearState(){//
        if(this.father){
          return this.father.$data.clearState;
        }
      },
      fatherProps(){//props
        return this.$parent.$props;
      },
      fatherHasTimeReal(){//
        return this.$parent.$props.hasTime;
      },
      fatherHasTime(){//
        return this.$parent.$props.oneDateHasTime;
      },
      fatherClearable(){//
        return this.$parent.$props.clearable;
      },
      theComTime: {  //
        get(){
          if(this.type==="datetime"){
            if(this.isRightPanel){
              return new Date(
                this.initEndDate.year,
                this.initEndDate.month,
                this.initEndDate.date,
                this.initEndDate.hour,
                this.initEndDate.minute,
                this.initEndDate.second
              )
            }else{
              return new Date(
                this.initDate.year,
                this.initDate.month,
                this.initDate.date,
                this.initDate.hour,
                this.initDate.minute,
                this.initDate.second
              )
            }
          }else if(this.fatherHasTime){
            return new Date(
              this.initDate.year,
              this.initDate.month,
              this.initDate.date,
              this.initDate.hour,
              this.initDate.minute,
              this.initDate.second
            )
          }
        },
        set(val){
//          this.$parents.initFunc();
        }
      },

      fatherChangeYear(){
        if(this.father){//
          return this.father.$data.changeYearByKey;
        }
      },

      fatherChangeMonth(){//
        if(this.father){
          return this.father.$data.changeMonthByKey;
        }
      },

      father(){//
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name !== 'ZoehisDatePicker') {
            parent = parent.$parent;
          } else {
            return parent;
          }
        }
        return false;
      },
    },

    watch: {
      '$parent.$data.isShowPanel': {
        immediate: true,
        handler(boo){
          this.showPopper = boo;
        }
      },

      fatherChangeYear(newYear){
        this.theYear = newYear;
      },

      fatherChangeMonth(newMonth){
        this.theMonth = newMonth;
      },

      theTime(time){
//        
        let format = "HH:mm:ss";//  
        // hh:ss
        if(this.format == "HH:mm"){
          format = this.format;
        }
        var timeJson = {
          internationalTime: time,
          inputTime: this.$refs.picker1.getTime(format),
          isRightPanel: this.isRightPanel
        }
        this.$emit("timeValue",timeJson);
      }
    },

    methods: {
      /**
       *  daterange  start
       **/
      //
      updateInfo(date,dateStamp){
        if(!this.isDate(date)) return;// return
        let curDate = new Date(date);
        this.theYear = curDate.getFullYear();
        this.theMonth = curDate.getMonth()+1;
        this.dealYearArr();
        this.$refs.oneTable.setStartEndStamp(dateStamp);
      },
      dealYearArr(){
        let arr = this.selectYear,arr2=[];
        var min = arr[0].id;
        var max = arr[arr.length-1].id;
        let cur = this.theYear;
        //    50
        if(min>cur || max<cur || arr.length>50){
          for(let i=cur-10; i<cur+10;i++){
            arr2.push({id: i,text: i+this.i18nAttr.years});
          }
          this.selectYear=arr2;
        }
      },
      /**
       *  daterange  end
       **/

      // 
      isDate: function(date) {
        if (date === null || date === undefined) return false;
        if (isNaN(new Date(date).getTime())) return false;
        return true;
      },

      // -
      handleClickTimePicker(){
        this.$refs.zoehisDatePanelRef.focus();
      },
      // focusin
      dropdownFocusin(){
        this.father.setCurrentFocus(2);
      },
      // focusout
      dropdownFocusout(){
        this.father.setCurrentFocus(0);
      },
      // x-placement bug
      listenDomAttr(){
        if(this.listenDomObj || !this.$refs.zoehisDatePanelRef){
          return;
        }
        this.listenDomObj=new MutationObserver(res=>{
          if(res.length > 0 && res[0].target){
            this.father.setErrorTipPlace(res[0].target.getAttribute("x-placement"));
          }
        });
        let options = {
          attributes:true,//
          attributeFilter:["x-placement"]
        };
        this.listenDomObj.observe(this.$refs.zoehisDatePanelRef, options);
      },


      getActiveDate(){//
        this.father.touchActiveEvent();
      },
      getMouseOverStamp: function (mouseStamp) {
        this.$emit("getMouseOverStamp",mouseStamp);
      },

//      
      clickGetDate: function () {
        // if(this.type!="date"&&(!this.$refs.oneTable.$data.startTimestamp&&!this.$refs.oneTable.$data.endTimestamp)){
        //   this.$parent.changePanel(false);
        //   return
        // }
        if(this.fatherClearState){
          this.$parent.changePanel(false);
          // 
          if(this.type === "date" && !this.fatherHasTimeReal){

          }else{
            return
          }
        }
        if(this.type == "date" && this.fatherHasTimeReal){
          if(!this.theTime){
            this.theTime = new Date();
          }
          var theTimeJson = this.father.getNewTimeHMS(this.theTime);
          if(!this.getOneDate){//
            //
            if(this.initDate.hour != theTimeJson.hour||this.initDate.minute != theTimeJson.minute||this.initDate.second != theTimeJson.second){
              this.getOneDate = {
                year:this.initDate.year,
                month:this.initDate.month,
                date:this.initDate.date,
                hour:theTimeJson.hour,
                minute:theTimeJson.minute,
                second:theTimeJson.second
              }
              var initDateStamp = this.father.jsonToDate(this.initDate);
              var date = {day: this.initDate.date, type: "",stamp:initDateStamp};
              this.getDateToParent(date);
              //            null---------------------------------------------------------
            }
          }else{//aaa

            let isNoNum = (isNaN(theTimeJson.hour) || isNaN(theTimeJson.minute) || isNaN(theTimeJson.second));//  true: false
            let isTime = (theTimeJson.hour<0 || theTimeJson.hour>23 || theTimeJson.minute<0 || theTimeJson.minute>59 || theTimeJson.second<0 || theTimeJson.minute>59)
            if(isNoNum || isTime){
              theTimeJson = this.father.getNewTimeHMS(new Date());//
            }
            let panelDate = this.$refs.oneTable.$data.selectedNum;//
            this.getOneDate = {
              year:this.theYear,
              month:this.theMonth,
              date:panelDate,
              hour:theTimeJson.hour,
              minute:theTimeJson.minute,
              second:theTimeJson.second
            }
            var initDateStamp = this.father.jsonToDate(this.getOneDate);
            var date = {day: this.getOneDate.date, type: "",stamp:initDateStamp};
            this.getDateToParent(date);
          }
        }
        this.$emit("clickGetDate",this.getOneDate);
      },

//      
      clickClearDate: function () {
        if(this.type==="date"){
          this.isClearOneDateJson = true;
        }
        this.$emit("clickClearDate",this.isClearOneDateJson);
      },
      changeClear(boo){
        this.$parent.changeClear(boo);
      },


      //      
      addYearJsonToArr: function (yearArrs) {
        var minId = yearArrs[0].id,
          maxId = yearArrs[yearArrs.length-1].id,
          initYear = this.theYear,
          addJson;
        if(initYear<minId){
          for(var i=minId; i>initYear-5;i--){
            addJson = {id: i-1,text: i-1};
            yearArrs.unshift(addJson);
          }
        }else if(initYear>maxId){
          this.temp = this.theYear + 6;
          for(var i=maxId; i<initYear+5;i++){
            addJson = {id: i+1,text: i+1};
            yearArrs.push(addJson);
          }
        }
      },

//      (m_)
      m_prevMonth: function () {

        if(this.theMonth===1) {
          this.theMonth = 12;
          this.theYear--;
        }else{
          this.theMonth--;
        }
      },
//      (m_)
      m_nextMonth: function () {
        if(this.theMonth===12) {
          this.theMonth = 1;
          this.theYear++
        }else{
          this.theMonth++;
        }
      },

      //      
      yearScrollTop: function () {
        var item = {};
        for(let i=5;i>0;i--){
          item.text = item.id = this.selectYear[0].id - 1;
          item.text += this.i18nAttr.years;
          this.selectYear.unshift(Object.assign({},item));
        }
        this.$nextTick(()=>{
          //  33px,  165
          this.$refs.yearRef.scrollMove(165);
          //
          this.$refs.yearRef.hoverIndex=5;
        })
      },

//      
      yearScrollBottom: function () {
        var item = {};
        for(let i=5;i>0;i--){
          item.text = item.id = this.selectYear[this.selectYear.length-1].id + 1;
          item.text += this.i18nAttr.years;
          this.selectYear.push(Object.assign({},item));
        }
      },

//        emit
      getDateToParent: function (date) {
//        type012
        if (date.type === 0){
          this.m_prevMonth();
        }else if (date.type ===1){
          this.m_nextMonth();
        }
        if(this.fatherHasTime){
          let timeJson;
          if(!this.theTime){
            this.theTime = new Date();
          }
          timeJson = this.father.getNewTimeHMS(this.theTime);
          if(isNaN(timeJson.hour)||isNaN(timeJson.minute)||isNaN(timeJson.second)){//
            let nowDate = new Date();
            timeJson = this.father.getNewTimeHMS(nowDate);
            this.theTime = nowDate;
          }
            date.stamp = new Date(date.stamp).setHours(
              timeJson.hour,
              timeJson.minute,
              timeJson.second);
        }
//        
        var dateJson = {year: this.theYear, month: this.theMonth, date: date.day,stamp:date.stamp};
        this.getOneDate = dateJson;
//        
        this.isClearOneDateJson = false;
//        
        dateJson.changeType = "clickPanelChange";
        this.$emit("selectedDate", dateJson,this.isRightPanel);
        if(this.type === "date" && !this.fatherHasTimeReal){
          this.clickGetDate();
        }

      },

//      
      prevYear: function () {
        if(this.selectYear[0].id>=this.theYear-1){
          this.yearScrollTop();
        }
        this.$nextTick(()=>{
          this.theYear--;
        })
      },
//      
      prevMonth: function () {
        this.m_prevMonth();
      },
//      
      nextYear: function () {
        if(this.selectYear[this.selectYear.length-1].id<=this.theYear+1){
          this.yearScrollBottom();
        }
        this.$nextTick(()=>{
          this.theYear++;
        })
      },
//      
      nextMonth: function () {
        this.m_nextMonth();
      },

      //      
      eStop: function (e) {
        e.stopPropagation();
      },
    }

  }
</script>



