            loadNode(node,resolve){

              if (node.level === 0) {
                return resolve([{ text :&quot;&quot;,id: 1 }]);
              }
              if (node.level === 1){
                return resolve([{ text :&quot;&quot;,id: 2 },{ text :&quot;&quot;,id: 3 },{ text :&quot;&quot;,id: 4 }]);
