<template>
  <div
    class="zoehis_select"
    :style="{width:width+(!isPercent?'px':'')}"
    ref="selectWrap"
  >
    <zoehis-input
      width="100%"
      ref="reference"
      v-model="selectedLabel"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="!filterable"
      @clear="handleClear"
      @mousedownClear="handleMousedownClear"
      @clickicon="toggleMenu"
      @mousedown.native="handleMouseDown"
      @keyup.native="handleKeyup"
      @keydown.native.down.prevent="navigateOptions('next')"
      @keydown.native.up.prevent="navigateOptions('prev')"
      @keydown.native.192.prevent
      @keydown.native.27.prevent="handleClose"
      @keyup.native.enter.prevent="selectOption"
      @keydown.native.tab="handleTab"
      @focus="handleFocus"
      @focusout.native="handleBlur"
      icon="z_dropD_form"
      :clearable="showClose"
      :clickselect="false"
      showclose
      :error-tip="errorTip"
      :prevent-over-id="preventOverId"
      :delete-key="true"
      :before-clear="beforeClear"
      :show-error-tip="showErrorTip"
      :ime-mode="imeMode"
    >
    </zoehis-input>
    <zoehis-select-dropdown
      tabindex="-1"
      v-if="visible"
      ref="popper"
      @focus.native="focusDrop"
      @mousedown.native="mousedownDrop"
      @click.native="mousedownDrop"
      @focusout.native="focusoutDrop"
      :prevent-over-id="preventOverId"
      :min-width="inputWidth"
      :max-width="boxMaxWidth"
      :placement="curPlacement"
      @placementchange="placementChangeDrop"
      :class="[{has_detail:hasDetailData || detailShow || showDetailPop,show_detail:showdetail || showDetailItemName},popperClass]"
    >
      <div
        class="zoehis_select_detail_box" v-if="hasDetailData"
        @mouseenter="hoverDetailFlag=true" @mouseleave="hoverDetailFlag=false">
        <zoehis-scrollbar
          class="zoehis_select_detail" wrap-class="zoehis_select_detail__wrap">
          <h5>{{uiShowSelectDetail[itemtext]}}</h5>
          <p><strong class="zoehis_text_tt"></strong>{{uiShowSelectDetail.indication}}</p>
          <p><strong class="zoehis_text_tt"></strong>{{uiShowSelectDetail.effect}}</p>
          <p><strong class="zoehis_text_tt"></strong>{{uiShowSelectDetail.attention}}</p>
        </zoehis-scrollbar>
      </div>

      <div class="zoehis_select_detail_box" :style="{height: detailBoxHeight+'px'}" v-if="detailShow">
        <zoehis-scrollbar class="zoehis_select_detail" wrap-class="zoehis_select_detail__wrap_new">
          <slot></slot>
        </zoehis-scrollbar>
      </div>

      <div
        class="zoehis_select_detail_box" :style="{height: detailBoxHeight+'px'}" v-if="showDetailPop"
        @mouseenter="detailPopEnter" @mouseleave="detailPopLeave">
        <zoehis-scrollbar class="zoehis_select_detail" wrap-class="zoehis_select_detail__wrap_new">
          <slot></slot>
        </zoehis-scrollbar>
      </div>

      <div class="zoehis_select_shadow"></div>
      <zoehis-scrollbar
        tag="ul"
        :class="{zoehis_had_detail:true}"
        wrap-class="zoehis_select_dropdown__wrap"
        ref="scrollbar"
        @scrollafter="scrollafter"
        @mouseenter.native="scrollbarEnter"
        @mouseleave.native="scrollbarLeave"
      >
        <zoehis-option
          v-for="(item, index) in selectdata"
          :class="{'noselect_color':noselect,detail_hover:(hasDetailData || detailShow || showDetailPop) && (uiShowSelectDetail[itemtext]==item[itemtext])}"
          :item="item"
          :key="item[itemkey]"
          :value="item[itemcode]"
          :label="item[itemtext]"
          :itemindex="index"
          :noselect="noselect"
          :selectdata="selectdata"
          :showDetailItemName="showDetailItemName"
          :showDetailFlag="showDetailFlag"
          @showDetail="showDetail"
          @focus="focus"
          @mouseenter.native="optionEnter(item)"
          @mouseleave.native="optionLeave"
        >
          <div class="zoehis_select_dropdown_renderItem" v-if="(typeof renderItem=='function')" v-html="renderItem(item)"></div>
        </zoehis-option>
      </zoehis-scrollbar>
      <p class="zoehis_select_dropdown__empty" v-if="isShowNone">{{ noMatchText }}</p>
    </zoehis-select-dropdown>
  </div>
</template>

<script type="text/ecmascript-6">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInput from '../../input/index.js'
  import ZoehisScrollbar from '../../scrollbar/index.js'
  import ZoehisOption from './option.vue'
  import ZoehisSelectDropdown from './select-dropdown.vue'
  import scrollIntoView from '../../../src/utils/scroll-into-view.js'
  import { getValueByPath,isNull } from '../../../src/utils/util';
  import debounce from '../../../src/utils/throttle-debounce.js';
  import {addResizeListener,removeResizeListener} from '../../../src/utils/resize-event';//resize()

  // 
  const PLACEMENT_MAP = {
    left: 'bottom-start',
    center: 'bottom',
    right: 'bottom-end'
  };
  export default{
    name: "ZoehisSelect",
    componentName: "ZoehisSelect",
    mixins: [Emitter],
    components: {
      ZoehisInput,
      ZoehisScrollbar,
      ZoehisOption,
      ZoehisSelectDropdown,
    },
    created: function () {
      //
      this.isPercent = !this.isWidthNumber(this.width);
      //
      this.selectItem = false;
      //object
      this.selected={};
      //option vue
      this.options=[];
      //
      this.cachedOptions=[];
      //
      this.optionsCount=0;
      //placement
      this.curPlacement = PLACEMENT_MAP[this.align] || PLACEMENT_MAP.left;//vue-popper 

      this.debouncePlacementChange = debounce(50, (placement) => {
        this.$refs.reference.setPlacementToError(placement);
      });

      //
      this.debounceFilterMethod = debounce(this.delayTime,(val)=>{
        this.filterMethodFlag=true;
        this.filterMethod(val);
      });
      this.$on('handleOptionClick', this.beforeOptionSelect); //
      this.$on('onOptionDestroy', this.onOptionDestroy);
      this.$on('setSelected', this.setSelected);//
    },
    methods:{
      //
      placementChangeDrop(placement){
        if(this.curPlacement==placement) return;// 
        if(!this.inputFocus) return;// 
        this.curPlacement = placement;
        this.handlePlacementChange();
      },
      //  placement placement
      handlePlacementChange(){
        let placement2 = this.curPlacement;
        if(!placement2) return;
        //bottomtop
        if(placement2.indexOf("bottom")!=-1){
          placement2 = placement2.replace("bottom","top");
        }else {
          placement2 = placement2.replace("top","bottom");
        }
        this.debouncePlacementChange(placement2);
      },
      focusDrop(){
        this.dropFocus = true;
        this.visibleTimer && clearInterval(this.visibleTimer);
      },
      // 
      //focusin 
      mousedownDrop(){
        this.dropFocus = true;
      },
      //
      focusoutDrop(){
        this.dropFocus = false;
        this.handleBlur();
      },
      handleBlur(){
        this.inputFocus=false;
        if(this.selectFocus) return;
        this.hide();
      },
      //
      //return true   false
      getFocusFlag(){
        return this.selectFocus;
      },
      //
      hide(){
        if(this.visibleTimer){
          clearTimeout(this.visibleTimer);
          this.visibleTimer=null;
        }
        this.visibleTimer = setTimeout(()=>{
          this.visible = false;
        },200);
      },
      handleKeyup(e){
        //
        if(e.keyCode==38 || e.keyCode==40){
            this.showDetailFlag = false;
            this.showDetailSign = false;
          e.stopPropagation();
        }
        if(e.keyCode==27){
          return;
        }
        //ctrl + C   
        if(e.ctrlKey && e.keyCode==67) return;
        switch (e.keyCode){
          case 13://enter
          case 37://left
          case 38://top
          case 39://right
          case 40://down
          case 9://tab
          case 16: //shift
            //isComposing true 
            //  shift bug
            if(e.isComposing){
              // "351"  queryselectedLabel query""
              setTimeout(()=>{
                this.onInputChange(e.target.value);
              },5)
            }
            break;
          case 17: //ctrl
          case 20: //alt
            break;
          default:
            this.onInputChange(e.target.value);
        }
      },
      //
      getDisabled(){
        return this.disabled;
      },
      scrollMove(num){
        this.$refs.scrollbar && this.$refs.scrollbar.move({top:num});
      },
      //
      getText(){
        return this.selectedLabel;
      },
      //
      getSelected(){
        if(this.selected && this.selected.item){
          //uiInitFlag selectdata
          if(this.selected.item.uiInitFlag){
            return this.getItemBySelectData(this.selected.item);
          }
          return this.selected.item;
        }
        return null;
      },
      /**
       * keyCode  item
       **/
      getItemBySelectData(item){
        let option = null;
        for(let i=0,l=this.selectdata.length; i<l; i++){
          if(item[this.itemcode] == this.selectdata[i][this.itemcode]){
            option = Object.assign({},this.selectdata[i]);
            break;
          }
        }
        return option || item;
      },
      //
      doDestroy() {
        this.$refs.popper && this.$refs.popper.doDestroy();
      },
      //
      focus(index){
        if(index !== undefined){
            this.hoverIndex = index;
        }
        this.$refs.reference.focus();
      },
      //
      optionEnter(item){
        if(typeof this.showDetailFunc=='function' && this.showDetailFunc(item) === true){
            this.showDetailSign = true;
            this.$emit('detailShow',item);
        }else{
            this.showDetailSign = false;
        }
      },
      optionLeave(){
        setTimeout(()=>{
            if(!this.hoverDetailSign && !this.hoverScrollbarSign) {
                this.showDetailSign = false;
            }
        },5);
      },
      detailPopEnter(){
        this.hoverDetailSign = true;
      },
      detailPopLeave(){
        this.hoverDetailSign = false;
        if(this.showDetailSign) {
            this.showDetailSign = false;
        }
      },
      scrollbarEnter(){
        this.hoverScrollbarSign = true;
      },
      scrollbarLeave(){
        this.hoverScrollbarSign = false;
      },
      scrollafter(wrap){
        const [h,sh,st]=[wrap.clientHeight,wrap.scrollHeight,wrap.scrollTop];
        this.$emit("scroll-after",this.query,this);
        //
        if(sh-h-st<10){
          this.$emit("scroll-to-bottom",this.query,this);
        }
        //
        if(st<10){
          this.$emit("scroll-to-top",this.query,this);
        }
      },
      //
      initSelectData(){
        this.hoverIndex = this.defaultfirst ? 0 : -1;//
        let option = this.getOption(this.value);//option
        if(option){
          if(typeof this.renderShowData === "function"){
            this.selectedLabel=this.renderShowData(option.item);
          }else {
            this.selectedLabel = option.item[this.itemtext];
          }
          if(option.index !== undefined){
            this.hoverIndex = option.index;
          }
          this.selected = option;
        }
      },
      /**
       * selectv-model
       */
      initSelData(){
        this.hoverIndex = this.defaultfirst ? 0 : -1;//
        return this.getRowData();
      },
      /**
       * vm
       */
      getRowData(){
        if(isNull(this.value)) return null;
        let option = null;
        for(let i=0,l=this.selectdata.length; i<l; i++){
          if(this.value == this.selectdata[i][this.itemcode]){
            option = Object.assign({},this.selectdata[i]);
            option.tempIndex= i;
            break;
          }
        }
        if(!option && this.textvalue!==undefined){
          option = {};
          option[this.itemcode]=this.value;
          option[this.itemtext]=this.textvalue;
          option["uiInitFlag"]=true;// selectdata
        }
        if(option){
          if(typeof this.renderShowData === "function"){
            this.selectedLabel=this.renderShowData(option);
          }else {
            this.selectedLabel = option[this.itemtext];
          }
          this.hoverIndex = option.tempIndex;
          let item = {};
          item.item = option;
          item.value = this.value;
          item.currentLabel = this.selectedLabel;
          return item;
        }else {
          return null;
        }

      },
      //
      setSelectedLabel(val){
        this.selectedLabel=val;
      },
      //
      onOptionDestroy(option) {
        this.optionsCount--;
        this.filteredOptionsCount--;
        let index = this.options.indexOf(option);
        if (index > -1) {
          this.options.splice(index, 1);
          this.cachedOptions.splice(index, 1);
        }
      },
      //
      onInputChange(val) {
        //
        if (this.filterable) {
          //numString
          if(this.numString!=undefined && this.isWidthNumber(this.selectedLabel)){
            if(this.numTimer) clearTimeout(this.numTimer);
            this.numTimer = setTimeout(()=>{
              let item = null;
              for(let i=0,l=this.cachedOptions.length; i<l; i++){
                let cacheItem = this.cachedOptions[i];
                if(cacheItem.visible && cacheItem.item[this.numString]==this.selectedLabel){
                  item=cacheItem;
                  break;
                }
              }
              if(item){
                this.hoverIndex = item.itemindex;
                this.scrollToOption(item);
              }
              this.broadcast('ZoehisInput', 'inputSelect');//
            },300);
            return;
          }
          // val selectedLabel  selectedLabel
          //input""delete
          if(this.query === "" && val==""){
            this.resetHoverIndex(true);
            this.$emit('input', "");//""
            this.selected="";//
            if(!this.visible && this.selectItem) {
              this.visible = true; //visibletrue,
            }
          }else {
            this.selectItem = false;
            this.selected="";//
            this.visible = true;
            //351 
            this.$nextTick(()=>{
              this.query = val;
            });
          }
          //
          this.$nextTick(function(){
            this.scrollMove(0);
          })

        }
      },
      //
      scrollToOption(p) {
        let option = p || this.selected;
        //0
        if(!option) {
          this.$nextTick(()=>{
            this.$refs.scrollbar && this.$refs.scrollbar.move({top:0})
          });
          return;
        }
        const target = Array.isArray(option) && option[0] ? option[0].$el : option.$el;
        if (this.$refs.popper && target) {
          const menu = this.$refs.popper.$el.querySelector('.zoehis_select_dropdown__wrap');
          scrollIntoView(menu, target);
        }
      },
      //
      handleClose() {
        this.visible = false;
      },
      //
      handleFocus() {
        this.dropFocus=false;
        // 
        if(this.isClickClear){
          this.isClickClear=false;
          return;
        }
        //  
        this.visibleTimer && clearInterval(this.visibleTimer);
        this.inputFocus=true;
        this.visible = true;
        this.selectItem=false;
        this.$emit('focus');
      },
      //
      handleMouseDown(event){
        if (event.target.tagName !== 'INPUT') return;
        let inputs = this.$el.querySelectorAll('input');
        //selectinput
        if ([].indexOf.call(inputs, document.activeElement) != -1) { //
          if(!this.visible){
            this.visible = true;
            this.selectItem=false;
            event.preventDefault();
          }
        }
      },
      //
      handleMousedownClear(){
        if(this.visible){
          this.dropFocus=true;
        }
      },
      //
      handleClear(event){
        event && event.stopPropagation();
        this.handleClearData(true);
      },
      // isClickClear
      handleClearData(isClickClear=true){
        this.deleteSelected();
        this.$emit('clear');
        this.isClickClear=isClickClear;//
        if(this.visible){// 
          clearInterval(this.visibleTimer);
        }
      },
      ///
      toggleMenu(e) {
        //
        if (!this.disabled) {
          e.stopPropagation();
          this.visible = !this.visible;
          if(this.visible){// 
            this.inputFocus=true;//
            clearInterval(this.visibleTimer);
          }
        }
        this.$emit("clickicon",e)
      },
      //tab
      handleTab(e){
        this.visible = false;
      },
      //
      navigateOptions(direction) {
        if (!this.visible) {
          this.visible = true;
          return;
        }
        //option
        if (this.options.length === 0 || this.filteredOptionsCount === 0) return;
        //
        this.optionsAllDisabled = this.options.length === this.options.filter(item => item.disabled === true).length;
        if (!this.optionsAllDisabled) {
          if (direction === 'next') {
            this.hoverIndex++;
            // hoverIndex=0
            if (this.hoverIndex === this.options.length) {
              this.hoverIndex = this.options.length-1;
              return;
            }
            //
            if (this.options[this.hoverIndex].disabled === true ||
              !this.options[this.hoverIndex].visible) {
              this.navigateOptions('next');
            }
          }
          if (direction === 'prev') {
            this.hoverIndex--;
            //
            if (this.hoverIndex < 0) {
              this.hoverIndex = -1;
            }else {
              //
              if (this.options[this.hoverIndex].disabled === true ||
                !this.options[this.hoverIndex].visible) {
                this.navigateOptions('prev');
              }
            }
          }
        }
        this.hoverIndex !== -1 && this.$nextTick(() => this.scrollToOption(this.options[this.hoverIndex]));
      },
      //value option
      getOption(value) {
        let option;
        const isObject = Object.prototype.toString.call(value).toLowerCase() === '[object object]';
        for (let i = this.cachedOptions.length - 1; i >= 0; i--) {
          const cachedOption = this.cachedOptions[i];
          const isEqual = isObject
            ? getValueByPath(cachedOption.value, this.valueKey) === getValueByPath(value, this.valueKey)
            : cachedOption.value === value;
          if (isEqual) {
            option = cachedOption;
            break;
          }
        }
        if (option) return option;


        const label = !isObject
          ? this.textvalue===undefined ? value : this.textvalue : '';
        let newOption = {
          value: value,
          currentLabel: label,
          item:{}
        };
        newOption.item[this.itemcode]=value;
        newOption.item[this.itemtext]=label;
        return newOption;
      },
      //
      setSelected() {

//        let option = this.getOption(this.value);//option
        let option = this.visible ? this.getOption(this.value) : this.initSelData();//option
//        if (option.created) {
//          this.createdLabel = option.currentLabel;
//          this.createdSelected = true;
//        } else {
//          this.createdSelected = false;
//        }
        if(option){
          this.selectedLabel = option.currentLabel;
        }else {
          //v-model 
          if(!this.noClearBlur){
            this.selectedLabel = '';
          }
        }
        this.selected = option;
        this.selectItem = true;
      },
      //enteroption
      selectOption() {
        //enterbug
        if(!this.visible) {
          this.$emit("enter");
          return;
        }
        this.beforeOptionSelect(this.options[this.hoverIndex],true);
      },
      //
      deleteSelected() {
        this.$emit('input', '');
        this.selected="";
        if(!this.noClearBlur){
          this.selectedLabel="";
        }
        this.query = "";
      },
      /**
       * hoverIndex
       * selected
       * @param isQuery (selected)
       */
      resetHoverIndex(isQuery) {
        let nowIndex=-1;
        let dataLength = this.options.length;
        if(this.selected && !isQuery){
          for(let i=0; i<dataLength; i++){
            if(this.options[i].itemSelected){
              nowIndex=this.options[i].index;
              this.selected = this.options[i];//
              break;
            }
          }
        }
        // 
        if(nowIndex===-1 && this.defaultfirst){
          for(let j=0; j<dataLength; j++){
            if(this.options[j].visible){
              nowIndex=this.options[j].index;
              break;
            }
          }
        }
        this.hoverIndex = nowIndex;
      },

      //option: 
      //isEnter: enter
      beforeOptionSelect(option,isEnter){
        if(option && option.visible){
          if (typeof this.beforeSelect === 'function' && this.beforeSelect(option.value, option.item)===false) {
            //beforeSelectfalse  
          }else {
            this.handleOptionSelect(option,isEnter);
          }
        }else {
          // 
          if(this.selected && !this.outclear){
            if (typeof this.beforeSelect === 'function' && this.beforeSelect(this.selected.value, this.selected.item)===false) {
              //beforeSelectfalse  
            }else {
              this.handleOptionSelect(this.selected,true);
            }
          }else {
            if (typeof this.beforeSelect === 'function' && this.beforeSelect("", null)===false) {
              //beforeSelectfalse  
            }else {
              this.handleClearData(false);
              this.visible = false;
            }
          }
        }

      },
      //
      handleOptionSelect(option,isEnter) {
        this.$emit('input', option.value);
        this.$emit('selected', option.value, option.item);//
        this.visible = false;
        // bug
        if(!isEnter){
          this.isClickClear=true;//
          this.focus();
        }
        this.$nextTick(() =>{
//          this.scrollToOption(option);

          this.$emit("enter");
        });

      },
      //
      isNoSelectData(val){
        if(val==undefined || val==="" || (Array.isArray(val) || val.length===0)){
          return true;
        }else {
          return false;
        }//
      },
      setInputWidth(){
        if (this.$refs.reference && this.$refs.reference.$el) {
          let temp=0;
          if(this.dropdownWidth){
            temp = this.dropdownWidth;
          }else {
            temp = this.$refs.reference.$el.getBoundingClientRect().width || this.width;
          }
          this.inputWidth = this.isWidthNumber(temp) ? `${temp}px` : temp;
        }
      },
      //
      resetSelectPos(val){
        //
        this.$nextTick(() => {
          this.resetHoverIndex();
          this.scrollToOption();
          !val && this.broadcast('ZoehisSelectDropdown', 'updatePopper')
        });
      },
      //
      isWidthNumber(val){
        var re = /^[0-9]+\.?[0-9]*$/; //
        return re.test(val);
      },
      //
      showDetail(showDetailFlag,item){
          //
          if (!showDetailFlag && item!='' && this.itemData !='' && this.itemData == item){
              return;
          }
          if(showDetailFlag){//
              this.$emit('detailShow',item);
          }
          if(item!=''){
              this.itemData = item;
          }
          this.showDetailFlag = showDetailFlag;
      }
    },
    computed:{
      isShowNone(){
        if(this.filteredOptionsCount===0){
          return true;
        }else {
          return false;
        }
      },
      //XX
      showClose(){
        let isShowClose = this.clearable &&
          this.selectedLabel!=="" &&
          this.selectedLabel!==undefined;
        return isShowClose ? true : false;
      },
      //
      selectFocus(){
        return this.inputFocus || this.dropFocus;
      },
      //  
      hasDetailData(){
        //
        let hasData =  this.showdetail && this.uiSelectDetail && (this.uiSelectDetail.indication || this.uiSelectDetail.effect || this.uiSelectDetail.attention);
        //  
        return hasData|| this.hoverDetailFlag;
      },
      //
      detailShow(){
        return this.showDetailItemName && this.showDetailFlag;
      },
      //
      showDetailPop(){
        return this.showDetailSign || this.hoverDetailSign;
      },
      //
      boxMaxWidth(){
        // 
        if(this.hasDetailData || this.detailShow || this.showDetailPop){
          return parseFloat(this.dropdownMaxWidth)+this.detailWidth;
        }else {
          return this.dropdownMaxWidth;
        }
      }
    },
    mounted(){
      //
      this.setInputWidth();
//      this.currentPlaceholder = this.placeholder;
      this.$nextTick(() => {
        this.selected = this.initSelData();
        if(this.selected){
          this.selectedLabel = this.selected.currentLabel;
        }
      });
      addResizeListener(this.$refs.selectWrap,()=>{
        //inputWidth
        this.isPercent && this.setInputWidth();
      });
    },
    beforeDestroy(){
      removeResizeListener(this.$refs.selectWrap);
    },
    watch:{
      uiSelectDetail(val){
        if(!val) return;
        this.uiShowSelectDetail = val;
      },
      //
      query(val) {
        //
        this.showDetailFlag = false;
        this.showDetailSign = false;
        //
        this.$nextTick(() => {
          if (!this.visible){
            if(!this.selectItem || (this.options.length && val!="")){// / 
              this.visible=true;
              this.selectItem=false;
            }else {
              if(val!=""){
                this.selectItem=false;
              }
            }
          }
          this.broadcast('ZoehisSelectDropdown', 'updatePopper')
        });
        //-1
        this.hoverIndex = -1;
//        this.$emit('input', "");

        if(!this.selectItem){
          this.isChangeQuery = true;
          this.$emit('input', "");
          this.$emit('update:textvalue',this.noClearBlur?this.selectedLabel:"");
        }else {
          this.isChangeQuery = false;
        }
        if (typeof this.filterMethod === 'function') {
          this.hoverIndex = -1;
          this.debounceFilterMethod(val);
        } else { //
          this.filteredOptionsCount = this.optionsCount;
          this.broadcast('ZoehisOption', 'queryChange', val);
        }
        //hoverIndex 
        this.$nextTick(()=>{
          this.resetHoverIndex(true);
        });
      },
      value(val,oldValue){
        if(val===oldValue) return;
        //value
        if(this.isNoSelectData(val)){
          this.selected="";
          if(!this.isChangeQuery){
            this.selectedLabel="";
            this.$emit('update:textvalue','');
          }else {
            this.isChangeQuery = false;
          }
          this.$emit("change",val,null);//change
        }else {
          this.setSelected();
          this.$emit('update:textvalue',this.selectedLabel);
          this.$emit("change",val,this.selected?this.selected.item:null);//change
        }

      },
      visible(val) {
        if (!val) {
          //
          this.showDetailFlag = false;
          this.showDetailSign = false;
          //
          if(!this.noClearBlur){
            this.selectedLabel = '';
          }else {
            //noClearBlurbug
            this.$emit('update:textvalue',this.selectedLabel);
          }
          if (this.selected) {
//            if (this.filterable && this.allowCreate &&
//              this.createdSelected && this.createdOption) {
//              this.selectedLabel = this.createdLabel;
//            } else {
            //
            if(typeof this.renderShowData === "function"){
              this.selectedLabel = this.renderShowData(this.selected.item);
            }else {
              this.selectedLabel = this.selected.currentLabel;
            }
//            }
          }
          if (this.filterable) {
            this.selectItem=true;
            this.query = "";
          }
        } else {
          this.filteredOptionsCount = this.optionsCount;
          if (this.filterable) {
            this.broadcast('ZoehisInput', 'inputSelect');//
          }
          //
          this.$nextTick(() => {
            this.broadcast('ZoehisOption', 'queryChange', [this.selectedLabel,true]); // 

            this.broadcast('ZoehisSelectDropdown', 'updatePopper'); //()

            this.setInputWidth();//
            this.resetHoverIndex();
            this.scrollToOption();
          });
        }
        this.$emit('visible-change', val,this);
      },
      selectdata(){
        if(!this.visible) return;//
        //api   $nextTickselectdata 
        this.$nextTick(()=>{
          this.broadcast('ZoehisSelectDropdown', 'updatePopper'); //
          // 
          this.broadcast('ZoehisOption', 'queryChange', [this.selectedLabel,true]);
          let tempArr=[];
          //optionindex
          for(let i=0, l=this.options.length; i<l; i++){
            this.options[i].index = this.options[i].itemindex;
            tempArr[this.options[i].itemindex]=this.options[i];
          }
          this.options=tempArr;
          //
          if(this.filterMethodFlag){
            this.filterMethodFlag=false;
            this.hoverIndex = this.defaultfirst ? 0 : -1;//
          }

          // 
          this.$refs.scrollbar && this.$refs.scrollbar.update();
        });
      },
      disabled(val){
        if(val){
          // disabled bug
          this.visible = false;//
        }
      }
      //width input
//      width:{
//        handler(val){
//          //
//          if(this.isWidthNumber(val)){
//            this.selectStyle = `width:${val}px;`;
//          }else {
//            this.selectStyle = `width:${val};`;
//          }
//        },
//        immediate:true
//      }
    },
    data() {
      return {
        filteredOptionsCount: 0,//
        visible:false,  //
        isSelect: true,//Selectoption
        hoverIndex: -1,//
        selectedLabel:"",  //
        query: '',//
        inputWidth: '',//
        inputFocus:false,//
        dropFocus:false,//
        curPlacement:"bottom-start",//placement
        hoverDetailFlag:false,//
        uiSelectDetail: null,//
        uiShowSelectDetail: {},//(hoverDetailFlag)
        detailWidth:420,//
        showDetailFlag:false, //
        itemData:'', //hover
        showDetailSign: false,//
      }
    },
    props: {
      clearable: {  //XX
        type: Boolean,
        default: true
      },
      placeholder: {//
        type: String,
        default:""
      },
      disabled:Boolean,// truefalse
      noselect:Boolean,// truefalse
      filterable: Boolean,// truefalse:
      value: {
        required: true
      },
      textvalue: [Number,String],
      delayTime: {    //filterMethod
        type:[String,Number],
        default:200
      },
      filterMethod: Function,//
      filterRowData: Function,//
      noMatchText: {
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.select.noMatchText');
          }else {
            return "";
          }
        },
        type:String
      },
      renderShowData:Function,
      renderItem:Function,
      scrollToBottom:Function,// 
      scrollToTop:Function,// 
      selectdata:{//
        type:Array,
        default:function(){
          return [];
        }
      },
      itemkey:{ //
        type:String,
        default(){
          return this.itemcode;
        }
      },
      itemcode:{ //
        type:String,
        default:"id"
      },
      itemtext:{//
        type:String,
        default:"text"
      },
      width:{//
        type:[Number,String],
        default:115
      },
      dropdownWidth:[Number,String],//
      dropdownMaxWidth:{// 
        type:[Number,String],
        default:500
      },
      noClearBlur:{//false
        type:Boolean,
        default:false
      },
      defaultfirst:{
        type:Boolean,
        default:true
      },
      preventOverId:{
        type:String,
        default:""
      },
      filterField:{//  code
        type:Array,
        default(){
          return [this.itemtext,this.itemcode,"spellCode","wbzxCode"];
        }
      },
      errorTip: { //  "" 
        type: String,
        default: ""
      },
      beforeSelect: Function,//
      beforeClear: Function,//
      numString:String,// 
      outclear:String,//enter hoverIndex true:
      showErrorTip:{   //true 
        type:Boolean,
        default:false
      },
      showdetail:{//
        type:Boolean,
        default:false
      },
      imeMode:{   // ime-mode 
        type:String,
        default:"disabled"
      },
      align: {    // left center right
        type: String,
        default: 'left'
      },
      showDetailItemName: {//
        type: String,
        default:''
      },
      showDetailFunc: Function,
      detailBoxHeight: {//
        type: [Number,String],
        default: 300
      },
      popperClass:String,//class
    }
  }
</script>



