<template>
  <div
    :class="[
      type === 'textarea' ? 'zoehis_textarea' : 'zoehis_input',
      {
        'zoehis_over':(mouseover || isFocus),
        'zoehis_disabled':disabled,
        'zoehis_line':isShowLine,
        'zoehis_error':errorTip!='',
      }
    ]"
    ref="reference" @click="handleClick"
    :style="textStyle" @mouseover="mouseover = true" @mouseleave="mouseover = false"
  >
    <!---->
    <transition name="zoehis_zoom_in_top" @after-leave="doDestroy">
      <zoehis-input-error v-show="showError" :placement="placement" ref="popper" :error-tip="errorTip" :prevent-over-id="preventOverId" :width="errorTipWidth"></zoehis-input-error>
    </transition>
    <i tabindex="-1" v-if="showCloseIcon" class="zoeIconfont" :class="iconClass" @click.stop="handleClear" @mousedown="handleMouseDownClear"></i>
    <i tabindex="-1" v-else-if="showIcon" class="zoeIconfont" :class="iconClass" @click.stop="handleClickIcon" @mousedown="handleMouseDownIcon"></i>
    <input
      v-if="type==='text'" class="zoehis_input_inner" type="text" :maxlength="maxlength" :ime-mode="imeMode"
      :style="{textAlign:textAlign}" ref="input" :placeholder="isShowLine ? '' : placeholder" :disabled="disabled" :readonly="readonly"
      v-on="{focus:handleFocus,blur:handleBlur}" @input="handleInput" @keyup.46="handleDelete" v-model="currentValue" :title="currentValue"/>
    <input
      v-else-if="type==='password'" class="zoehis_input_inner" type="password" ref="input" :ime-mode="imeMode"
      :placeholder="isShowLine ? '' : placeholder" :disabled="disabled" :readonly="readonly" :maxlength="maxlength"
      v-on="{focus:handleFocus,blur:handleBlur}" @input="handleInput" @keyup.46="handleDelete" v-model="currentValue"/>
    <textarea
      v-else class="zoehis_textarea_inner" type="textarea" ref="textarea" :class="{limit_length:limitLength}" :ime-mode="imeMode"
      :placeholder="disabled ? '' : placeholder" :disabled="disabled" :readonly="readonly" :maxlength="maxlength"
      v-on="{focus:handleFocus,blur:handleBlur}" @input="handleInput" @keyup.46="handleDelete" v-model="currentValue"
    ></textarea>
    <div v-if="type=='textarea' && limitLength" class="zoehis_textarea_total">
      <span class="length_now">{{currentLength}}</span><span class="length_total">/{{limitLength}}</span>
    </div>
  </div>
</template>
<script type="text/babel">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInputError from './input-error';
  import debounce from '../../../src/utils/throttle-debounce.js';
  import {addResizeListener,removeResizeListener} from '../../../src/utils/resize-event';//resize()
  export default {
    name: 'ZoehisInput',
    componentName: 'ZoehisInput',
    components: {
      ZoehisInputError
    },
    mixins: [Emitter],
    props: {
      limitLength:[Number,String],//(textarea)
      type: {    //
        type: String,
        default: "text"
      },
      value: {
        default:""
      }, //v-model
      icon: String,
      disabled: Boolean,
      readonly: Boolean,//truefalse
      placeholder: String,
      width: {      //
        type:[Number,String],
        default: 115
      },
      height: {      //height
        type:[Number,String],
        default: ""
      },
      clearable: {    //
        type: Boolean,
        default: true
      },
      clickselect: {    //
        type: Boolean,
        default: true
      },
      textAlign: {//input textAlign
        type: String,
        default: "left"
      },
      maxlength: {//input maxlength
        type: [Number,String],
        default: ""
      },
      showclose: Boolean,  //XX
      reg:RegExp,//
      checkType:String, //  "num/int/float/digit"
      decimalLength:Number,//
      digitLength:Number,//
      preventOverId:{
        type:String,
        default:""
      },
      errorTip:{
        type:String,
        default:""
      },
      positive:Boolean, // 0
      checkzero:{   //0
        type:Boolean,
        default:true
      },
      deleteKey:{   //delete
        type:Boolean,
        default:false
      },
      delayTime: {    //
        type:[String,Number],
        default:0
      },
      beforeClear: Function,//clear
      showErrorTip:{   //true 
        type:Boolean,
        default:false
      },
      imeMode:{   // ime-mode 
        type:String,
        default:"auto"
      },
      firstChange:{type:Boolean,default:false},//true changefalsechange
    },
    data(){
      return{
        currentLength:0,//,limitLength
        currentValue:"",//
        mouseover:false,//
        isFocus:false,//
        isPercent: false,//
        errorTipWidth:0,
//        showErrorTip:false,//
        placement:"top-start"
      }
    },
    computed:{
      isShowLine:function(){
        return (this.disabled && this.currentValue==="") ? true : false;
      },
      textStyle(){
        var re = /^[0-9]+\.?[0-9]*$/; //
        let obj = {
          height: re.test(this.height) ? this.height+'px' : this.height
        };
        if(re.test(this.width)){
          obj.width = this.width+'px';
          this.isPercent=false;
        }else {
          obj.width = this.width;
          this.isPercent=true;
        }
        return obj;
      },
      showIcon(){
        // 
        return (this.icon && !this.disabled);
      },
      showCloseIcon(){
        //clearabletrue 
        let isShow = (this.mouseover && this.clearable && !this.disabled && this.currentValue!=="" && this.currentValue!==null && (!this.showclose && !this.icon || this.showclose));
        return isShow;
      },
      iconClass(){
        let html = '';
        if(this.type === 'textarea'){
          html = 'zoehis_textarea_icon'
        }else {
          html = 'zoehis_input_icon ';
        }

        if(this.showCloseIcon && !this.icon){
          html += ' zoehis_close z_close_normal';
        }else if(this.icon && !this.showclose){
          html += this.icon;
        }else if(this.showclose && this.showCloseIcon){
          html += ' zoehis_close z_close_normal';
        }else if(this.icon){
          html += this.icon;
        }
        return html;
      },
      regStr(){
        if(this.reg) return this.reg;
        let reg,temp;
        let dec = this.decimalLength ? `{0,${this.decimalLength}}` : '*';
        let dig = this.digitLength ? `{0,${this.digitLength-1}}` : '*';
        let pos = this.positive ? "" : "-?";
        let zero = this.checkzero ? "[0]|" : "";
        switch (this.checkType){
          case 'num':
            temp = `^(${zero}[1-9]\\d${dig})$|^$`;// 
            reg = new RegExp(temp);
            break;
          case 'int':
            temp = `^(${zero}${pos}[1-9]\\d${dig})$|^${pos}$`;// 
            reg = new RegExp(temp);
            break;
          case 'float':
            temp = `^(${pos}([1-9][0-9]${dig})(\\.\\d${dec})?|[0](\\.\\d${dec})?)$|^${pos}$`;// 
            reg = new RegExp(temp);
            break;
          case 'digit': //
            let dig2 = this.digitLength ? `{0,${this.digitLength}}` : '*';
            temp = `^\\d${dig2}$|^$`;// 
            reg = new RegExp(temp);
            break;
          default:
        }
        return reg;
      },
      showError(){
        if(this.errorTip!="" && (this.isFocus || this.showErrorTip)){
          return true;
        }else {
          return false;
        }
      }
    },
    watch: {
      value(val){
        this.setValue(val);
      },
      showError:{
        handler(val) {
          if(val){
            this.$nextTick(() => {
              this.broadcast('ZoehisInputError', 'updatePopper')
            });
          }else {
            this.$nextTick(() => {
              this.broadcast('ZoehisInputError', 'destroyPopper');
              this.placement="top-start";//
            });
          }
        },
        immediate:true //
      }
    },
    methods: {
      //
      //placement:
      setPlacementToError(placement='top-start'){
        if(!this.showError) return;// 
        if(placement==this.placement) return;// 
        this.placement = placement;
        this.$nextTick(() => {
          this.broadcast('ZoehisInputError', 'updatePopper')
        });
      },
      setErrorTipWidth(){
        this.$nextTick(()=>{
          try {
            this.errorTipWidth = this.$refs.reference.getBoundingClientRect().width || this.width;
          }catch (e){
            this.errorTipWidth = this.width;
          }
        });
      },
      //
      doDestroy(){
        this.$refs.popper && this.$refs.popper.doDestroy();
      },
      //
      getDisabled(){
        return this.disabled;
      },
      getValue:function(){ //
        return this.currentValue;
      },
      setValue:function(val){ //
        if(val===this.currentValue) return;
        this.currentValue=val;

        this.$emit('input', val);
        this.$emit('change', val);
        // todo  
        if(this.limitLength && (""+this.limitLength)!="0"){
          this.currentLength = this.getBytesLength(val);//
        }
      },
      handleFocus(event) {//
        this.isFocus=true;
//        this.changeShowError();
        this.$emit('focus', event);
      },
      handleBlur(event) {//
        this.isFocus=false;
//        this.changeShowError();
        this.$emit('blur', event);
      },
      //showError
      changeShowError(){
        if(this.isFocus && this.errorTip!=""){
          this.showError = true;
        }else {
          this.showError = false;
        }
      },
      // 
      getBytesLength(str){
        str = ""+str;//
        let strCurL = str.length;//
        var reg = /[\u4e00-\u9fa5]/g;
        str = str.replace(reg, "");
        let strNowL = str.length;//
        return (strCurL*2 - strNowL);
      },
      //
      handleInput(event) {
        let value = event.target.value;
        const _value = event.target._value;//
        //    
        if(!this.reg && this.checkType=="float" && value){
          value = value.replace(//g,".");
        }
        let reg = this.regStr;
        if(reg && !reg.test(value)){
          value = _value;
        }
        //
        if(this.limitLength && (""+this.limitLength)!="0"){
          let curLength = this.getBytesLength(value);
          if(curLength>this.limitLength){
            value = _value;
          }else {
            this.currentLength = curLength;//
          }
        }

        this.currentValue=value;
        if(value===_value) return;
        this.debounceEmitValue(value);
//        this.$emit('input', value);
//        this.$emit('inputnative', value);
//        this.$emit('change', value);
      },
      handleClickIcon(event) {
        this.$emit('clickicon', event);
      },
      handleMouseDownIcon(event) {
        this.$emit('mousedownIcon', event);
      },
      //delete
      handleDelete(e){
        if(!this.deleteKey) return;
        this.handleClear(e);
      },
      handleMouseDownClear(event){
        this.$emit('mousedownClear', event);
      },
      //
      handleClear(event) {
        let isClear = this.clearable &&
          this.currentValue !=="" &&
          this.currentValue !==null &&
          !this.disabled;
        if(!isClear) return;
        //
        if (typeof this.beforeClear === 'function' && this.beforeClear(event)===false) {
          return;
        }
        this.$emit('input', "");
        this.$emit('change', "");
        this.$emit('clear', event);
        this.focus();
      },
      handleClick(event) { //
        this.$emit('click', event);
        //
        if(this.type == 'text' && (this.$refs.input.selectionStart == this.$refs.input.selectionEnd)){
          this.focus();
        }
      },
      //
      inputSelect() {
        this.$refs.input.select();
      },
      //
      focus() {
        this.$refs[this.type!='textarea' ? 'input' : 'textarea'].focus();
        this.clickselect && this.$refs[this.type!='textarea' ? 'input' : 'textarea'].select();
      }
    },
    created() {
      //firstChange trueinputchange change
      if(this.firstChange){
        this.currentValue=this.value;
        // todo  
        if(this.limitLength && (""+this.limitLength)!="0"){
          this.currentLength = this.getBytesLength(this.value);//
        }
      }else{
        this.setValue(this.value);
        this.currentValue=this.value;
      }
      //
      this.$on('inputSelect', this.inputSelect);
      //
      this.debounceEmitValue= debounce(this.delayTime,(value)=>{
        this.$emit('input', value);
        this.$emit('inputnative', value);
        this.$emit('change', value);
      });
    },
    mounted(){
      this.setErrorTipWidth();
      addResizeListener(this.$refs.reference,()=>{
        //inputWidth
        this.isPercent && this.setErrorTipWidth();
      });
    },
    beforeDestroy(){
      removeResizeListener(this.$refs.reference);
    },
  };
</script>



