<template>
  <div
    class="zoehis_message_box_wrapper"
    v-show="visible"
    @click.self="handleWrapperClick"

  >
    <div
      class="zoehis_message_box"
      ref="dialog"
      :class="customClass"
    >
      <div class="zoehis_message_box_header" v-if="title !== ''" @mousedown="handleMousedown">
        <span>{{title}}</span>
      </div>
      <div class="zoehis_message_box_body" :style="{height: (height-89)+'px'}">
        <div class="zoehis_message_box_body_inner">
          <i v-if="type" class="zoehis_icon zoeIconfont" :class="[iconTypeClass]"></i>
          <div class="zoehis_message_box_message">
            <div class="zoehis_message_box_info">
              <p v-html="message"></p>
            </div>
          </div>
        </div>
      </div>
      <div class="zoehis_message_box_footer" v-if="showConfirmButton || showNoButton || showCancelButton">
        <a
          class="zoehis_message_box_error_toggle"
          v-if="showErrorFlag"
          @click="errorToggleClick"
        >
          <i
            class="zoeIconfont"
            :class="errorSlideDown ? 'z_dropU_form' : 'z_dropD_form'"
          ></i>
          {{errorSlideDown ? errorSlideDownName : errorSlideUpName}}
        </a>
        <zoehis-button
          v-show="showCancelButton"
          @keydown.native="handleKeydown(2,$event)"
          :ref="btnRef.cancel"
          icon="z_cancellationOA_normal02"
          :disabled="buttonDisabled"
          @clickenter="handleAction('cancel')">{{confirmType=='1' ? cancelButtonText : noButtonText}}</zoehis-button>
        <zoehis-button
          v-show="showNoButton"
          @keydown.native="handleKeydown(1,$event)"
          :ref="btnRef.no"
          icon="z_positiveF_normal"
          :disabled="buttonDisabled"
          @clickenter="handleAction('no')">{{noButtonText}}</zoehis-button>
        <zoehis-button
          @keydown.native="handleKeydown(0,$event)"
          type="primary"
          @clickenter="handleAction('confirm')"
          v-show="showConfirmButton"
          :ref="btnRef.confirm"
          icon="z_examine_normal"
          :disabled="buttonDisabled"
        >{{(type=="question" || confirmType!='1')?yesButtonText:confirmButtonText}}</zoehis-button>
      </div>
      <div
        class="zoehis_message_box_error"
        v-if="showErrorFlag"
        v-show="errorSlideDown"
        ref="error"
      >
        <div class="zoehis_message_box_error_detail">{{errorMessage}}</div>
        <div class="zoehis_message_box_error_send" @click="sendManagerEn"><span>{{sendManagerFlag?sendManagerDefault:sendManagerActive}}</span></div>
      </div>
    </div>
  </div>
</template>

<script type="text/babel">
  import Popup from '../../../src/utils/popup/index';
  import ZoehisButton from '../../button/src/button';
  import {setStyle,on} from '../../../src/utils/dom';

  let iconTypeMap = {
    success: 'success_icon z_right_normal',
    error: 'error_icon z_wrong_normal',
    question: 'question_icon z_question_normal',
    warn: 'warn_icon z_warm_normal'
  };

  export default {
    data() {
      return {
        title:undefined,
        message: '',
        type: '',
        showConfirmButton: true,
        showNoButton: false,
        showCancelButton: true,
        yesButtonText:"",
        confirmButtonText: '',
        noButtonText: '',
        cancelButtonText: '',
        errorSlideDownName:'',
        errorSlideUpName:'',
        sendManagerDefault:'',
        sendManagerActive:'',
        sendManagerFlag:true,
        action: "",
        callback: null,
        width: 420,
        height: 235,
        customClass: '',
        errorSlideDown:true,//truefalse
        errorMessage:"",
        confirmType:1,
        isSetTimeout:false,//
        timeoutNum:1000,// 500ms
        buttonDisabled:true,//
        initTop:"",//

      };
    },
    mixins: [Popup],
    components: {
      ZoehisButton
    },
    props:{
      modal: {//
        default: true
      },
      closeOnClickModal: {//false
        default: false
      },
      closeOnPressEscape: {//escfalsetrue
        default: true
      }
    },
    computed: {
      showErrorFlag(){
        //  true
        return (this.errorMessage && this.errorMessage.replace(/\s|\xA0/g,""));
      },
      iconTypeClass() {
        return iconTypeMap[this.type];
      },
      //
      keysData(){
        return [
          {index:0,ref:this.btnRef.confirm,disabled:this.buttonDisabled,showFlag:this.showConfirmButton},
          {index:1,ref:this.btnRef.no,disabled:this.buttonDisabled,showFlag:this.showNoButton},
          {index:2,ref:this.btnRef.cancel,disabled:this.buttonDisabled,showFlag:this.showCancelButton}
        ]
      }
    },

    created(){
      let time = new Date().getTime();
      this.btnRef={
        confirm:"confirm" + time,
        no:"no" + time,
        cancel:"cancel" + time
      };
    },
    watch: {
      visible(val) {
        if(val){
          //
          this.btnTimer = setTimeout(()=>{
            this.buttonDisabled=false;
            //
            this.$nextTick(()=>{
              if(this.showConfirmButton){
                this.$refs[this.btnRef.confirm].focus();
              }else if(this.showNoButton){
                this.$refs[this.btnRef.no].focus();
              }else if(this.showCancelButton){
                this.$refs[this.btnRef.cancel].focus();
              }
            });
          },this.timeoutDisabled);
          //
          this.$nextTick(()=>{
            this.winDispatchEvent("zoehisDialogOpened");
          });
        }else {
          this.buttonDisabled=true;//
          //
          this.btnTimer && clearInterval(this.btnTimer);
          //
          this.$nextTick(()=>{
            this.winDispatchEvent("zoehisDialogClosed");
          });
        }
      }
    },

    methods: {
      //
      sendManagerEn(){
        if(!this.sendManagerFlag)return;//sendManagerFlagfalse
        if (typeof this.sendManagerEvn === 'function') {
          this.sendManagerEvn(this);
        }
      },
      // 
      setSendManagerFlag(isTrue=false){
        this.sendManagerFlag=isTrue
      },

      //window
      //name: 
      winDispatchEvent(name='zoehisDialogOpened'){
        let evenName = new Event(name);
        window.dispatchEvent(evenName);
      },
      //
      handleKeydown(index,e){
        let keyCode = e.keyCode;
        if(keyCode==9 || keyCode==37 || keyCode==39){
          e.preventDefault();
          e.stopPropagation();
          let editNum=0;
          for(let i=0; i<3; i++){
            if(!this.keysData[i].disabled && this.keysData[i].showFlag){
              editNum++;
            }
          }
          if(editNum>1){//
            let obj=null;
            if(keyCode==9 || keyCode==37){
              obj = this.getNextFocus(this.keysData,index+1);
            }else {
              obj = this.getPrevFocus(this.keysData,index-1);
            }
            this.$refs[obj.ref].focus();
          }
        }
      },
      //
      getNextFocus(arr,index){
        if(index+1>arr.length){
          index=0;
        }
        if(!arr[index].disabled && arr[index].showFlag){
          return arr[index];
        }else {
          return this.getNextFocus(arr,index+1);
        }
      },
      //
      getPrevFocus(arr,index){
        if(index<0){
          index=arr.length-1;
        }
        if(!arr[index].disabled && arr[index].showFlag){
          return arr[index];
        }else {
          return this.getNextFocus(arr,index-1);
        }
      },
      errorToggleClick(){
        this.errorSlideDown = !this.errorSlideDown;
        //dominitStyle
        this.$nextTick(function(){
          this.initStyle(true);
        });
      },
      /**
       * 
       * @param fixedLeft  true false/
       */
      initStyle(fixedLeft){      //
        let wh = window.innerHeight;//
        let ww = window.innerWidth;//
        var tempParam = {
          width:this.width+'px',
          top:(wh - (this.$refs.error ? this.$refs.error.offsetHeight : 0)-this.height)/2+'px'
        };
        //toptop
        if(this.initTop){
          tempParam.top = this.initTop;
        }
        if(!fixedLeft) tempParam.left=(ww-this.width)/2+'px';
        setStyle(this.$refs.dialog,tempParam);
      },
      handleMousedown(ev){
        var g=this;
        var _start = true; //
        var wh = window.innerHeight;//
        var ww = window.innerWidth;//
        var dlgBox = this.$refs.dialog;
        dlgBox.style.cursor="move";
        //
        var d=document;
        if(!ev) ev=window.event;
        //
        var x=ev.clientX-dlgBox.offsetLeft;
        //x=x--
        var y=ev.clientY-dlgBox.offsetTop;
        //y=y--
        d.onmousemove=function(moveEv){//
          if(!_start) return false;
          moveEv = moveEv || window.event;//
          var l=moveEv.clientX-x;
          var t=moveEv.clientY-y;
          if(l<0){
            l=0;
          }else if(l+ g.width>ww){
            l=ww- g.width;
          }
          if(t<0){
            t=0;
          }else if(t+ g.height>wh){
            t=wh- g.height;
          }
          dlgBox.style.left=l+"px";
          dlgBox.style.top=t+"px";
          if (moveEv.preventDefault) {
            moveEv.preventDefault();
          }
          return false;
        };
        d.onmouseup=function (){//
          if(!_start == true) return false;
          document.onmousemove=null;
          document.onmouseup = null;
          dlgBox.style.cursor="default";//
          _start = false; //
        };
      },
      handleWrapperClick:function(){//
        if(this.closeOnClickModal){
          this.handleAction("cancel");
        }
      },
      handleAction(action) {
        this.action = action;
        if (typeof this.beforeClose === 'function') {
          this.beforeClose(action, this, this.doClose);
        } else {
          this.doClose();
        }
      },
      doClose() {
        if (!this.visible) return;
        this.visible = false;
        this._closing = true;

//        this.onClose && this.onClose();

        this.opened = false;


        this.doAfterClose();//

        //
        if (this.action) this.callback(this.action, this);
      },
      //
      hide(){
        this.doClose();
      },
    }
  };
</script>



