<template>
  <div class="component_example">
    <h1>MessageBox </h1>
    <h2></h2>
    <div class="item" id="textId">
      <h4></h4>
      <div class="item-content">
        <zoehis-button @clickenter="myClickEnterEve">messageBox</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              myClickEnterEve:function(evt){
                this.$messageBox({
                  type:"success",
                  preventOverId:"textId",// dom ID
                  message:"!&lt;h1&gt;h1&lt;/h1&gt;"
                });
              },
            },
          </code></pre>
        </div>
        <div class="item-desc">
          this.$messageBoxVue.prototype.$messageBox = MessageBox;Vue$messageBox
          messagemessageBoxhtml<br>
          type: (success/warn/question/error,"") <br>
          preventOverId: ID body
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button @clickenter="openBtnDisabled">open</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              openBtnDisabled:function(evt){
                this.$messageBox.success("3",{timeoutDisabled:5000});
              },
            },
          </code></pre>
        </div>
        <div class="item-desc">
          timeoutDisabledms 
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button @clickenter="myClickEnterEve1">messageBox</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              myClickEnterEve1:function(evt){
                var g=this;
                this.$messageBox({
                  title:"",
                  closeOnClickModal:true,//messageBoxfalse
                  closeOnPressEscape:false,//EscmessageBox,true
                  showConfirmButton:true,//
                  showNoButton:true,// questionmessageBox
                  showCancelButton:true,//
                  confirmButtonText: '',//""
                  noButtonText: '',      //""
                  cancelButtonText: '',//""
                  type:"success",
                  message:"!"
                });
              },
            },
          </code></pre>
        </div>
        <div class="item-desc">
           <br>
          title=''
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button @clickenter="myClickEnterEve2">messageBox</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              myClickEnterEve2:function(evt){
                var g=this;
                this.$messageBox({
                  title:"",
                  message:"1..beforeClose2..callback",
                  beforeClose:function(action,thisVm,done){
                    console.log(".beforeClose")
                    done();
                  },
                  callback:function(action,thisVm){
                    console.log(".callback")
                  },
                });
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          1beforeClose:function(action,thisVm,done);MessageBox <br>
          action"confirm/no/cancel"<br>
          thisVm MessageBox <br>
          done MessageBox  <br>
          2callback:function(action,thisVm); MessageBox <br>
          action"confirm/no/cancel"<br>
          thisVm MessageBox <br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>messageBox</h4>
      <div class="item-content">
        <zoehis-button @clickenter="myClickEnterEve3" type="primary">success</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve4">error</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve5">warn</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve6">question</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve7">confirm</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve8">confirm2</zoehis-button>
        <zoehis-button @clickenter="myClickEnterEve9">timeoutPopup</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              myClickEnterEve3:function(evt){
                this.$messageBox.success("success",{
                  confirmButtonText:""
                });
              },
              myClickEnterEve4:function(evt){
                this.$messageBox.error("error");
              },
              myClickEnterEve5:function(evt){
                this.$messageBox.warn("warn");
              },
              myClickEnterEve6:function(evt){
                this.$messageBox.question("question");
              },
              myClickEnterEve7:function(evt){
                this.$messageBox.confirm("?confirm");
              },
              myClickEnterEve8:function(evt){
                this.$messageBox.confirm2("?confirm2)");
              },
              myClickEnterEve9:function(evt){
                this.$messageBox.timeoutPopup("?timeoutPopup)");
              }
            }
          </code></pre>
        </div>
        <div class="item-desc">
          this.$messageBox.success(message,options)messageBox <br>
           message: <br>
          options: messageBox
        </div>
      </div>
    </div>
    <div class="item">
      <h4>messageBoxdialog</h4>
      <div class="item-content">
        <zoehis-button @clickenter="showMsgSuccess">showMsgSuccess</zoehis-button>
        <zoehis-dialog :visible.sync="visibleDlg1" :width="width" :height="height">
          dialog1 <br>
          <zoehis-input placeholder=""></zoehis-input>
          <zoehis-button></zoehis-button>
          <div slot="footer">
            <zoehis-button @clickenter="dlgSure" type="primary"></zoehis-button>
            <zoehis-button @clickenter="visibleDlg1=false"></zoehis-button>
          </div>
        </zoehis-dialog>
        <zoehis-dialog :visible.sync="visibleDlg2"  :width="width1" :height="height1">
          dialog2 <br>
          <zoehis-input placeholder=""></zoehis-input>
          <div slot="footer">
            <zoehis-button @clickenter="visibleDlg2=false"></zoehis-button>
          </div>
        </zoehis-dialog>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button @clickenter=&quot;showMsgSuccess&quot;&gt;showMsgSuccess&lt;/zoehis-button&gt;
            &lt;zoehis-dialog :visible.sync=&quot;visibleDlg1&quot; :width=&quot;width&quot; :height=&quot;height&quot;&gt;
              dialog1 &lt;br&gt;
              &lt;zoehis-input placeholder=&quot;&quot;&gt;&lt;/zoehis-input&gt;
              &lt;zoehis-button&gt;&lt;/zoehis-button&gt;
              &lt;div slot=&quot;footer&quot;&gt;
                &lt;zoehis-button @clickenter=&quot;dlgSure&quot; type=&quot;primary&quot;&gt;&lt;/zoehis-button&gt;
                &lt;zoehis-button @clickenter=&quot;visibleDlg1=false&quot;&gt;&lt;/zoehis-button&gt;
              &lt;/div&gt;
            &lt;/zoehis-dialog&gt;
            &lt;zoehis-dialog :visible.sync=&quot;visibleDlg2&quot;  :width=&quot;width1&quot; :height=&quot;height1&quot;&gt;
              dialog2 &lt;br&gt;
              &lt;zoehis-input placeholder=&quot;&quot;&gt;&lt;/zoehis-input&gt;
              &lt;div slot=&quot;footer&quot;&gt;
                &lt;zoehis-button @clickenter=&quot;visibleDlg2=false&quot;&gt;&lt;/zoehis-button&gt;
              &lt;/div&gt;
            &lt;/zoehis-dialog&gt;

            &lt;script&gt;
            methods:{
              showMsgSuccess:function () {
                this.visibleDlg1 = true;
              },
              dlgSure:function () {
                let vThis = this;
                this.$messageBox.success(&quot;&quot;, {
                  showCancelButton: true,
                  confirmButtonText: &#x27;&#x27;,
                  beforeClose: function (action, thisVm, done) {
                    if (action == &quot;confirm&quot;) {
                      vThis.visibleDlg2 = true
                    } else {
                      done();
                    }
                  }
                });
              }
            }
            &lt;/script&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          messageBoxdialog <br>
          
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button @clickenter="showMsgError">showMsgError</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
              showMsgError:function(){
                this.$messageBox.error("",{
                  errorMessage:"",
                  sendManagerEvn:(thisVm)=>{
                    thisVm.setSendManagerFlag(false);//sendManagerFlagfalse
                  }
                });
              }
            }
          </code></pre>
        </div>
        <div class="item-desc">
          errorMessage: <br>
          errorSlideDown:truefalseerrorMessage!="" <br>
          sendManagerEvn: 1.thisVm :vm,   thisVm.setSendManagerFlag(false);
        </div>
      </div>
    </div>
    <div class="item">
      <h4> hide(),()</h4>
      <div class="item-content">
        <zoehis-button @clickenter="handleOpenDlg"></zoehis-button>
        <zoehis-button @clickenter="handleHideDlg" style="z-index: 222222222222;"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            methods:{
            handleOpenDlg:function(){
              //
              this.messageObj = this.$messageBox.success("");
            },
            handleHideDlg:function(){
              if(this.messageObj){
                this.messageObj.hide();
                this.messageObj=null;//null
              }
            },
            }
          </code></pre>
        </div>
        <div class="item-desc">
          
          <strong></strong>
        </div>
      </div>
    </div>
    <div class="item">
      <h4> closeAllMsg</h4>
      <div class="item-content">
        <zoehis-button @clickenter="handleOpenDlg2"></zoehis-button>
        <zoehis-button @clickenter="handleCloseAll" style="z-index: 222222222222;"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            //
            handleCloseAll(){
              this.$messageBox.closeAllMsg();
            },
            //
            handleOpenDlg2(){
              this.openDlgNum(1,false);
              this.$nextTick(()=>{
                this.openDlgNum(2,true);
                this.openDlgNum(3,false);
                this.openDlgNum(4,true);
              });
            },
            openDlgNum(num,enabledClose=true){
              this.$messageBox({
                message:""+num+"; enabledClose:"+enabledClose,
                enabledClose:enabledClose
              });
            },
          </code></pre>
        </div>
        <div class="item-desc">
          this.$messageBox.closeAllMsg(); <br>
          enabledClose:truecloseAllMsgfalsecloseAllMsg
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr><td>title</td><td>MessageBox </td><td>string</td><td>-</td><td></td></tr>
        <tr><td>message</td><td>MessageBox </td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>type</td><td></td><td>string</td><td>success/error/warn/question</td><td>-</td></tr>
        <tr><td>customClass</td><td>messageBoxclass</td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>showConfirmButton</td><td></td><td>boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>showNoButton</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>showCancelButton</td><td></td><td>boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>confirmButtonText</td><td></td><td>string</td><td>-</td><td></td></tr>
        <tr><td>confirmType</td><td>,"1":/  "2"/</td><td>string</td><td>1/2</td><td>1</td></tr>
        <tr><td>noButtonText</td><td></td><td>string</td><td>-</td><td></td></tr>
        <tr><td>cancelButtonText</td><td></td><td>string</td><td>-</td><td></td></tr>
        <tr><td>closeOnClickModal</td><td> MessageBox</td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>closeOnPressEscape</td><td> ESC  MessageBox</td><td>boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>preventOverId</td><td>bodyID</td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>errorMessage</td><td></td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>errorSlideDown</td><td>truefalseerrorMessage!=""</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>sendManagerDefault</td><td>sendManagerFlagtruesendManagerFlagfalse sendManagerEvn</td><td>String</td><td>-</td><td>""</td></tr>
        <tr><td>sendManagerActive</td><td>sendManagerFlagfalse(sendManagerEvn</td><td>String</td><td>-</td><td>""</td></tr>
        <tr><td>sendManagerFlag</td><td></td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>isSetTimeout</td><td>true false</td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>timeoutNum</td><td>(1000)</td><td>number</td><td>-</td><td>1000</td></tr>
        <tr><td>timeoutDisabled</td><td> (0)</td><td>number</td><td>-</td><td>0</td></tr>
        <tr><td>enabledClose</td><td>closeAllMsg(),true()</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>initTop</td><td>top</td><td>String</td><td>calc(100% - 10px)/30px/30%</td><td>-</td></tr>
        <tr>
          <td>callback</td><td>MessageBox </td><td>function1.action 'confirm/no/cancel'2. thisVm  MessageBox </td>
          <td>-</td><td>-</td>
        </tr>
        <tr>
          <td>beforeClose</td><td>MessageBox </td>
          <td>function1.action 'confirm/no/cancel'2. thisVm  MessageBox  3.done  MessageBox </td>
          <td>-</td><td>-</td>
        </tr>
        <tr>
          <td>sendManagerEvn</td><td>MessageBox  </td>
          <td>function1.. thisVm  MessageBox </td>
          <td>-</td><td>-</td>
        </tr>
        <tr>
          <td>hide</td><td> </td>
          <td>this.msgObj = this.$messageBox({});  this.msgObj.hide();</td>
          <td>-</td><td>-</td>
        </tr>
        <tr>
          <td>closeAllMsg()</td><td>(enabledClose)</td>
          <td>this.$messageBox.closeAllMsg();</td>
          <td>-</td><td>-</td>
        </tr>
        <tr><td>-</td><td>-</td><td>-</td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script type="text/babel">
  export default {
    name: 'messageBox',
    data() {
      return {
        width:650,
        height:450,
        width1:350,
        height1:200,
        visibleDlg1:false,
        visibleDlg2:false
      }
    },
    methods:{
      handleCloseAll(){
        this.$messageBox.closeAllMsg();
      },
      handleOpenDlg2(){
        this.openDlgNum(1,true);
        this.$nextTick(()=>{
          this.openDlgNum(2,true);
          this.openDlgNum(3,false);
          this.openDlgNum(4,true);
        });
      },
      openDlgNum(num,enabledClose=true){
        this.$messageBox({
          message:""+num+"; enabledClose:"+enabledClose,
          enabledClose:enabledClose
        });
      },

      handleOpenDlg:function(){
        //
        this.messageObj = this.$messageBox.success("");
      },
      handleHideDlg:function(){
        if(this.messageObj){
          this.messageObj.hide();
          this.messageObj=null;//null
        }
      },
      showMsgError:function(){
        this.$messageBox.error("",{
          errorMessage:"",
//          errorSlideDown:false//truefalse
          sendManagerEvn:(thisVm)=>{
            thisVm.setSendManagerFlag(false);
          }
        });
      },

      showMsgSuccess:function(){
        this.visibleDlg1=true;
      },
      dlgSure:function(){
        let vThis = this;
        this.$messageBox.success("",{
          showCancelButton: true,
          confirmButtonText: '',
          beforeClose:function(action,thisVm,done){
            if(action=="confirm"){
              vThis.visibleDlg2=true
            }else {
              done();
            }
          }
        });
      },
      myClickEnterEve:function(evt){
        var g=this;
        this.$messageBox({
          type:"success",
          preventOverId:"textId",
          message:"!<h1>h1</h1>",
          customClass:"asdkjhaksd asdasd"
        });
      },
      openBtnDisabled:function(evt){
        var g=this;
        this.$messageBox.success("3",{timeoutDisabled:5000});
      },

      myClickEnterEve1:function(evt){
        var g=this;
        this.$messageBox({
          title:"",
          closeOnClickModal:true,//messageBoxfalse
          closeOnPressEscape:false,//EscmessageBox,true
          showConfirmButton:true,//
          showNoButton:true,// questionmessageBox
          showCancelButton:true,//
          confirmButtonText: '',//
          noButtonText: '',      //
          cancelButtonText: '',//
          type:"success",
          message:"!"
        });
      },
      myClickEnterEve2:function(evt){
        var g=this;
        this.$messageBox({
          title:"",
          message:"1..beforeClose2..callback",
          beforeClose:function(action,thisVm,done){
            console.log(".beforeClose")
            done();
          },
          callback:function(action,thisVm){
            console.log(".callback")
          }
        });
      },
      myClickEnterEve3:function(evt){
        this.$messageBox.success("success",{
          confirmButtonText:""
        });
      },
      myClickEnterEve4:function(evt){
        this.$messageBox.error("error");
      },
      myClickEnterEve5:function(evt){
        this.$messageBox.warn("warn");
      },
      myClickEnterEve6:function(evt){
        this.$messageBox.question("question");
      },
      myClickEnterEve7:function(evt){
        this.$messageBox.confirm("?confirm");
      },
      myClickEnterEve8:function(evt){
        this.$messageBox.confirm2("?confirm2");
      },
      myClickEnterEve9:function(evt){
        this.$messageBox.timeoutPopup("?timeoutPopup)");
      }

    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

