import {on} from './dom.js'

/**
 * 
 * @param nextVm  
 * @param vnode   
 */
const focusToVm = function (vnode,nextVm) {
  let contentVm = vnode.context.$refs[nextVm.ref];
  if(_comKeyType=="0"){// focus
    contentVm.focus();
  }else {
    contentVm[0].focus();// v-for 
  }
};

/**
 * binding keyRefs
 * @param binding   
 * @param vnode
 * @returns {Array}
 */
const getKeyRefs = function(binding,vnode){
  const keyFocusObj = binding.value;//
  let prefix = keyFocusObj.prefix || "keyRef"; //  keyRef
  let total = keyFocusObj.total || 0; // 0
  let keyRefs = [];
  if(keyFocusObj.keyRefs && keyFocusObj.keyRefs.length){
    keyRefs = keyFocusObj.keyRefs;
  }else {
    for(let i=0; i<total; i++){
      keyRefs.push({ref: prefix+i});
    }
  }
  return keyRefs;
};

/**
 * vm
 * @param el dom
 * @param current 
 * @param vnode 
 * @param isNext  'next' (next/prev)
 * @return {obj} vm
 */
const findTheVm = function(el,current,vnode,isNext='next'){
  let keyRefs = JSON.parse(el.getAttribute("keyRefs"));
  //keyArr
  if(!keyRefs || !keyRefs.length) return null;
  let theVm = null;//vm
  //current-1/,array[0]/array[keyL-1]
  //if(current == -1) return keyRefs[isNext === 'next' ? 0 : keyL-1];

  //vm ()
  let curVm = findRefByIndex(keyRefs,current);
  // null;
  if(curVm === null) return theVm;

  //next/prev/
  if(keyRefs[0][isNext] === undefined){ //
    return nextOrPrevToRef(keyRefs,curVm,vnode,isNext);
  }else {
    return findRefByIndex(keyRefs,curVm[isNext],vnode,isNext);
  }
};

/**
 * isNextcurrentkeyIndexvm
 * isNext"next/prev"currentkeyIndexvm
 * @param keyRefs  ref
 * @param current  
 * @param vnode   
 * @param isNext  (next/prev/undefined())for
 * @returns {*}
 */
const findRefByIndex = function(keyRefs,current,vnode,isNext){
  let keyL = keyRefs.length;
  let curVm = null;
  for(let j=0; j<keyL; j++){
    if(keyRefs[j].ref == current){
      curVm = keyRefs[j];
      //isNext'next/prev'  getDisabled truevm
      if(isNext && getDisabledStatus(vnode,curVm.ref)){
        curVm = findRefByIndex(keyRefs,keyRefs[j][isNext],vnode,isNext);
      }
      break;
    }
  }
  return curVm;
};

/**
 * 
 * @param keyRefs ref
 * @param curVm  vm
 * @param vnode 
 * @param isNext  (next/prev)
 * @returns {*}
 */
const nextOrPrevToRef = function(keyRefs,curVm,vnode,isNext){
  let arrIndex = keyRefs.indexOf(curVm);
  let keyL = keyRefs.length;
  let theVm = null;
  if(isNext==="next"){
    theVm = arrIndex+1 < keyL ? keyRefs[arrIndex+1] : keyRefs[0];
  }else {
    theVm = arrIndex == 0 ? keyRefs[keyL-1] : keyRefs[arrIndex-1];
  }
  if(getDisabledStatus(vnode,theVm.ref)){
    theVm = nextOrPrevToRef(keyRefs,theVm,vnode,isNext);
  }
  return theVm;
};

/**
 * 
 * return true  return false 
 * @param vnode     
 * @param refName   ref
 * @returns {boolean}
 */
const getDisabledStatus = function(vnode,refName){
  let curRef = vnode.context.$refs[refName];
  if(!curRef) return true;//
  let disStatus = null;
  if(_comKeyType=="0"){
    disStatus=curRef.getDisabled;
  }else {
    disStatus=curRef[0] ? curRef[0].getDisabled : false;
  }
  return (disStatus && disStatus()===true) ? true : false;
};
//
const tabEnterFn = function(el,vnode,isNext,fn){
  var current = el.dataset.current; //ref
  let keyRefs = JSON.parse(el.getAttribute("keyRefs"));
  //vm ()
  let curVm = findRefByIndex(keyRefs,current);
  // null;
  if(curVm === null) return null;
  let theVm = null;
  //next/prev/
  if(keyRefs[0][isNext] === undefined){ //
    theVm = nextOrPrevToRef(keyRefs,curVm,vnode,isNext);
  }else {
    theVm = findRefByIndex(keyRefs,curVm[isNext],vnode,isNext);
  }
  if($.isFunction(fn)){
    let curVm = getNextVmByRef(keyRefs,current);
    fn(current,theVm,vnode, curVm);
  }
};

//refName 
// null
const getNextVmByRef = function(keyRefs,curRef){
  if(!keyRefs.length || !curRef) return null;
  for(let i=0,l=keyRefs.length; i<l; i++){
    if(curRef==keyRefs[i].ref){
      return Object.assign({},keyRefs[i]);
    }
  }
  return null;
};

//"0"   "1":forItem
var _comKeyType = "0";
export default {
  bind(el,binding,vnode){
    _comKeyType = el.dataset.type||"0"; //ref
    el.setAttribute('tabindex',-1);//tabindex-1
    let keyRefs = getKeyRefs(binding,vnode);//keyRefs
    //keyRefs keyrefs
    el.setAttribute('keyrefs',JSON.stringify(keyRefs));
    //el
    const onEvn = vnode.data.attrs;

    let onBeforeNext,onAfterNext,onBeforePrev,onAfterPrev;

    if(onEvn){
      onBeforeNext = onEvn['before-next'];//
      onAfterNext = onEvn['after-next'];//
      onBeforePrev = onEvn['before-prev'];//
      onAfterPrev = onEvn['after-prev'];//
    }
    on(el,'keydown',function(e){
      // tab/enter
      if(e.keyCode==9 || e.keyCode==13){
        e.stopPropagation();
        e.preventDefault();
      }
    });
    on(el,'keyup',function(e){
      var keyCode = e.keyCode;
      //bug
      if(keyCode==9 || keyCode==13 || keyCode == 38 || keyCode == 40 || e.ctrlKey && (keyCode == 9 || keyCode == 13)){
        e.stopPropagation();
      }
      // ctrl+tab / ctrl+enter
      if(e.ctrlKey && (keyCode == 9 || keyCode == 13)){

        tabEnterFn(el,vnode,'prev',function(current,theVm,vnode,curVm){
          // false 
          if($.isFunction(onBeforePrev) && onBeforePrev(current,theVm,e,curVm)===false) return;
          if(theVm==null) return; //
          //
          focusToVm(vnode,theVm);
          //
          $.isFunction(onAfterPrev) && onAfterPrev(theVm,e);
        });
      }else if(keyCode == 9 || keyCode == 13){//tabenter
        tabEnterFn(el,vnode,'next',function(current,theVm,vnode,curVm){
          // false 
          if($.isFunction(onBeforeNext) && onBeforeNext(current,theVm,e,curVm)===false) return;
          if(theVm==null) return; //
          //
          focusToVm(vnode,theVm);
          //
          $.isFunction(onAfterNext) && onAfterNext(theVm,e);
        });
      }
    });
  },
  update(el,binding,vnode){
    if(binding.value == binding.oldValue) return;
    let keyRefs = getKeyRefs(binding,vnode);//keyRefs
    //keyRefs keyrefs
    el.setAttribute('keyrefs',JSON.stringify(keyRefs));
  },
  unbind(el){
    //
    on(el,'keydown');
    on(el,'keyup');
  }
}



