<template>
  <div class="component_example">
    <h1>Func </h1>
    <p>excel tableToExcel</p>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-func ref="zoehisFunc"></zoehis-func>
        <zoehis-button @clickenter="handleDownloadEn"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li><strong>tableToExcel(excelData,fileName,worksheet,tableConfig)</strong>:excel</li>
              <li>1.excelData():</li>
              <li>2.fileName():</li>
              <li>3.worksheet()tab</li>
              <li>4.tableConfig():()</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-func ref=&quot;zoehisFunc&quot; &gt;&lt;/zoehis-func&gt;
            &lt;script&gt;
            /**
            * 
            */
            handleDownloadEn(){
              /** @param {Array} excelData: 
                * @param {String} fileName: excel
                * @param {String} worksheet: exceltab
                * @param {Array} tableConfig: 
                **/ 
              //
              let excelData = [];
              for(let i=0;i&lt;1000; i++){
                excelData.push({
                  text1:i+&quot;1&quot;,
                  text2:i+&quot;2&quot;,
                  text3:i+&quot;3&quot;,
                  text4:i+&quot;4&quot;,
                  text5:i+&quot;5&quot;,
                });
              }
              let fileName = &quot;&quot;;
              let worksheet = &quot;exceltab&quot;;
              //
              let tableConfig = [
                { dataItemCode: &quot;text1&quot;, dataItemName: &quot;1&quot;},
                { dataItemCode: &quot;text2&quot;, dataItemName: &quot;2&quot;},
                { dataItemCode: &quot;text3&quot;, dataItemName: &quot;3&quot;},
                { dataItemCode: &quot;text4&quot;, dataItemName: &quot;4&quot;},
                { dataItemCode: &quot;text5&quot;, dataItemName: &quot;5&quot;},
              ];
              this.$refs.zoehisFunc.tableToExcel(excelData,fileName,worksheet,tableConfig);
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr>
            <td>tableToExcel(excelData,fileName,worksheet,tableConfig)</td>
            <td>excel</td>
            <td>Function</td>
            <td>1.excelData():2.fileName():3.worksheet()tab4.tableConfig():</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'Func',
    data () {
      return {
    
      }
    },
    methods:{
      /**
       * 
       */
      handleDownloadEn(){
        /** @param {Array} excelData: 
          * @param {String} fileName: excel
          * @param {String} worksheet: exceltab
          * @param {Array} tableConfig: 
          **/ 
        //
        let excelData = [];
        for(let i=0;i<1000; i++){
          excelData.push({
            text1:i+"1",
            text2:i+"2",
            text3:i+"3",
            text4:i+"4",
            text5:i+"5",
          });
        }
        let fileName = "";
        let worksheet = "exceltab";
        //
        let tableConfig = [
          { dataItemCode: "text1", dataItemName: "1"},
          { dataItemCode: "text2", dataItemName: "2"},
          { dataItemCode: "text3", dataItemName: "3"},
          { dataItemCode: "text4", dataItemName: "4"},
          { dataItemCode: "text5", dataItemName: "5"}
        ];
        this.$refs.zoehisFunc.tableToExcel(excelData,fileName,worksheet,tableConfig);
      },

      handleToggle(e) {
        $(e.target)
          .next()
          .find("pre code")
          .toggle();
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style scoped>
.component_example .zoehis_table {
  height: 300px;
}
ul {
  margin: 0 0 0 20px;
  padding: 0;
}
.component_example .clearflex {
  display: block;
}
.component_example .item-toggle {
  cursor: pointer;
  background: #8fcae5;
  text-align: center;
  font-weight: bold;
}
.component_example .item-desc {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code pre code {
  display: none;
}
</style>



