<template>
  <div class="zoehis_dialog_wrapper" v-show="visible" @click.self="handleWrapperClick">
    <div
      class="zoehis_dialog"
      ref="dialog"
    >
      <div
        class="zoehis_dialog_header"
        v-if="title"
        @mousedown="handleMousedown('header',$event)"
      >
        <span :title="title" class="titleSpan">{{title}}</span>
        <i class="zoeIconfont zoehis_header_close z_close_normal" v-if="headerclose" @click="handleHeaderClose"></i>
      </div>
      <template v-if="newscrollbar">
        <div
          class="zoehis_dialog_body"
          v-if="rendered" :style="initBodyH"
          @mousedown="handleMousedown('body',$event)"
        >
          <template v-if="bodyscroll">
            <zoehis-scrollbar class="zoehis_dialog_scroll" ref="scrollbar" wrap-class="zoehis_dialog_scroll_wrap">
              <slot></slot>
            </zoehis-scrollbar>
          </template>
          <template v-else>
            <slot></slot>
          </template>
        </div>
      </template>
      <zoehis-scrollbar v-else ref="scrollbar"  wrap-class="zoehis_dialog_scroll_wrap">
        <div
          class="zoehis_dialog_body"
          v-if="rendered"
          :style="initBodyH"
          @mousedown="handleMousedown('body',$event)"
        ><slot></slot></div>
      </zoehis-scrollbar>

      <div class="zoehis_dialog_footer" v-if="$slots.footer">
        <a
          class="zoehis_dialog_error_toggle"
          v-if="$slots.error"
          @click="errorToggleClick"
        >
          <i
            class="zoeIconfont"
            :class="errorSlideDown ? 'z_dropU_form' : 'z_dropD_form'"
          ></i>
          {{errorSlideDown ? errorSlideDownName : errorSlideUpName}}
        </a>
        <slot name="footer"></slot>
      </div>
      <div
        class="zoehis_dialog_error"
        v-if="$slots.error"
        v-show="errorSlideDown"
        ref="error"
      >
        <div class="zoehis_dialog_error_detail"><slot name="error"></slot></div>
        <div class="zoehis_dialog_error_send"><span>{{i18nAttr.sendManager}}</span></div>
      </div>
    </div>
  </div>
</template>
<script type="text/babel">
  import Popup from '../../../src/utils/popup/index';
  import {setStyle} from '../../../src/utils/dom';
  import { addResizeListener, removeResizeListener } from '../../../src/utils/resize-event';
  import ZoehisScrollbar from '../../scrollbar/index.js'
  export default {
    name: 'ZoehisDialog',
    componentName: 'ZoehisDialog',
    mixins: [Popup],
    components:{ZoehisScrollbar},
    data(){
      return {
        errorSlideDown:false,//truefalse
        dHeight:0,
        eHeight:50,//
        i18nAttr:{
          sendManager:""
        }
      }
    },
    props:{
      title: { // ""
        type: String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.dialog.title');
          }else {
            return "";
          }

        }
      },
      modal: {//
        type: Boolean,
        default: true
      },
      modalAppendToBody: {////body
        type: Boolean,
        default: false
      },
      width:{
        type:[Number,String],
        default:420
      },
      height:{
        type:[Number,String],
        default:235
      },
      closeOnClickModal: {//false
        type: Boolean,
        default: false
      },
      closeOnPressEscape: {//escfalsetrue
        type: Boolean,
        default: true
      },
      beforeClose: Function, //
      errorSlideDownName: {
        type:String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.dialog.errorSlideDownName');
          }else {
            return "";
          }
        }
      },
      errorSlideUpName: {
        type:String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.dialog.errorSlideUpName');
          }else {
            return "";
          }
        }
      },
      headerclose:Boolean,
      supportdrag:{//
        type:Boolean,
        default:true
      },
      newscrollbar:{//
        type:Boolean,
        default:false
      },
      bodyscroll:{//
        type:Boolean,
        default:false
      },
      //body
      appendToBody: {
        type: Boolean,
        default: false
      },

    },
    methods:{
      //
      scrollMove(left=0,top=0){
        this.$refs.scrollbar.move({
          left:left,top:top
        });
      },
      handleHeaderClose(){
        this.$emit("headerclose");
      },
      /**
       * 
       * @param fixedLeft  true false/
       */
      initStyle(fixedLeft){      //
        let wh = window.innerHeight;//
        let ww = window.innerWidth;//
        var tempParam = {
          width:this.width+'px',
          top:(wh-this.dHeight)/2+'px'
        };
        if(!fixedLeft) tempParam.left=(ww-parseInt(this.width))/2+'px';
        setStyle(this.$refs.dialog,tempParam);
      },
      errorToggleClick(){
        const errorSlide = !this.errorSlideDown;
        this.toggleErrorDetail(errorSlide);
      },
      //
      toggleErrorDetail(val){
        //val
        if(val===undefined){
          this.errorSlideDown = !this.errorSlideDown;
        }else if(this.errorSlideDown === val){//valerrorSlideDown 
          return;
        }else {
          this.errorSlideDown = val;
        }
        this.$nextTick(function(){
          if(this.errorSlideDown){
            this.eHeight = this.$refs.error.clientHeight;
            this.dHeight = this.dHeight+this.eHeight;
          }else {
            this.dHeight = this.dHeight-this.eHeight;
          }
          this.initStyle(true);
        });
      },
      handleWrapperClick:function(){//
        if(!this.closeOnClickModal) return;
        this.handleClose();
      },
      // beforeClose 
      handleClose() {
        if (typeof this.beforeClose === 'function') {
          this.beforeClose(this.hide);
        } else {
          this.hide();
        }
      },
      hide(cancel) {
        if (cancel !== false) {
          this.$emit('update:visible', false);
        }
      },
      //
      show() {
        this.$emit('update:visible', true);
      },
      handleMousedown(type,ev){
        if(type=="body" && this.title!="") return;//
        if(!this.supportdrag) return;
        var g=this;
        var _start = true; //
        var wh = window.innerHeight;//
        var ww = window.innerWidth;//
        var dlgBox = this.$refs.dialog;
        dlgBox.style.cursor="move";
        //
        var d=document;
        if(!ev) ev=window.event;
        //
        var x=ev.clientX-dlgBox.offsetLeft;//x=x-
        var y=ev.clientY-dlgBox.offsetTop;//y=y-
        d.onmousemove=function(moveEv){//
          if(!_start) return false;
          moveEv = moveEv || window.event;//
          var l=moveEv.clientX-x;
          var t=moveEv.clientY-y;
          if(l<0){
            l=0;
          }else if(l+ parseInt(g.width)>ww){
            l=ww- parseInt(g.width);
          }
          if(t<0){
            t=0;
          }else if(t+ g.dHeight>wh){
            t=wh- g.dHeight;
          }
          dlgBox.style.left=l+"px";
          dlgBox.style.top=t+"px";
          if (moveEv.preventDefault) {
            moveEv.preventDefault();
          }
          return false;
        };
        d.onmouseup=function (){//
          if(!_start == true) return false;
          document.onmousemove=null;
          document.onmouseup = null;
          dlgBox.style.cursor="default";//
          _start = false; //
        };
      },
      //window
      //name: 
      winDispatchEvent(name='zoehisDialogOpened'){
        let evenName = new Event(name);
        window.dispatchEvent(evenName);
      }
    },
    computed:{
      initBodyH(){
        var bodyH = this.height-(this.title=="" ? 0 : 38)-(this.$slots.footer ? 51 : 0);
        return "height:"+bodyH+"px;"
      }
    },
    watch:{
      visible(val){
        this.$emit('update:visible', val);
        if (val) {
          this.$emit('open');
          if (this.appendToBody) {
            document.body.appendChild(this.$el);
          }
          //
          this.$nextTick(()=>{
            this.winDispatchEvent("zoehisDialogOpened");
          });
        } else {
          this.$emit('close');
          //
          this.$nextTick(()=>{
            this.winDispatchEvent("zoehisDialogClosed");
          });
        }
      },
      width(val){
        this.initStyle();
      },
      height(val){
        this.dHeight=parseInt(val);
        this.initStyle();
      }
    },
    created(){
      //i18n   $t 
      if(this.$t && (typeof this.$t == "function")){
        this.i18nAttr={
          sendManager:this.$t('zoehisUI.dialog.sendManager')
        }
      }
      this.dHeight=parseInt(this.height);
    },
    mounted() {
      const vThis = this;
      if (vThis.visible) {
        vThis.rendered = true;//
        vThis.open();
        if (this.appendToBody) {
          document.body.appendChild(this.$el);
        }
      }
      vThis.initStyle();
      addResizeListener(this.$el,()=>{
        this.initStyle();
    });
    },
    beforeDestroy() {
      if (this.$el && this.initStyle) removeResizeListener(this.$el, this.initStyle);
    },
    destroyed() {
      if (this.appendToBody && this.$el && this.$el.parentNode) {
        this.$el.parentNode.removeChild(this.$el);
      }
    }
  };
</script>



