<template>
  <div class="component_example">
    <h1>Button </h1>
    <h2></h2>
    <h4>30px</h4>
    <h4>26px</h4>
    <div class="item">
      <h4></h4>
      <div class="item-content" id="testtest">
        <zoehis-button icon="z_delete_normal"></zoehis-button>

        <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button>
        <zoehis-button icon="z_delete_normal"></zoehis-button><zoehis-button icon="z_delete_normal" type="primary"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>type2default/primary/</li>
              <li>icon</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;&lt;zoehis-button icon=&quot;z_delete_normal&quot; type=&quot;primary&quot;&gt;&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" :disabled="false" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button type="primary" :disabled="true" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button :disabled="false" icon="z_delete_normal"></zoehis-button>
        <zoehis-button  icon="z_delete_normal"type="primary" :disabled="true"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>disabled2true/false/</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; :disabled=&quot;false&quot; icon=&quot;z_delete_normal&quot;&gt;()&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; :disabled=&quot;true&quot; icon=&quot;z_delete_normal&quot;&gt;()&lt;/zoehis-button&gt;
               &lt;zoehis-button :disabled=&quot;false&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
               &lt;zoehis-button  icon=&quot;z_delete_normal&quot;type=&quot;primary&quot; :disabled=&quot;true&quot;&gt;&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button><zoehis-button type="primary" size="small"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>size2normal/small/</li>
              <li> normal</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; size=&quot;small&quot;&gt;&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" keyboard="0">()</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>keyboardString</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
              &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot; keyboard=&quot;0&quot;&gt;()&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>/enter</h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" @clickenter="myClickEnterEve"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>@clickenter="myClickEnterEve",/enterenter</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot; @clickenter=&quot;myClickEnterEve&quot;&gt;&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
               methods:{
                 myClickEnterEve:function(evt){
                   alert(&quot;/enter&quot;);
                 },
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button-group>
          <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button><zoehis-button icon="z_delete_normal" type="default"></zoehis-button>
        </zoehis-button-group>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>zoehis-button-group </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button-group&gt;
                 &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;&lt;zoehis-button icon=&quot;z_delete_normal&quot; type=&quot;default&quot;&gt;&lt;/zoehis-button&gt;
               &lt;/zoehis-button-group&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" :keyboard="keyboard" icon="z_delete_normal">{{featuresName}}</zoehis-button>
        <zoehis-button type="default" icon="z_delete_normal">{{defaultName}}</zoehis-button>
        <zoehis-button icon="z_delete_normal" @clickenter="changeBtnCt" type="primary"></zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>keyboard defaultNamefeaturesName</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">/</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; :keyboard=&quot;keyboard&quot; icon=&quot;z_delete_normal&quot;&gt;{{featuresName}}&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;default&quot; icon=&quot;z_delete_normal&quot;&gt;{{defaultName}}&lt;/zoehis-button&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot; @clickenter=&quot;changeBtnCt&quot; type=&quot;primary&quot;&gt;&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {
                   featuresName:&quot;&quot;,
                   defaultName:&quot;&quot;,
                   keyboard:&quot;2&quot;
                 }
               },
               methods:{
                 changeBtnCt:function(evt){
                   this.defaultName=&quot;&quot;;
                   this.featuresName=&quot;&quot;;
                   ++this.keyboard;
                 },
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <zoehis-table keycode="code" :data="propData" :configs="demoConfig1"></zoehis-table>
      <h4></h4>
      <zoehis-table keycode="code" :data="functionData" :configs="demoConfig1"></zoehis-table>
      <h4></h4>
      <zoehis-table keycode="code" :data="eventData" :configs="demoConfig1"></zoehis-table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Button',
    data() {
      return {
        featuresName:"",
        defaultName:"",
        keyboard:"2",
        propData:[], //
        functionData:[], //
        eventData:[], //
        demoConfig1:[],
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="";
        this.featuresName="";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("/enter");
      },
      handleToggle(e){
        $(e.target).next().find("pre code").toggle()
      },
    },
    mounted:function() { //
      this.demoConfig1=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"explain",dataItemName:"",filterType:"",itemWidth:"340px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"type",dataItemName:"",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"optional",dataItemName:"",filterType:"",itemWidth:"180px",fixedFlag:""},
        {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"defaultVal",dataItemName:"",filterType:"",itemWidth:"",fixedFlag:""},
      ];
      this.propData=[
        {code:1,parameter:"type", explain:"", type:"string",optional:"default/primary/",defaultVal:"default"},
        {code:2,parameter:"icon", explain:"", type:"string",optional:"-",defaultVal:"-"},
        {code:3,parameter:"disabled", explain:"", type:"Boolean",optional:"true/false",defaultVal:"false"},
        {code:4,parameter:"size", explain:"", type:"string",optional:"'normal'/'small'",defaultVal:"'normal'"},
        {code:5,parameter:"keyboard", explain:"", type:"string",optional:"-",defaultVal:"-"}
      ];
      this.functionData=[
        {code:1,parameter:"focus()", explain:"", type:"	",optional:"-",defaultVal:"-"}
      ];
      this.eventData=[
        {code:1,parameter:"@clickenter", explain:"/enter", type:"	1.e",optional:"-",defaultVal:"-"}
      ];
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul{margin: 0 0 0 20px; padding: 0;}
  .component_example .clearflex{display: block;}
  .component_example .item-toggle{cursor: pointer; background: #8fcae5; text-align: center; font-weight: bold;}
  .component_example .item-code{ width: 100%;max-width: 100%;}
  .component_example .item-code pre code{display: none;}
</style>



