<template>
  <div>
    <div class="component_example">
      <h1>v-keyfocus </h1>
      <h2></h2>
      <div class="item">
        <h4></h4>
        <div class="item-content" v-keyfocus="keyfocusbox" :data-current="current">
          <zoehis-time-picker ref="keyfocus0" v-model="time2v" @focus="current='keyfocus0'"></zoehis-time-picker>
          <zoehis-button ref="keyfocus1" @focus="current='keyfocus1'"></zoehis-button>
          <zoehis-checkbox v-model="checked" ref="keyfocus2" @focus="current='keyfocus2'"></zoehis-checkbox>
          <zoehis-input v-model="input1" ref="keyfocus3" @focus="current='keyfocus3'" placeholder=""></zoehis-input>
          <zoehis-input disabled type="textarea" v-model="textarea1" ref="keyfocus4" @focus="current='keyfocus4'" placeholder=""></zoehis-input>
          <zoehis-select v-model="select1V" :selectdata="select1data" ref="keyfocus5" @focus="current='keyfocus5'"></zoehis-select>
          <zoehis-time-picker ref="keyfocus6" v-model="time1v" @focus="current='keyfocus6'"></zoehis-time-picker>
          <zoehis-date-editor
            @tab-end="focus('keyfocus8')" @save="focus('keyfocus8')"
            ref="keyfocus7" v-model="time1v" @focus="current='keyfocus7'"></zoehis-date-editor>
          <input type="text" @focus="current='keyfocus8'" ref="keyfocus8" placeholder="">
          <textarea @focus="current='keyfocus9'" ref="keyfocus9" placeholder=""></textarea>
        </div>
        <div class="item-detail">
      <div class="item-code"><pre><code>
        &lt;div class=&quot;item-content&quot; v-keyfocus=&quot;keyfocusbox&quot; :data-current=&quot;current&quot;&gt;
          &lt;zoehis-time-picker ref=&quot;keyfocus0&quot; v-model=&quot;time2v&quot; @focus=&quot;current=&#x27;keyfocus0&#x27;&quot;&gt;&lt;/zoehis-time-picker&gt;
          &lt;zoehis-button ref=&quot;keyfocus1&quot; @focus=&quot;current=&#x27;keyfocus1&#x27;&quot;&gt;&lt;/zoehis-button&gt;
          &lt;zoehis-checkbox v-model=&quot;checked&quot; ref=&quot;keyfocus2&quot; @focus=&quot;current=&#x27;keyfocus2&#x27;&quot;&gt;&lt;/zoehis-checkbox&gt;
          &lt;zoehis-input v-model=&quot;input1&quot; ref=&quot;keyfocus3&quot; @focus=&quot;current=&#x27;keyfocus3&#x27;&quot; placeholder=&quot;&quot;&gt;&lt;/zoehis-input&gt;
          &lt;zoehis-input disabled type=&quot;textarea&quot; v-model=&quot;textarea1&quot; ref=&quot;keyfocus4&quot; @focus=&quot;current=&#x27;keyfocus4&#x27;&quot; placeholder=&quot;&quot;&gt;&lt;/zoehis-input&gt;
          &lt;zoehis-select v-model=&quot;select1V&quot; :selectdata=&quot;select1data&quot; ref=&quot;keyfocus5&quot; @focus=&quot;current=&#x27;keyfocus5&#x27;&quot;&gt;&lt;/zoehis-select&gt;
          &lt;zoehis-time-picker ref=&quot;keyfocus6&quot; v-model=&quot;time1v&quot; @focus=&quot;current=&#x27;keyfocus6&#x27;&quot;&gt;&lt;/zoehis-time-picker&gt;
          &lt;input type=&quot;text&quot; @focus=&quot;current=&#x27;keyfocus7&#x27;&quot; ref=&quot;keyfocus7&quot; placeholder=&quot;&quot;&gt;
          &lt;textarea @focus=&quot;current=&#x27;keyfocus8&#x27;&quot; ref=&quot;keyfocus8&quot; placeholder=&quot;&quot;&gt;&lt;/textarea&gt;
        &lt;/div&gt;
        &lt;script&gt;
          data: function () {
            return {
              current:"",//
              keyfocusbox:{//v-keyfocus Object   keyRefs
                prefix:"keyfocus", //ref             keyRefslength0keyRefs
                total:9           //ref
              },
            }
          }
        &lt;/script&gt;
      </code></pre></div>
          <div class="item-desc">
            v-keyfocus <br>
             :data-current <br>
            v-keyfocus Object
            <pre><code>
              {
                keyRefs:[{
                  next:'selectRef1',
                  prev:'selectRef2',
                  ref:"selectRef"
                }],
                prefix: "keyfocus",
                total: 9
              }
            </code></pre>
            total: ref prefix keyRefskeyRefskeyRefs.length==0 <br>
            prefix: ref <br>
            keyRefs:[{ <br>
              next:'selectRef1', //nextref<br>
              prev:'selectRef2', //prevref<br>
              ref:"selectRef" // refref<br>
            }] <br>
            @focuscurrentkeyfocusgetDisabled()
          </div>
        </div>
      </div>
      <div class="item">
        <h4></h4>
        <div class="item-content">
          <div style="border: 1px solid black; padding: 5px; margin-bottom: 10px;" v-keyfocus="keyfocusbox2" :data-current="current1"
               :before-next="onBeforeNext"
               :after-next="onAfterNext"
               :before-prev="onBeforePrev"
               :after-prev="onAfterPrev"
          >
            <h4></h4>
            <zoehis-button ref="keyfocus10" @focus="current1='keyfocus10'"></zoehis-button>
            <zoehis-input type="textarea" v-model="textarea11" ref="keyfocus11" @focus="current1='keyfocus11'" placeholder=""></zoehis-input>
            <zoehis-select v-model="select1V" :selectdata="select1data" ref="keyfocus12" @focus="current1='keyfocus12'"></zoehis-select>
          </div>
          <div style="border:1px solid red; padding: 5px;">
            <h4></h4>
            <zoehis-input v-model="input2v" ref="tb2Input"></zoehis-input>
          </div>
        </div>
        <div class="item-detail">
      <div class="item-code"><pre><code>
        &lt;div v-keyfocus=&quot;keyfocusbox2&quot; :data-current=&quot;current1&quot;
          :before-next=&quot;onBeforeNext&quot;
          :after-next=&quot;onAfterNext&quot;
          :before-prev=&quot;onBeforePrev&quot;
          :after-prev=&quot;onAfterPrev&quot;
        &gt;
          &lt;h4&gt;&lt;/h4&gt;
          &lt;zoehis-button ref=&quot;keyfocus10&quot; @focus=&quot;current1=&#x27;keyfocus10&#x27;&quot;&gt;&lt;/zoehis-button&gt;
          &lt;zoehis-input type=&quot;textarea&quot; v-model=&quot;textarea11&quot; ref=&quot;keyfocus11&quot; @focus=&quot;current1=&#x27;keyfocus11&#x27;&quot; placeholder=&quot;&quot;&gt;&lt;/zoehis-input&gt;
          &lt;zoehis-select v-model=&quot;select1V&quot; :selectdata=&quot;select1data&quot; ref=&quot;keyfocus12&quot; @focus=&quot;current1=&#x27;keyfocus12&#x27;&quot;&gt;&lt;/zoehis-select&gt;
        &lt;/div&gt;
        &lt;div style=&quot;border:1px solid red; padding: 5px;&quot;&gt;
          &lt;h4&gt;&lt;/h4&gt;
          &lt;zoehis-input v-model=&quot;input2v&quot; ref=&quot;tb2Input&quot;&gt;&lt;/zoehis-input&gt;
        &lt;/div&gt;
        &lt;script&gt;
          data: function () {
            return {
              current:-1,//
              keyfocusbox2:{
                keyRefs:[  //v-keyfocus
                  {next:'keyfocus11',prev:'keyfocus12',ref:'keyfocus10'},
                  {next:'keyfocus12',prev:'keyfocus10',ref:'keyfocus11'},
                  {next:'keyfocus10',prev:'keyfocus11',ref:'keyfocus12'},
                ]
              }
            }
          }
          methods: {
            onBeforeNext(current){//current
              if(current=='keyfocus11'){
                this.$refs.tb2Input.focus();
                return false;
              }
            },
          }
        &lt;/script&gt;
      </code></pre></div>
          <div class="item-desc">
            v-keyfocus  :before-next 
            :before-next: currentvm .e<br>
            :after-next: 1.vm:  vm 2.e<br>
            :before-prev: currentvm.e <br>
            :after-prev: vm:  vm.e <br>
            before-next  before-prev return false
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <table>
            <tbody v-gridkeyfocus="keyfocusbox3" :before-next="onBeforeNextGrid" data-prefix="table_" :data-current="current3">
              <tr v-for="(item,index) in tbData" :key="item.id">
                <td><zoehis-input v-model="item.text"  :ref="`table_${index}`" @focus="current3=`table_${index}[0]`;rowdata=item"></zoehis-input></td>
                <td><zoehis-input v-model="item.text2" :ref="`table_${index}`" @focus="current3=`table_${index}[1]`"></zoehis-input></td>
                <td><zoehis-select
                    itemcode="myId"
                    itemtext="myName"
                    v-model="item.myId"
                    :textvalue.sync="item.myName"
                    filterable
                    :selectdata="selectdatatb" :ref="`table_${index}`" @focus="current3=`table_${index}[2]`"></zoehis-select>
                </td>
                <td>
                  <zoehis-time-picker :ref="`table_${index}`" v-model="item.mytime" @focus="current3=`table_${index}[3]`"></zoehis-time-picker>
                </td>
                <td>
                  <zoehis-date-editor
                    @save="dateSaveEvn(index)" @tab-end="dateSaveEvn(index)" @after-prev="afterPrevEvn(index)"
                    :ref="`table_${index}`" v-model="item.mytime" @focus="current3=`table_${index}[4]`"></zoehis-date-editor>
                </td>
              </tr>
            </tbody>
          </table>
          <button @click="btnClick('1')"></button>
          <button @click="btnClick('2')"></button>
          <button @click="btnClick('3')"></button>
        </div>
        <div class="item-detail">
  <div class="item-code"><pre><code>
    &lt;tbody v-gridkeyfocus=&quot;keyfocusbox3&quot; :before-next=&quot;onBeforeNextGrid&quot; data-prefix=&quot;table_&quot; :data-current=&quot;current3&quot;&gt;
      &lt;tr v-for=&quot;(item,index) in tbData&quot; :key=&quot;item.id&quot;&gt;
        &lt;td&gt;&lt;zoehis-input v-model=&quot;item.text&quot;  :ref=&quot;&#x60;table_${index}&#x60;&quot;
            @focus=&quot;current3=&#x60;table_${index}[0]&#x60;;rowdata=item&quot;&gt;&lt;/zoehis-input&gt;&lt;/td&gt;
        &lt;td&gt;&lt;zoehis-input v-model=&quot;item.text2&quot; :ref=&quot;&#x60;table_${index}&#x60;&quot;
            @focus=&quot;current3=&#x60;table_${index}[1]&#x60;&quot;&gt;&lt;/zoehis-input&gt;&lt;/td&gt;
        &lt;td&gt;&lt;zoehis-select
          itemcode=&quot;myId&quot;
          itemtext=&quot;myName&quot;
          v-model=&quot;item.myId&quot;
          :textvalue.sync=&quot;item.myName&quot;
          filterable
          :selectdata=&quot;selectdatatb&quot; :ref=&quot;&#x60;table_${index}&#x60;&quot; @focus=&quot;current3=&#x60;table_${index}[2]&#x60;&quot;&gt;&lt;/zoehis-select&gt;
        &lt;/td&gt;
        &lt;td&gt;&lt;zoehis-time-picker :ref=&quot;&#x60;table_${index}&#x60;&quot; v-model=&quot;item.mytime&quot;
              @focus=&quot;current3=&#x60;table_${index}[3]&#x60;&quot;&gt;&lt;/zoehis-time-picker&gt;
        &lt;/td&gt;
      &lt;/tr&gt;
    &lt;/tbody&gt;
    &lt;script&gt;
    data: function () {
      return {
        //
        tbData:[
          {id:1,text:"a1",text2:"a1",myId:1,mytime:new Date(),myName:"my1",disabled:false},
          {id:2,text:"a2",text2:"a2",myId:2,mytime:"2017.8.3",myName:"my2",disabled:true},
          {id:3,text:"a3",text2:"a3",myId:3,mytime:1505804859052,myName:"my3",disabled:false},
        ],
        keyfocusbox3: true,
      }
    }
    //
    onBeforeNextGrid(curVm,theVm){
      if(curVm.colIndex===0){ //  
        this.$refs[curVm.rowRefName][2].focus();
        return false;
      }else if(theVm==null){//

      }
    },
    btnClick(parms){
      if(parms==="1"){
        this.tbData.splice(1,1);

      }else if(parms==="2"){
        this.tbData.push(
          {id:this.tbData.length+1,text:`a${this.tbData.length+1}`,text2:`a${this.tbData.length+1}`,
          myId:this.tbData.length+1,mytime:1505804859052,myName:`my${this.tbData.length+1}`,disabled:false}
          );
      }else if(parms ==3){
        this.tbData.splice(1,0, {id:this.tbData.length+1,text:`a${this.tbData.length+1}`,text2:`a${this.tbData.length+1}`,
        myId:this.tbData.length+1,mytime:1505804859052,myName:`my${this.tbData.length+1}`,disabled:false});
      }
      //
      this.keyfocusbox3=!this.keyfocusbox3;
    },
    &lt;/script&gt;
  </code></pre></div>
          <div class="item-desc">
             v-gridkeyfocus="keyFocusBox"   :data-current="current"  data-prefix="table_"
             :data-rowdata='rowdata'
            //keyFocusBox <br>
            keyFocusBox = true;  //true false, <br>

            //current <br>
            prefix + "_" + index + [colIndex] <br>
            @focus="current3=`table_${index}[colIndex]` <br>
            index:forcolIndex <br>

            //data-prefix <br>
            ref

            //:data-rowdata <br>
            @focusrowdata <br>
             @focus='rowdata=item' <br>

            @focuscurrentkeyfocusgetDisabled()
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr><td>v-keyfocus="keyFocusBox"</td>
            <td>(),total this.keyFocusBox={...}, this.keyFocusBox.total=""
          </td><td>object</td><td>-</td><td>-</td></tr>
          <tr><td>total(keyFocusBox)</td><td>ref</td><td>number</td><td>-</td><td>-</td></tr>
          <tr><td>prefix(keyFocusBox)</td><td>ref'keyRef_'</td><td>string</td><td>-</td><td>'keyRef'</td></tr>
          <tr><td>keyRefs(keyFocusBox)</td><td>(keyRefskeyRefs.length>0,totalprefix)</td><td>array</td><td>-</td><td>'keyRef'</td></tr>
          <tr><td>:data-current</td><td>()</td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>:before-next</td><td>(false)</td><td>function1.current,2.vmvm3.e4.curVm:</td><td>-</td><td>-</td></tr>
          <tr><td>:after-next</td><td></td><td>function1.vmvm3.e</td><td>-</td><td>-</td></tr>
          <tr><td>:before-prev</td><td>(false)</td><td>function.1.current2.vmvm3.e4.curVm:</td><td>-</td><td>-</td></tr>
          <tr><td>:after-prev</td><td></td><td>function1.vmvm2.e</td><td>-</td><td>-</td></tr>
          <tr><td>:data-type</td><td>v-for</td><td>"0":"1":v-forref</td><td>"0" / "1"</td><td>"0"</td></tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>
      <div class="item">
        <h4>gridkeyfocus</h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr><td>v-gridkeyfocus="updateGrid"</td><td></td><td>Boolean</td><td>true/false</td><td>-</td></tr>
          <tr><td>data-prefix</td><td>ref'keyRef_'</td><td>string</td><td>-</td><td>'keyRef_'</td></tr>
          <tr><td>:data-current</td><td>()</td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>:before-next</td><td>(false)</td><td>function1.current,2.vmvm3.rowdata:data-rowdata4.e</td><td>-</td><td>-</td></tr>
          <tr><td>:after-next</td><td></td><td>function1.vmvm2.rowdata:data-rowdata3.e</td><td>-</td><td>-</td></tr>
          <tr><td>:before-prev</td><td>(false)</td><td>function.1.current2.vmvm3.rowdata:data-rowdata4.e</td><td>-</td><td>-</td></tr>
          <tr><td>:after-prev</td><td></td><td>function1.vmvm2.rowdata:data-rowdata3.e</td><td>-</td><td>-</td></tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/babel">
  export default{
    name:'keyfocus1',
    data: function () {
      return {
        current:"",//
        keyfocusbox:{
          keyRefs:  //v-keyfocus
            [
              {next:'keyfocus1',prev:'keyfocus18',ref:'keyfocus0'},
              {next:'keyfocus2',prev:'keyfocus0',ref:'keyfocus1'},
              {next:'keyfocus3',prev:'keyfocus1',ref:'keyfocus2'},// refref
              {next:'keyfocus4',prev:'keyfocus2',ref:'keyfocus3'},// next
              {next:'keyfocus5',prev:'keyfocus3',ref:'keyfocus4'},//prev
              {next:'keyfocus6',prev:'keyfocus4',ref:'keyfocus5'},//next/prev 
              {next:'keyfocus7',prev:'keyfocus5',ref:'keyfocus6'},
              {next:'keyfocus8',prev:'keyfocus6',ref:'keyfocus7'},
              {next:'keyfocus9',prev:'keyfocus7',ref:'keyfocus8'},
              {next:'keyfocus110',prev:'keyfocus8',ref:'keyfocus9'}
            ],
          prefix:"keyfocus",
          total:10
        },
        keyfocusbox2:{
          keyRefs:  //v-keyfocus
            [
              {next:'keyfocus11',prev:'keyfocus12',ref:'keyfocus10'},
              {next:'keyfocus12',prev:'keyfocus10',ref:'keyfocus11'},
              {next:'keyfocus10',prev:'keyfocus11',ref:'keyfocus12'},
            ]
        },
        current1:-1,//
        test1:"1",
        checked:true,
        input1:"",
        input2v:"",
        textarea1:"",
        textarea11:"",
        time1v:"",
        time2v:new Date(),
        select1V:1,
        select1data:[
          {id:1,text:""},
          {id:2,text:""},
          {id:3,text:""}
        ],

        //
        tbData:[
          {id:1,text:"a1",text2:"a1",myId:1,mytime:new Date(),myName:"my1",disabled:false},
          {id:2,text:"a2",text2:"a2",myId:2,mytime:"2017.8.3",myName:"my2",disabled:true},
          {id:3,text:"a3",text2:"a3",myId:3,mytime:1505804859052,myName:"my3",disabled:false},
        ],
        keyfocusbox3:true,
        selectdatatb:[
          {myId:1,myName:"my1"},
          {myId:2,myName:"my2"},
          {myId:3,myName:"my3"},
          {myId:4,myName:"my3"},
          {myId:5,myName:"my3"},
          {myId:6,myName:"my3"},
          {myId:7,myName:"my3"},
        ],
        current3:"",
        rowdata:"",
      };
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {
      focus(ref){
        this.$refs[ref] && this.$refs[ref].focus();
      },
      btnClick(parms){
        if(parms==="1"){
          this.tbData.splice(1,1);

        }else if(parms==="2"){
          this.tbData.push(
            {id:this.tbData.length+1,text:`a${this.tbData.length+1}`,text2:`a${this.tbData.length+1}`,
            myId:this.tbData.length+1,mytime:1505804859052,myName:`my${this.tbData.length+1}`,disabled:false}
          );
        }else if(parms ==3){
          this.tbData.splice(1,0, {id:this.tbData.length+1,text:`a${this.tbData.length+1}`,text2:`a${this.tbData.length+1}`,
            myId:this.tbData.length+1,mytime:1505804859052,myName:`my${this.tbData.length+1}`,disabled:false});
        }
        this.keyfocusbox3=!this.keyfocusbox3;
      },
      onBeforeNext(current,vm){//current
        console.log('',current,vm)
        if(current=='keyfocus11'){
          this.$refs.tb2Input.focus();
          return false;
        }
      },
      onAfterNext(vm){//current
        console.log('',vm)
      },
      onBeforePrev(current,vm){//current
        console.log('',current,vm)
      },
      onAfterPrev(vm){//current
        console.log('',vm)
      },


      //
      onBeforeNextGrid(curVm,theVm,rowdata){
        if(curVm.colIndex===0){ //  
          this.$refs[curVm.rowRefName][2].focus();
          return false;
        }else if(theVm==null){//

        }
      },
      dateSaveEvn(index){
        this.$refs[`table_${index+1}`] && this.$refs[`table_${index+1}`][0].focus();
      },
      afterPrevEvn(index){
        this.$refs[`table_${index}`] && this.$refs[`table_${index}`][3].focus();
      },
    }
  }
</script>



